@import "../../assets/variables.scss"; 

.app-header__content {
  background-color: white;
  display: flex;
  justify-content: center;

  .app-header-container {
    display: flex;
    align-items: center;

    .app-header-item {
      cursor: pointer;
    }

    .active-header-item {
      path {
        fill: $primary-default;
      }
    }

    .app-header-item:not(:last-child) {
      margin-right: 40px;
    }

    .header-logo-container {
      // max-width: 90px;
      height: 42px;
      img {
        // width: 100%;
        // height: auto;
        width: auto;
        height: 100%;
      }
    }
  }
  .user-icon-container{
    position: relative;
    .attention-icon{
      position: absolute;
      top: 4px;
      right: 4px;
      transform: translate(50%,-50%);
      width: 18px;
      height: 18px;
      path{
        fill: #D52330;
      }
    }
  }
}
