@import "../../../assets/variables";

.react-datepicker-wrapper {
  width: 100%;
}

.dope-date-picker {
  &.disabled-calendar {
    pointer-events: none;
  }

  .loader-container {
    position: absolute;
    left: 54%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }

  .react-datepicker__header--custom,
  .react-datepicker__day-names {
    background-color: white;
    border: none;
  }

  .react-datepicker__header--custom {
    padding-top: 26px;
  }

  .react-datepicker__day-names {
    .react-datepicker__day-name {
      color: $dope-grey-100;
      font-size: 10px;
      line-height: 16px;
      font-weight: bold;
    }
  }

  .dope-calendar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 14px;
    line-height: 20px;

    button {
      border: none;
      outline: none;
      background-color: transparent;
      svg{
        path{
          fill: $primary-default;
        }
      }
    }
  }

  .react-datepicker__day {
    font-weight: bold;
    border-radius: 50%;

    &:hover {
      background-color: $primary-hover;
      border-radius: 50%;
    }

    &.react-datepicker__day--disabled,
    &.react-datepicker__day--today {
      pointer-events: none;
    }
  }

  .dope-date-picker-disabled-day {
    color: white;
    background-color: $primary-disabled;
    font-weight: bold;
    pointer-events: none;
  }

  .react-datepicker__day--selected,
  .react-datepicker__day--today {
    background-color: $primary-default;
    color: white;
  }
}
