@import "../../../assets/components/bootstrap4/mixins/breakpoints";
@import "src/assets/variables";

.order-step3 {
  .row:first-child {
    margin-bottom: 40px;
  }

  .wizard-block-header {
    display: flex;
    align-items: flex-end;
    height: 68px;

    &.shipping-address-header {      
      font-size: 24px;
      line-height: 28px;
      margin-bottom: 10px;
    }

    .validation-message {
      position: relative;
    }
  }

  .validation-message {
    position: absolute;
    color: #d52330;
    font-size: 10px;
  }

  .form-group {
    margin-bottom: 20px;
  }
  .order-step3-img {
    display: flex;
    align-self: flex-start;
    .order-step3-img-wrapper{      
      flex: 1 1 auto;
      img {
        width: 100%;
        height: 100%;     
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
        object-position: 50% 50%;
      }
    }
  }  
}

@media screen and (max-width: 1600px) {
  .order-step3 {
    .order-summary,
    .order-step3-img,
    .address-block,
    .credit-card-block {
      flex: 0 0 50%;
      max-width: 50%;
    }
  }
}

@media screen and (max-width: 1200px) {
  .order-step3 {
    .order-summary,
    .order-step3-img,
    .address-block,
    .credit-card-block {
      flex: 0 0 100%;
      max-width: 100%;
    }

    .order-step3-img {
      margin-top: 20px;
      justify-content: center;
    }
  }
}
