@import "src/assets/variables";
.AutomationSummary{
  .approval-grid{
    @include breakpoint(xl){
      display: flex;
    }
    &:not(:last-child){
      margin-bottom: 8px;
    }
  }
  .approval-grid-item{
    &:not(:last-child){
      margin: 0 0 8px 0;
    }
    @include breakpoint(xl){
      flex: 0 1 auto;
      max-width: 320px;
      width: 50%;
      &:not(:last-child){
        margin: 0 15px 0 0;
      }
    }
    .item-title{
      font-family: IBMPlexSans, sans-serif;
      color: #7F8FA4;
      font-size: 14px;
      font-weight: 700;
      line-height: 1.43;
    }    
    // .item-info{
    //   color: #354052;
    //   font-size: 14px;
    //   line-height: 1.43;
    //   &:last-child{
    //     margin-bottom: 0;
    //   }
    // }
    .img-prev-container{
      overflow: hidden;
    }
  }
  .list-info{
    @include breakpoint(xl){
      display: flex;
    }
    .dope-form-group{
      &:first-child{         
        @include breakpoint(xl){
          margin-right: 16px;
        }
      }
      &:last-child{
        flex: 1 1 auto;
      }
    }
  }
  .urlInput-container{
    display: inline-flex;
    position: relative;
    margin-bottom: 24px;
    max-width: 100%;
    .CopyUrlIcon{
      position: absolute;
      // left: 8px;
      right: 12px;
      top: 50%;
      transform: translate(0,-50%);
      cursor: pointer;
      path{
        transition: fill 0.8s ease-in-out;        
      }
    }
    &:hover{
      .CopyUrlIcon{
        path{
          fill: #1D73FF;
        }
      }
    }
    .urlInput{
      cursor: pointer;
      @include outline(0);
      // padding: 10px 16px 10px 48px;
      padding: 10px 48px 10px 16px;
      border-radius: 4px;
      background-color: #F8F9FA;
      border: none;
      height: 40px;
      width: 600px;
      max-width: 100%;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 400;      
      line-height: 1.42;
      color: #1D73FF;      
    }
    .urlcopy-text{
      font-size: 12px;
      position: absolute;
      // left: 0;
      right: 0;
      bottom: 0;
      transform: translate(0,50%);
      color: $primary-default;
    }
  }
  .instr-title{
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;
    font-weight: 700;      
    line-height: 1.42;
    margin-bottom: 8px;
  }
  .instr-ext-grid{
    margin: 0 -16px;
    @include breakpoint(md){
      display: flex;      
    }
  }
  .instr-part-grid{
    padding: 0 16px;
    margin-bottom: 8px;
    display: grid;
    grid-template-columns: 1fr 20px 1fr;
    @include breakpoint(sm){
      grid-template-columns: 125px 20px 125px;
    }
    grid-gap: 8px 14px;
    justify-items: center;
    align-items: center;
    @include breakpoint(md){
      &:not(:last-child){
        position: relative;
        &:after{
          content: '';
          width: 1px;
          height: 100%;
          background-color: #E5E9ED;
          position: absolute;
          right: 0;
          top: 0;
        }
      }
    }
  }
  .inp-wrap{
    position: relative;
    cursor: pointer;
    .instrIcon{
      position: absolute;
      right: 8px;
      top: 50%;      
      transform: translate(0,-50%); 
      opacity: 0;  
      transition: opacity 0.8s ease-in-out;    
      path{
        fill: #1D73FF;     
      }
    }
    &:hover{
      .instrIcon{
        opacity: 1;
      }    
    }
    .copy-text{
      font-size: 10px;
      position: absolute;
      right: 0;
      bottom: 0;
      transform: translate(0,50%);
      color: $primary-default;
    }
  }
  .instrInput{
    cursor: pointer;
    @include outline(0);
    padding: 10px 16px 10px 16px;
    border-radius: 4px;
    background-color: #F8F9FA;
    border: none;
    height: 40px;
    width: 100%;  
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;
    font-weight: 400;      
    line-height: 1.42;    
    color: #354052; 
  }  
  .btnContainer{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 8px;
    margin-bottom: 24px;
  }  
  .summary-radio-row{
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		min-height: 32px;
    // &:not(:last-child){
    //   margin-bottom: 16px;
    // }
    .DopeRadio{
      margin-right: 16px;
      min-width: 200px;
    }
    .tooltips-text{
      color: $dope-grey-100;
      font-size: 12px;
      font-weight: 600;
      line-height: 1.33;
      text-decoration: underline;
      cursor: pointer;
    }
    .summary-tooltip{
      width: 360px;
      max-width: 100%;
      border-radius: 4px;
      span{
        display: block;
        font-size: 12px;
        font-weight: 400;          
        line-height: 18px;          
        color: #ffffff;
      }
    }
	} 
  .custom-wrap{
    display: flex;
    align-items: center;
    margin-left: 20px;
    .custom-label{      
      margin-bottom: 0;
      margin-right: 8px;
      font-weight: 700;      
    }
    .dope-input{
      width: 80px;
    }
  }
}