.modal-dialog.postcard-pricing-modal {
  max-width: 536px;
  border-radius: 4px;

  .dope-modal-body {
    margin: 0 -24px;
  }

  .dope-table-container .react-bootstrap-table {
    overflow-x: auto;

    .table {
      tbody td,
      thead th {
        padding: 14px 0;
      }

      tbody td:nth-child(1),
      thead th:nth-child(1) {
        padding: 14px 0 14px 24px;
      }

      tbody td:nth-child(2),
      thead th:nth-child(2),
      tbody td:nth-child(3),
      thead th:nth-child(3) {
        text-align: right;
        padding: 14px 24px 14px 0;
      }

      thead th,
      tbody td {
        &:nth-child(1) {
          width: 216px;
        }

        &:nth-child(2),
        &:nth-child(3) {
          width: 160px;
        }
      }
    }
  }
}
