@import "../../assets/variables";

.vertical-nav-menu .metismenu-item .metismenu-link, .side-menu .menu-container .menu-item a {
  padding: 12px 24px 12px 24px;
  font-size: 14px;
  font-weight: bold;
  color: #515557;
  border-radius: 0;
  height: unset;
  border-bottom: 1px solid #e5e9ed;
  display: block;
  line-height: 2.5rem;

  svg path {
    fill: $dope-grey-50;
  }

  &:hover, &.active {
    background-color: $primary-default;
    color: #ffffff;
    height: auto;
    text-decoration: none;

    svg path {
      fill: #fff;
    }
  }

  svg {
    margin-right: 16px;
    margin-top: -5px;
  }
}

.vertical-nav-menu .metismenu-container i.metismenu-icon {
  left: 24px;
}

.vertical-nav-menu .metismenu-item .metismenu-link {
  padding: 12px 24px 12px 74px;
  font-size: 14px;
  font-weight: bold;
  color: #515557;
  border-radius: 0;
  height: unset;
  border-bottom: 1px solid #e5e9ed;
  display: block;
  line-height: 2.5rem;
}

.menu-container {
  padding-left: 0;
}

.app-sidebar {

  .app-sidebar__heading {
    padding: 0 28px;
  }

  .account-select-container {
    padding: 0 20px 0 28px;
    margin-bottom: 10px;
  }

  .status-text{
    font-size: 14px;
    font-weight: 400;
    color: #515557;
    margin: 0.75rem 0;
    padding: 0 28px;
  }
}