@import "../../../assets/variables";

.dope-textarea.form-control {
  padding: 10px 16px 10px 16px;  
  font-size: 14px;
  line-height: 18px; 
  min-height: 80px;
  border-radius: 4px;   
  border-color: $athens-gray-approx;
  color: $dark-grey-blue;
  &:hover{
    border-color: #bfc7d1;
    color: $dark-grey-blue;
  }
  &:focus{
    border-width: 2px;
    padding: 9px 15px 9px 15px;
    // border-color: rgba(213, 35, 48, 0.2);
    border-color: #bfc7d1;
    color: $dark-grey-blue;
  }
  &::placeholder {
    color: $dope-grey-50;
  }
}

.dope-textarea.form-control.error {
  border-color: $primary-default !important;
}

.form-group .dope-textarea.form-control,
.input-group .dope-textarea.form-control {
  padding: 10px 16px 10px 16px;
  &:focus{    
    padding: 9px 15px 9px 15px;    
  }  
}

.textarea-title {
  font-size: 18px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: normal;
  color: #7f8fa4;
}