.btn-group {
  .btn {
    background: white;
    color: black;
    border: 1px solid #b5b5b5;
  }
  .btn:focus {
    background: #dfdfdf !important;
    color: black!important;
  }
  .btn:hover {
    background: #dfdfdf !important;
    color: black!important;
  }
}

.clicked {
  background: #dfdfdf !important;
}
