@import "../variables.scss";

.dope-select {
  &.dope--is-disabled {
    pointer-events: all;
   

    &:hover {
      cursor: not-allowed;
    }

    .dope__control ,
    .matchingContacts__control  {
    pointer-events: none;
    background-color: $dope-grey-5;
    color: $dope-grey-50;
    border: 1px solid $dope-grey-10;
    }
  }

  .dope__control ,
  .matchingContacts__control {
    outline: none;
    box-shadow: none;
    border-width: 1px;
    border-color: #e5e9ed;
    cursor: pointer;
    transition: border-width 0s;
    min-height: 40px;
    .dope__value-container ,
    .matchingContacts__value-container {
      padding: 2px 8px;
      // transition: none;
    }
    .dope__indicator ,
    .matchingContacts__indicator {
      padding: 8px;
      // transition: none;
      color: #e5e9ed;
    }
    &:hover {
      border-color: #bfc7d1;
      

      .dope__indicator ,
      .matchingContacts__indicator {
        color: #bfc7d1;
      }
    }
    &.dope__control--is-focused ,
    &.matchingContacts__control--is-focused {
      outline: none;
      box-shadow: none;
      // border-color: rgba(213, 35, 48, 0.2);
      border-color: #bfc7d1;
      border-width: 2px;
      .dope__value-container ,
      .matchingContacts__value-container {
        padding: 1px 8px 1px 7px;
      }
      .dope__indicator ,
      .matchingContacts__indicator {
        padding: 7px 7px 7px 8px;
        // color: rgba(213, 35, 48, 0.2);
        color: #bfc7d1;
      }
    }
    &.dope__control--menu-is-open ,
    &.matchingContacts__control--menu-is-open {
      outline: none;
      box-shadow: none;
    }
    .dope__value-container ,
    .matchingContacts__value-container {
      // .dope__input{}

      .dope__placeholder ,
      .matchingContacts__placeholder {
        color: $dope-grey-50;
      }
    }
    .dope__indicators ,
    .matchingContacts__indicators {
      .dope__indicator-separator ,
      .matchingContacts__indicator-separator {
        display: none;
      }
      // .dope__indicator{}
      // .dope__dropdown-indicator{}
    }
  }
  .dope__single-value ,
  .matchingContacts__single-value {
    color: #354052;
  }
  .dope__menu ,
  .matchingContacts__menu {
    margin: 0;
  }
}
// especially for React-Portalled menu
div.dope__menu ,
div.matchingContacts__menu {
  margin: 0; // cann't find another way to touch to margin and disable him
}
.dope__menu ,
.matchingContacts__menu {
  border: none;
  // margin: 0;
  // padding: 0;
  border-radius: 4px;
  outline: none;
  box-shadow: none;
  box-shadow: inset 0 -1px 0 0 #e5e9ed;
  overflow: hidden;
  .dope__menu-list ,
  .matchingContacts__menu-list {
    margin: 0;
    padding: 0;
    .dope__option ,
    .matchingContacts__option {
      box-shadow: inset 0 -1px 0 0 #e5e9ed;
      background-color: #ffffff;
      color: #354052;
      cursor: pointer;
      transition: background-color .3s ease, color .3s ease;
    }
    .dope__option:hover ,
    .matchingContacts__option:hover {
      // background-color: #f8f9fa;
      // color: #354052;
      background-color: $primary-hover;
      color: #ffffff;
    }
    .dope__option:active ,
    .matchingContacts__option:active {
      // background-color: #e5e9ed;
      // color: #354052;
      background-color: $primary-pressed;
      color: #ffffff;
    }
    .dope__option--is-selected ,
    .matchingContacts__option--is-selected {
      background-color: #ffffff;
      font-weight: bold;
      color: $primary-default;
    }
    .dope__option--is-selected:hover ,
    .matchingContacts__option--is-selected:hover {
      font-weight: bold;
      // background-color: #f8f9fa;
      // color: #d52330;
      background-color: $primary-hover;
      color: #ffffff;
    }
    .dope__option--is-disabled ,
    .matchingContacts__option--is-disabled {
      background-color: #f2f3f5;
      color: #bfc7d1;
      cursor: not-allowed;
      &:hover,
      &:focus,
      &:active {
        background-color: #f2f3f5;
        color: #bfc7d1;
      }
    }
  }
}

.select-error {
  .dope-select {
    .dope__control {
      border: 1px solid $primary-default;
    }
  }
}
