@import "../../../assets/variables";
// defoult horizontally
$md-radio-bg-color:$dope-grey-20;
$label-color: $dark-grey-blue;
$md-radio-size: 24px;
$md-radio-int-size: 16px;
$md-radio-border-size: 4px;

.DopeRadioGroup {
    // margin: 0;
    display: inline-flex;
    justify-content: flex-start;
    &.horizontal{
        flex-wrap: wrap;
        .radio{            
            &:not(:last-child){
                margin-right: 36px;
            }
        }
    }
    &.vertical{
        flex-direction: column;
        .radio{            
            &:not(:last-child){
                margin-bottom: 8px;
            }
        }
    }
    .radio{
        display: inline-flex;
        align-items: center;
        margin: 0;
        vertical-align: middle;
        cursor: pointer;        
    }
    input[type="radio"] {
        display: none;
        &:checked + .custom-radio:before {
            border-color: $primary-default;
            // animation: ripple 0.2s linear forwards;   
        }
        &:checked:disabled + .custom-radio:before {
            border-color: $primary-disabled;
            // animation: ripple 0.2s linear forwards;   
        }
        &:disabled + .custom-radio:before {
            background-color: $dope-grey-10;
            border-color: $dope-grey-10; 
        }
        
    }
    .custom-radio {
        display: inline-block;
        height:$md-radio-size;
        width:$md-radio-size;   
        position: relative;
        margin-right: 8px;
        margin-bottom: 0;
        cursor: pointer;
        vertical-align: bottom;
        &:before{
            position: absolute;                 
            content: '';  
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            width: $md-radio-int-size;
            height: $md-radio-int-size;
            background-color: $md-radio-bg-color;
            border: $md-radio-border-size solid $md-radio-bg-color;
            border-radius: 50%;
            transition: all .3s ease;            
        }            
    }
    .radio-label{
        color: $label-color;
        // font-family: IBMPlexSans, sans-serif;
        font-size: 14px;
        line-height: 20px;
        user-select: none;
    }
}
