@import "../../assets/variables";

.add-number-modal.dope-modal {
  // max-width: 536px;
  max-width: 555px;

  .modal-body {
    p {
      margin-bottom: 10px;
      color: $dark-grey-blue;
    }

    .dope-form-group {
      margin-bottom: 18px;

      &:last-of-type {
        margin-bottom: 16px;
      }
    }
  }

  .one-line {
    display: flex;
    align-items: flex-end;

    .code-area-container {
      flex-grow: 1;
      margin-right: 8px;

      &.select-error {
        div[class*=control] {
          border-color: $primary-default;
        }
      }
    }
  }
}
