@import "../../../../assets/variables";

.auth-container {
  .forgot-password-form-phone {
    max-width: 432px;
    padding: 24px;
    background-color: white;
    box-shadow: $form-shadow;    

    .forgot-password-text {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 24px;
      color: $dark-grey-blue;
    }

    .forgot-password-form-bottom {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      min-height: 40px;
      a {
        color: $primary-default;
        text-decoration: underline;
      } 
    }

    .input-title {
      font-size: 14px;
    }
    .phone-sms-addon{
      margin-bottom: 8px;
      .dope-input{                
        font-weight: 700;
        padding: 1px 8px; 
        &:focus{
          padding: 1px 7px 1px 7px;
        }     
        font-size: 13px;
        &.form-control:focus ~ .input-group-append {
          padding: 0 9px 0 10px;
        }
      }
      .check-text{
        text-align: center;
        padding: 0 4px;
        font-weight: 700;      
        color: #fff;
        font-size: 10px;
      }      
      .input-group-append{
        padding: 0 10px 0 10px;
        height: 40px;
        display: flex;      
        justify-content: center;      
        align-items: center;
        border: 1px solid #e5e9ed;
        border-left: none;
        border-radius: 0 4px 4px 0;     
        background-color: $primary-default;
        transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
        &.disabled{
          opacity: 0.3;
          pointer-events: none;
        }
      }       
    }
    .check-code-form{
      opacity: 0;
      transition: opacity 0.6s ease-in;
      pointer-events: none;
      &.active{
        opacity: 1;
        pointer-events: auto;
      }
      margin-bottom: 8px;
      .dope-input-group{
        justify-content: center;
      }
      .dope-input{
        text-align: center;
        background-color: #eee;
        font-weight: 700;
        padding: 1px 8px; 
        &:focus{
          padding: 1px 7px 1px 7px;
        }     
        font-size: 13px;
        flex: 0 0 100px;
        @include breakpoint(sm){
          font-size: 18px;
          flex: 0 0 200px;
          letter-spacing: 10px;
        }
        &.form-control:focus ~ .input-group-append {
          padding: 0 9px 0 10px;
        }
      }
      .check-text{
        text-align: center;
        padding: 0 4px;
        font-weight: 700;      
        color: #fff;
        font-size: 10px;
      }
      .input-group-append{
        padding: 0 10px 0 10px;
        height: 40px;
        display: flex;      
        justify-content: center;      
        align-items: center;
        border: 1px solid #e5e9ed;
        border-left: none;
        border-radius: 0 4px 4px 0;     
        background-color: $primary-default;
        transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
      } 
      .input-group .validation-message{
        left: 50%;
        transform: translate(-50%,0);
      }
    }
  }
}
