@import "../../../../../assets/variables";

.map-btn-container {
  display: flex;
  align-items: center;

  .map-add-pin-block {
    flex-grow: 1;
    padding: 20px 10px;
  }

  .map-add-pin-block {
    .top-block,
    .pins-quantity-block {
      display: flex;
    }

    .top-block {
      margin-bottom: 20px;
    }

    .pins-quantity-item {
      display: flex;
      flex-direction: column;
      flex-grow: 1;

      span:first-child {
        font-weight: 500;
        color: $regent-gray;
      }

      span:last-child {
        font-size: 16px;
        color: $primary-default;
        font-weight: 600;
      }

      span {
        padding-left: 40%;
      }
    }

    .map-add-pin-btn {
      button {
        padding: 11px 40px;
      }

      .btn-secondary {
        margin-right: 20px;
      }
    }
  }

  .start-placing-btn {
    margin: 10px 0;
  }
}
