@import "src/assets/variables";
.EddmRouteMap{
  .main-title{
    font-family: IBMPlexSans, sans-serif;  
    font-weight: 700;
    font-size: 28px;
    line-height: 1.28;
    color: $dark-grey-blue;
    margin-bottom: 16px;
  }
  .info-text{
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;
    line-height: 1.43;
    color: $dark-grey-blue;
    margin-bottom: 32px;
  }
  .mapCard{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
    margin-bottom: 84px;
  }
  .topMapCard{
    position: relative;
    z-index: 1;//error under map
    padding: 15px 24px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    box-shadow: inset 0px -1px 0px #E5E9ED;    
  }
  .cityResult{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.33;
    color: $dark-grey-blue;
    margin-right: 16px;
  }
  .togglemapCardViewBox{  
    margin-left: auto;  
    align-self: stretch;
    height: 40px;
    background: $dope-grey-10;
    border-radius: 4px;
    padding: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .togglemapCardViewBtn{
      @include outline(0);
      border: none;
      padding: 2px 4px;
      border-radius: 4px;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      line-height: 1.43;
      text-align: center;
      color: $dope-grey-100;
      min-width: 104px; //because bold/normal
      min-height: 36px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      &:not(:last-child){
        margin-right: 2px;
      }
      &.active{
        font-weight: 700;
        background: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
        color: $dark-grey-blue;
      }
    }
  }
  .eddmRoute-container{
    @include breakpoint(xl){
      display: flex;  
    }  
  }
  .eddmRouteSummary{
    flex: 0 1 auto;
    width: 280px;
    background: #FFFFFF;
    box-shadow: inset 1px 0px 0px #E5E9ED;
    padding: 0 24px;
    display: flex;
    flex-direction: column;
    @include breakpoint(lg-down){
      width: 100%;       
      flex-direction: row;
      justify-content: space-between;
      padding: 24px 12px; 
    }  
    .eddmRouteSummaryTitle{    
      min-height: 70px;
      border-bottom: 1px solid #F2F3F5;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-family: IBMPlexSans, sans-serif;    
      font-weight: 700;
      font-size: 18px;
      line-height: 1.33;
      text-align: center;
      color: $dark-grey-blue;
      @include breakpoint(xl){
        width: 100%;
      }       
    }
    .eddmRouteSummaryItem{
      min-height: 76px;
      border-bottom: 1px solid #F2F3F5;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-family: IBMPlexSans, sans-serif; 
      text-align: center;
      color: $dark-grey-blue;
      .itemTitle{
        font-size: 14px;
        line-height: 1.43;
        margin-bottom: 8px;
      }
      .total-price-range{
        display: flex;
        align-items: center;
      }
      .itemValue{
        flex: 0 0 auto;
        font-weight: 700;
        font-size: 22px;
        line-height: 1.27;
        &.empty{
          color: $dope-grey-50;
        }
        &.sm{
          font-weight: 400;
          font-size: 16px;
        }
        .size-title{
          font-size: 0.7em;
        }
      }
      .itemTitleTooltips{
        margin-left: 2px;
        svg{
          path{
            fill: #354052;
          }        
        }
      } 
    }
    .btnContainer{
      min-height: 88px;
      max-height: 164px;//temporary design shifted one eddmRouteSummaryItem
      display: flex;
      justify-content: center;
      align-items: center;
      @include breakpoint(xl){
        flex: 1 0 auto;
      } 
    }
  }  
  .eddmRouteMapContainer {
    flex: 1 1 auto; 
    display: flex;
    //flex-direction: column;
    position: relative;
    height: 462px; //temporary  
    // @include breakpoint(lg-down){
    //   height: 462px;
    // }
    &.eventNone{
      pointer-events: none;
      .leaflet-control {
        pointer-events: none;
      }
    }
  }
  .EddmRouteStat{
    flex: 0 0 auto;
    display: grid;
    width: 120px;
    //grid-template-columns: repeat(4,1fr);
    grid-template-rows: repeat(4,1fr);
    &.large{
      //grid-template-columns: repeat(5,1fr);
      grid-template-rows: repeat(5,1fr);
    }
  }
  .leafletMapContainer{
    flex: 1 1 auto;
  }
  .eddmRouteStatItem{
    flex: 1 1 auto;
    background: #FFFFFF;
    box-shadow: inset 0px -1px 0px #E5E9ED, inset -1px 0px 0px #E5E9ED;
    padding: 16px 8px;
    .statItemTitle{
      font-family: IBMPlexSans, sans-serif;   
      font-weight: 700;
      font-size: 10px;
      line-height: 1.6;
      text-transform: uppercase;
      color: $dark-grey-blue;
    }
    .statItemVal{
      display: block;
      font-family: IBMPlexSans, sans-serif;     
      font-weight: 700;
      font-size: 14px;
      line-height: 1.43;
      color: $dark-grey-blue;
      &.empty{
        text-align: center; 
        color: $dope-grey-50;     
      }
    }
    &.hasModal{
      cursor: pointer;
      .statItemTitle-box{
        position: relative;
        &::after{
          content: '';
          position: absolute;
          right: 2px;
          top: 45%;
          transform: translateY(-50%) rotate(45deg);
          border-right: 2px solid $dark-grey-blue;
          border-bottom: 2px solid $dark-grey-blue;
          width: 7px;
          height: 7px;
        }
      }
    }
  }  
  .eddmRouteTableContainer{
    flex: 1 1 auto;
    th{
      text-transform: uppercase;
    }
    .dope-table-container .react-bootstrap-table .table thead th {
      color: $dark-grey-blue;
      border-right: 1px solid $dope-grey-20;
      &.hasModal{
        cursor: pointer;
        position: relative;
        width: auto;
        display: table-cell;
        &::after{
          content: '';
          position: absolute;
          right: 12px;
          top: 45%;
          transform: translateY(-50%) rotate(45deg);
          border-right: 2px solid $dark-grey-blue;
          border-bottom: 2px solid $dark-grey-blue;
          width: 7px;
          height: 7px;
        }
      }
    }
    .table-row{
      &.active{        
        color: #fff;
        background-color: $primary-default;
      }
    }
    // .dope-table-container .react-bootstrap-table .table tbody tr:hover{
    //   color: #fff;
    //   background-color: $primary-disabled;
    // }
    .dope-table-container .react-bootstrap-table .table tbody tr.active:hover{
      color: #fff;
      background-color: $primary-default;
    }
  }
  .hasModal{
    span{
      text-transform: none;
    }
    .itemTitleTooltips{
      margin-left: 2px;
      path {
        fill: $dark-grey-blue;
      }
    }
  }
  .adv-stat-container{
    margin: 0;
    padding: 0;
    list-style-position: inside;
    display: flex;
    flex-wrap: wrap;
    font-size: 8px;  
    .adv-stat-item{
      margin-right: 8px;
      
    }
    .title{
      color: $primary-default;
      font-weight: 700;
    }
    .info{
      color: $primary-default;
    }
  }
  
  .Test-container{
    margin-bottom: 12px;   
  }
  
  .selectedResult{
    // display: flex;
    // flex-wrap: wrap;
    margin-bottom: 12px;
  }
  .selectedLatLng-container{  
    margin-bottom: 12px;
    .sc-data{
      display: flex;    
    }
    .sl-location{
      border: 1px solid $primary-default;
      padding: 2px 8px;
      width: 200px;
      height: 30px;
      display: flex;
      align-items: center;      
      &:not(:last-child){
        margin-right: 12px;
      }
    }
  }
  .selectedRoutes-container{
    .sl-routes{
      border: 1px solid $primary-default;
      padding: 2px 8px;
      // width: 200px;
      height: 30px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
  }
  .zipSearch{
    margin-right: 24px;
    display: flex;
    // input{      
    //   -moz-appearance: textfield; 
    //   &::-webkit-inner-spin-button { 
    //     display: none;
    //   }
    // }
    form{
      margin-right: 5px;
    }
    .dope-form-group{
      margin-bottom: 0;
    }
    .searchZip{
      svg{
        position: absolute;
        top: 50%;
        left: 16px;
        transform: translateY(-50%);
      }
      .dope-input{
        padding-left: 40px;
        &:focus{
          padding-left: 39px;
        }
      }
    }
  }
}
