@import "src/assets/variables";

.DashboardN{
  padding: 10px;
  .dash-m-title{
    color: #354052;    
    font-size: 28px;
    font-weight: 700;    
    line-height: 1.14;
    margin-bottom: 32px;
  }
  .dash-title{
    color: #354052;    
    font-size: 18px;
    font-weight: 700;    
    line-height: 1.33;
    margin: 0;
  }
  .dashboard-grid{
    display: grid;
    grid-gap: 16px 16px;
    grid-template-columns: 1fr;
    @include breakpoint(xl){
      grid-template-columns: 1fr 1fr;
    }    
  }
  .Analytics{
    @include breakpoint(xl){
      grid-column: 1/3;
    }
  }
  .dash-card{
    border-radius: 4px;
    background-color: #FFFFFF;    
  }
  .dash-card-header{
    padding: 16px 24px;
    min-height: 56px;    
    box-shadow: inset 0 -1px 0 0 #E5E9ED;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}