@import "src/assets/variables";

.admin-accounts {
  .page-title {
    font-size: 28px;
    line-height: 32px;
    font-weight: bold;
    margin-bottom: 32px;
    color: $dark-grey-blue;
  }

  .admin-accounts-container {
    background-color: white;
    border-radius: 4px;

    .accounts-table-header {
      display: flex;
      justify-content: space-between;

      .accounts-count {
        margin-left: 24px;
        display: flex;
        align-items: center;
      }
    }
  }
}

.dope-search-container {
  margin: 8px 24px;
  position: relative;

  .form-group {
    margin: 0;
  }

  svg {
    position: absolute;
    top: 50%;
    left: 16px;
    transform: translateY(-50%);
  }

  .dope-input {
    width: 240px;
    padding-left: 40px !important;

    &:focus {
      padding-left: 39px !important;
    }
  }
}
