.back-selector{
  // width: 500px;
  width: calc(2775px * 0.15);
  @media screen and (min-width: 1350px) {
    width: calc(2775px * 0.18);
  }
  @media screen and (min-width: 1600px) {
    width: calc(2775px * 0.19);
  }
  display: flex;
  justify-content: center;
  .back-item{
    max-width: 150px;
    cursor: pointer;
    &:not(:last-child){
      margin-right: 15px;
    }
    .back-preview-box{
      position: relative;
      .back-checked{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);      
        &::before {
          content: "";        
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
          background: url("../../../../assets/img/icons/select.svg") no-repeat center;
          background-size: contain;
          height: 30px;
          width: 30px;        
        }
      }
    }
  }
}
