@import "src/assets/variables";

.accounts-admin-table {
  .dope-tab-content{
    padding: 0 0 40px;
  }
  .setting-ico {
    padding: 0 !important;
    background-color: transparent;

    &:hover,
    &:focus,
    &:active,
    &:focus:active {
      background-color: transparent !important;
    }

    &:hover {
      path {
        fill: $primary-default;

        &:active {
          fill: $primary-pressed;
        }
      }
    }
  }

  .react-bootstrap-table {
    thead {
      th{
        white-space: nowrap;
      }
      
      th:nth-child(1) {
        width: 30%;
      }

      th:nth-child(3) {
        width: 30%;
      }

      th:nth-child(2),
      th:nth-child(4) {
        width: 20%;
      }

      th:nth-child(5) {
        width: 50px;
      }

      th:nth-child(1) {
        min-width: 160px;
      }

      th:nth-child(2){
        min-width: 140px;
      }

      th:nth-child(3) {
        min-width: 120px;
      }
      
      th:nth-child(4) {
        min-width: 160px;
      }

      th:nth-child(5) {
        min-width: 50px;
      }
    }
  }  
}
.admin-accounts-container{
  .acc-nav-tabs{
    border: none;
    align-self: flex-end; 
    margin: 0;   
    padding: 8px 24px 0;  
    .acc-nav-item{
      margin: 0;    
      &:not(:last-child){
        margin-right: 12px;
      } 
      @include outline(0);      
      .nav-link{
        color: $dark-grey-blue!important;        
        font-size: 14px;
        line-height: 1.43;
        border-style: solid;
        border-width: 0 0 2px 0;
        border-color: transparent; 
        transition: border-color 300ms ease 0s; 
        // min-width: 130px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
        &.active{
          font-weight: 700;          
          border-width: 0 0 2px 0;
          border-color: $primary-default;
        }
        @include outline(0);
        &:hover,&:active,&:focus{
          border-width: 0 0 2px 0;
          border-color: $primary-default;          
        }
      }           
    } 
    .acc-nav-item:nth-child(1){      
      .nav-link{
        min-width: 84px;
      }
    }
    .acc-nav-item:nth-child(2){
      .nav-link{
        min-width: 107px;
      }
    }
    .acc-nav-item:nth-child(3){
      .nav-link{
        min-width: 120px;
      }
    }  
  }  
}
