@import "src/assets/variables";
.tab-content {
  padding: 20px 10px;
  border: 1px solid #dee2e6;
  border-top: none;
}

.simple-table {
  cursor: pointer;
}

.indexedStep {
  color: white;
  width: 20px;
  height: 20px;
  font-size: 14px;
  border-radius: 50%;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  &.accomplished {
    background-color: #515557;
  }
}

.RSPBprogressBar {
  // background-color: #F2BDBE;
  background-color: $primary-disabled;
  height: 8px;
}

.RSPBprogressBar .indexedStep {
  // background-color: #F2BDBE;
  background-color: $primary-disabled;
  &.accomplished {
    // background-color: #C7001C;
    background-color: $primary-default;
  }
}

.RSPBprogression {
  // background-color: #C7001C !important;
  background-color: $primary-default !important;
}


