@import "src/assets/variables";
.AdminManageRequestsList{  
  .page-title-block{
    margin-bottom: 32px;
    .page-title {
      font-size: 28px;
      line-height: 32px;
      font-weight: bold;      
      color: $dark-grey-blue;
    }
  }  
  .requestsTable{    
    th{      
      &:last-of-type{
        width: 48px;
      }
    }
    @include breakpoint(xl){      
      tr{
        // transform: scale(1);        
        .del-cell{          
          .del-icon{
            opacity: 0;
            transition: .3s;
            pointer-events: none;
          }
        }
        &:hover{
          .del-cell{            
            .del-icon{
              opacity: 1;
              pointer-events: auto;
            }
          }
        }
      }
    }
    .acc-cell-box{
      display: inline-flex;
      flex-direction: column; 
      align-items: flex-start;
      justify-content: center;
      word-break: break-word;
    }
    .status-cell {
      padding: 5px 10px;
      color: white;
      &.new{
        background-color: $alert-default;
      }
      &.charged{
        background-color: $positive-default;
      }
    }
    .list-cell{
      .list-text{
        display: block;
        white-space: nowrap;
        max-width: 200px;
        overflow: hidden;      
        text-overflow: ellipsis;
      } 
      .contact-list-tooltip {
        max-width: 600px;
        border-radius: 4px;
      }
      .contact-list-tooltip span {
        display: block;
        font-size: 14px;
        font-weight: 700;
        line-height: 1.43;
        color: #ffffff;
      }    
    }    
  }
  .norequestsData{
    min-height: 92px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $dope-grey-50;
    font-family: IBMPlexSans, sans-serif;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.27;
    text-align: center;
    border-top: 1px solid #dee2e6;
    box-shadow: inset 0 -1px 0 0 #E5E9ED;
    margin-bottom: 8px;
    position: relative;
    .loader-container{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}