@import "../../assets/variables";

.order-container {
  .orders-table-container {
    min-height: 300px;

    .title {
      font-size: 28px;
      font-weight: 700;
      margin: 0;
      line-height: normal;
      color: #354052;
      margin-bottom: 42px;
    }

    .order-table-message {
      margin: 30px auto;
      height: 100%;
    }
  }

  .card-header {
    text-transform: none;
    color: black;
    font-size: 20px;
    font-weight: 500;
  }

  .react-bootstrap-table {
    thead {
      th:nth-child(1) {
        width: 30%;
      }

      th:nth-child(2),
      th:nth-child(3),
      th:nth-child(4),
      th:nth-child(5) {
        width: 17.5%;
      }

      th:nth-child(6) {
        width: 100px;
      }

      th:nth-child(1) {
        min-width: 200px;
      }

      th:nth-child(3),
      th:nth-child(5) {
        min-width: 160px;
      }

      th:nth-child(2),
      th:nth-child(4) {
        min-width: 120px;
      }
    }

    .sent {
      background-color: $stromboli;
    }

    .created {
      background-color: $boulder;
    }

    .processing {
      background-color: $primary-default;
    }

    .status-cell {
      padding: 5px 10px;
      color: white;
      white-space: nowrap;
      svg {
        margin-right: 10px;

        path {
          fill: white;
        }
      }
    }
  }
}

.admin-block {
  .status-cell {
    color: black !important;

    [class*=indicatorSeparator],
    [class*=indicatorContainer] {
      display: none;
    }
  }
}
