$card-header-bg: rgb(230, 230, 230);

.create-edit-job {
  .steps {
    margin-right: 20px;
    .title {
      text-align: center;
      font-size: 25px;
      font-weight: bold;
    }
    .step {
      font-size: 18px;
      &.green {
        color: green;
      }
      &.red {
        color: red;
      }
    }
  }
  .credits-campaign {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .job-info-steps {
    display: flex;
    justify-content: space-between;
    .job-info {
      flex: 3;
      .title {
        background: $card-header-bg;
        padding: 10px;
        font-size: 20px;
      }
      .name {
        margin-bottom: 10px;
      }
      .campaign {
        display: flex;
        .add-button {
          margin-left: 10px;
        }
      }
      .type {
        .items {
          display: flex;
          .item {
            margin: 10px;
            text-align: center;
            font-weight: 600;
            .card {
              background: #efefef;
              cursor: pointer;
              height: 90px;
              &.selected {
                border: 3px solid grey;
              }
              .icon {
                height: 60px;
                width: 60px;
              }
              &:hover {
                background: #e5e5e5;
                border-radius: 12px;
              }
            }
          }
        }
      }
      .enabled-features {
        display: flex;
        margin-top: 10px;
        .label {
          margin-left: 10px;
        }
      }
    }
    .steps {
      flex: 2;
      margin-left: 30px;
    }
  }
  .call-tracking {
    margin-top: 20px;
    .title {
      font-size: 20px;
      font-weight: bold;
    }
    .description {
      margin-bottom: 10px;
      color: black;
    }
    .edit {
      .header {
        display: flex;
        justify-content: flex-end;
        background: $card-header-bg;
        padding: 10px;
        margin: 0;
      }
    }
    .create {
      .tracking-number {
        .content {
          display: flex;
          .dropdown {
            margin-right: 5px;
          }
        }
      }
      .forwarding-number {
        .label {
          margin-bottom: 10px;
        }
      }
    }
  }
  .mail-tracking {
    margin-top: 20px;
    .card {
      .header {
        background: $card-header-bg;
        padding: 10px;
        font-weight: bold;
        font-size: 20px;
        margin: 0;
      }
      .content {
        .checkbox {
          margin-right: 5px;
        }
      }
    }
  }
  .advertising {
    margin-top: 20px;
    .header {
      background: $card-header-bg;
      padding: 10px;
      font-weight: bold;
      font-size: 20px;
      margin: 0;
    }
  }
  .pricing {
    .header {
      background: $card-header-bg;
      padding: 10px;
      font-weight: bold;
      font-size: 20px;
      margin: 0;
    }
    .content {
      .margined {
        margin-top: 10px;
      }
    }
  }
}

.add-tracking-number {
  margin-top: 10px;
  .input-data {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    .button {
      margin-left: 10px;
    }
  }
}

.response-list {
  text-align: center;
  .title {
    color: grey;
  }
  .tr {
    cursor: pointer;
    &.selected {
      background: rgb(209, 209, 209);
    }
  }
  .cost {
    margin-top: 10px;
  }
}
