@import "../../../../../assets/variables";
.dope-modal.postcard-approval-modal{
  @include breakpoint(xl){
    max-width: 816px;
  }
  .dope-modal-body{
    padding: 0 0 15px 0;
  }
  .approval-text{
    color: #354052;
    font-size: 14px;
    line-height: 1.43;
    margin-bottom: 16px;
  }
  .approval-row-item{
    &:not(:last-child){
      margin: 0 0 8px 0;
    }
    .item-title{
      color: #7F8FA4;
      font-size: 14px;
      font-weight: 700;
      line-height: 1.43;
    }
    .dope-table-container{
      margin-bottom: 20px;
      .dope-pagination{
        margin-bottom: 0;
      }
    }
  }
  .approval-grid{
    @include breakpoint(xl){
      display: flex;
    }
    &:not(:last-child){
      margin-bottom: 8px;
    }
  }
  .approval-grid-item{
    &:not(:last-child){
      margin: 0 0 8px 0;
    }
    @include breakpoint(xl){
      flex: 0 1 auto;
      width: 50%;
      &:not(:last-child){
        margin: 0 15px 0 0;
      }
    }
    .item-title{
      color: #7F8FA4;
      font-size: 14px;
      font-weight: 700;
      line-height: 1.43;
    }
    .item-info{
      color: #354052;
      font-size: 14px;
      line-height: 1.43;
      &:last-child{
        margin-bottom: 0;
      }
    }
    .img-prev-container{
      overflow: hidden;
    }
  }
  .templateName-container{
    max-width: 250px;
  }
}