@import "../../assets/variables";

.dope-nav-item.nav-item {
  .nav-link {
    font-size: 16px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $abbey;
    border: none;

    & {
      border-bottom: 4px solid #e5e9ed;
    }

    &.active {
      border-bottom: 4px solid $primary-default;
      color: $primary-default;
    }

    &:hover, &:active {
      color: $primary-default !important;
    }
  }

}