@import "../../assets/variables";

.Toastify {
  .dope-toast-container {
    width: 560px;
    max-width: 100%; 
    transform: translateX(-50%);
    margin: 0;       
    @media only screen and (max-width: 480px){
      transform: none;
    }

    & > div {
      min-height: 0;
      margin: 0 auto;
      padding: 8px 16px;
      border-radius: 4px;

      .Toastify__toast-body {
        padding: 0 10px 0 0;
        font-weight: bold;
        display: flex;
        align-items: center;

        &:before {
          display: block;
          content: '';
          width: 16px;
          height: 16px;
          background-size: 16px;
          margin-right: 8px;
          flex-shrink: 0;
        }
      }

      .close-btn-container {
        display: flex;
        align-items: center;
      }
    }

    .Toastify__toast--success {
      background-color: $positive-default;

      .Toastify__toast-body:before {
        background-image: url('../../assets/img/icons/success.svg');
      }
    }

    .Toastify__toast--warning {
      background-color: $alert-default;

      .Toastify__toast-body:before {
        background-image: url('../../assets/img/icons/warning.svg');
      }
    }

    .Toastify__toast--error {
      background-color: $primary-default;

      .Toastify__toast-body:before {
        background-image: url('../../assets/img/icons/error.svg');
      }
    }
  }
}
