@import "../../../assets/variables";

.payment-history-tab {
  .react-bootstrap-table {
    thead {
      th:nth-child(1) {
        width: 10%;
        min-width: 80px;
      }
      th:nth-child(2),
      th:nth-child(6){
        width: 10%;
        min-width: 115px;
      }

      th:nth-child(3){
        width: 10%;
        min-width: 90px;
      }

      th:nth-child(5) {
        width: 20%;
        min-width: 200px;
      }

      th:nth-child(7){
        width: 10%;
        min-width: 90px;
      }

      th:nth-child(4) {
        width: 30%;
        min-width: 200px;
      }
      
      th {
        text-align: left;
        padding: 6px 15px 6px 15px;
      }
    }

    .table-row {
      td {
        text-align: left;
        padding: 6px 15px 6px 15px;
      }
      td:nth-child(4){
        word-break: break-word;
      }
    }

    .status-td {
      .status-cell{
        height: 16px;     
        padding: 0 8px;   
        border-radius: 4px;
        color: #FFFFFF;        
        font-size: 10px;
        font-weight: 700;        
        line-height: 1.6;   
        &.paid {
          background-color: #39B54A;
        }  
        &.pending {
          background-color: #F5A623;
        }  
        &.error {
          background-color: #D52330;
        }  
        &.refunded{
          background-color: #7F8FA4;
        }   
      }      
      // & > span {
      //   padding: 4px 10px;
      //   color: white;
      //   text-transform: capitalize;
      // }
    }
    .pdfReport-td{
      svg{
        path{
          fill: #7F8FA4;
        }
      }      
    }

    .card-cell {
      display: flex;
      align-items: center;

      .card-cell-last4 {
        display: flex;
        align-items: center;
        margin-right: 10px;
      }
    }

    .visa-ico::before {
      content: "";
      display: block;
      background: url('../../../assets/img/credit-cards/visa-logo.png')
        no-repeat center;
      background-size: 60%;
      height: 24px;
      width: 71px;
    }

    .master-card-ico::before {
      content: "";
      display: block;
      background: url('../../../assets/img/credit-cards/master-card-logo.png')
        no-repeat center;
      background-size: 60%;
      height: 24px;
      width: 71px;
    }

    .american-express-ico::before {
      content: "";
      display: block;
      background: url('../../../assets/img/credit-cards/express.jpg') no-repeat
        center;
      background-size: 60%;
      height: 24px;
      width: 71px;
    }
  }

  .loader-container {
    position: absolute;
    top: calc(50% - 50px);
    left: calc(50% - 50px);
  }
}
