@import "../../../assets/variables";
.dashboard-content {
  .calendar-period-btn {
    button {
      min-width: 95px;
    }
  }

  .map-wrapper {
    margin-bottom: 40px;
  }

  .incoming-calls-container {
    display: flex;
    
    h5 {
      font-size: 20px;
      margin-bottom: 20px;
      font-weight: 500;
    }
  }

  .no-incoming-calls {
    display: flex;
    flex-direction: column;

    .circle {
      display: flex;
      justify-content: center;
      align-items: center;
      align-self: center;
      background-color: $athens-gray;
      border-radius: 50%;
      height: 150px;
      width: 150px;
      margin-bottom: 20px;
  
      svg {
        height: 75px;
        width: 75px;
  
        path {
          fill: $regent-gray;
        }
      }
    }
  
    & > p {
      align-self: center;
      font-size: 18px;
      font-weight: 500;
      margin-bottom: 40px;
    }
  }
}
