@import "src/assets/variables";
.AdminRequest{
  .ListUpload{
    
  }
  .back-btn-container{
    margin-bottom: 24px;
    .back-btn{
      height: 24px;
      padding: 4px 8px;
      border: none;
      @include outline(0);
      border-radius: 4px;
      background-color: #FFFFFF;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: $primary-default;
      font-family: IBMPlexSans, sans-serif;
      font-size: 10px;
      font-weight: 700;      
      line-height: 1.7;
      text-transform: uppercase;
    }
    .back-icon{
      height: 16px;
      width: 16px;
      margin-right: 4px;
    }
  }
}