.map-info-block {
    padding: 15px;

    .map-img-container, 
    .contact-info-container {
        flex-grow: 1;
        flex-basis: 0;
    }

    .map-img-container img {
        max-width: 50%;
    }

    .contact-info-item {
        display: flex;

        svg {
            margin-right: 10px;
        }

        &:first-child {
            text-transform: uppercase;
            font-size: 20px;
            font-weight: 600;
            margin-bottom: 30px;
        }

        &:not(:first-child) {
            margin-bottom: 20px;
        }
    }
}