@import "../../assets/variables";

.app-main__outer {
  background-color: #edf0f5;
}

.dashboard-content {
  p {
    margin-bottom: 0;
  }

  .dropdown-view-container {
    display: flex;
    justify-content: center;
    margin-bottom: 25px;

    .dropdown {
      max-width: 455px;
      display: flex;
      flex-grow: 1;

      button {
        width: 100%;
        background-color: white;
        color: #000;
        border: 1px solid #b7b6b6;
      }

      .dropdown-menu {
        width: 100%;
      }
    }
  }

  .total-phone-calls {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $primary-default;
    margin-bottom: 20px;
    padding: 10px;
    color: white;

    svg {
      margin-right: 30px;

      path {
        fill: white;
      }
    }

    .total-calls-count {
      font-size: 26px;
      font-weight: bold;
      margin-right: 30px;
    }

    .total-phone-calls-title {
      margin: 0;
      font-size: 16px;
      text-transform: uppercase;
    }
  }

  .react-bootstrap-table-pagination {
    margin: 0;
  }

  .loader-container {
    position: absolute;
    left: calc(50% - 50px);
    top: calc(50% - 50px);
    z-index: 10;
  }

  .react-bootstrap-table {
    thead {
      th:nth-child(1),
      th:nth-child(3),
      th:nth-child(4),
      th:nth-child(5),
      th:nth-child(6) {
        width: 14%;
        min-width: 210px;
      }

      th:nth-child(2),
      th:nth-child(7),
      th:nth-child(8)  {
        width: 10%;
        min-width: 150px;
      }
    }
  }
}
