@import "src/assets/variables";
.FiltersBadges{
  &.opened{
    height: unset;
    overflow: auto;
  }
  height: 48px; //temporary
  overflow: hidden;
  padding: 8px 24px 0;
  
  box-shadow: inset 0 -1px 0 0 #E5E9ED;
  display: flex;
  align-items: flex-start;

  .seeAll-btn-container{
    width: 104px;
    flex: 0 0 auto;
    height: 32px;    
    margin-bottom: 8px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .dope-button{
    height: 24px;
    padding: 4px 8px;
    
    display: inline-flex;
    align-items: center;   
    .seeAll-text{
      color: #D52330;      
      font-size: 10px;
      font-weight: 700;      
      line-height: 1.3;
      text-transform: uppercase;
    }   
    .arrow-icon{
      width: 16px;
      height: 16px;
    }
  }     
  .filters-badge-list{    
    flex: 1 1 auto;
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;

    &.empty{
      align-self: stretch;
      justify-content: center;      
      align-content: center;
      margin-bottom: 8px;
    }
    .filters-badge-item{
      margin: 0 8px 8px 0;
      padding: 5px 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 32px;      
      border-radius: 20px;
      background-color: $primary-default; 
      color: #FFFFFF;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 700;      
      line-height: 1.43;
    }
    .close-icon{
      width: 16px;
      height: 16px;
      margin-left: 4px;
      flex: 0 0 auto;
      cursor: pointer;
    }
  }
  .no-filters-badge-item{    
    color: $dope-grey-100;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;      
    line-height: 1.43;
    text-align: center;
    font-weight: 400;
  }
}