@import "src/assets/variables";

.NextSendings{
  .nsend-grid{
    padding: 16px 24px;
    display: grid;
    grid-gap: 16px 16px;
    // grid-template-columns: 1fr;  
    grid-template-columns: repeat(auto-fit, minmax(auto, 144px));  
  }
  .nsend-card{
    min-height: 96px;
    padding: 8px;
    border-radius: 4px;
    background-color: #F8F9FA;
  }
  .p-item-preview{      
    flex: 0 1 auto;
    display: flex;   
    margin-bottom: 8px;   
    .preview-img-box{  
      align-self: center;      
      width: 97px;
      border: 1px solid #E5E9ED;
      border-radius: 3px;
      overflow: hidden;
      &:not(:last-child){
        margin-right: 4px;
      }
    }
    .preview-img{
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }
  .nsend-c1-text{
    display: block;
    color: #354052;    
    font-size: 14px;    
    line-height: 1.43;
  }  
  .nsend-c2-text{
    display: block;
    color: #7F8FA4;    
    font-size: 10px;    
    line-height: 1.6;
  }
}