@import "src/assets/variables";

.PostcardPreferences {
  .postcard-pricing-card {
    background-color: white;
    border-radius: 4px;
    padding: 16px 24px;
    margin-bottom: 16px;

    .postcard-printing-container{      
      margin-bottom: 16px; 
    }

    .pr-title{
      margin-bottom: 8px;
      color: $dope-grey-100;
      font-size: 14px;
      font-weight: 700;
      line-height: 20px;
    }

    .postcard-pricing-content {
      // max-width: 800px;
      display: inline-flex;
      flex-direction: column;
      min-width: 0;
      max-width: 100%;      
    }

    .company-name-title {
      margin-bottom: 24px;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
    .subscription-name{
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 24px;
      font-weight: 400;
    }
    .subscription-email{
      margin-bottom: 8px;
      font-size: 18px;
      line-height: 24px;
      font-weight: 400;
    }

    .admin-accounts-top-block {
      display: flex;
      align-items: center;
      justify-content: space-between;      
      margin-bottom: 18px;

      .header-label {
        display: flex;
        align-items: center;
        margin-bottom: 0;
        margin-right: 24px;

        .styled-checkbox {
          margin-right: 12px;
        }
      }

      .reset-postcard-pricing-btn {
        padding: 4px 8px;
        font-size: 10px;
        line-height: 16px;
      }
    }

    .postcard-pricing-table {
      margin-bottom: 16px;  
      
      &.toOneRow{
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        overflow: auto;
        .table-scroll-part{
          flex: 0 1 auto;
          display: flex;
          justify-content: flex-start;
          align-items: flex-start;
          // overflow: auto;
        }
        .fixed-table-item{
          .table-item-title{
            margin: 16px 0 4px;
            height: 38px;
          } 
          .table-item-yard-title{
            margin: 4px 0 4px;
            height: 40px;
            font-size: 14px;
          }           
          .table-bordered{
            border-left: 1px solid #e5e9ed;
          } 
          .table {              
            tbody {                
              td {
                &:first-child{
                  white-space: nowrap;
                  height: 49px;
                }                  
              }
            }
          }  
        }
        .table-item{
          flex: 0 1 auto;
          .table-item-title{
            margin: 16px 0 4px;
            height: 38px; //need to align all tables            
          } 
          .table-item-yard-title{
            margin: 4px 0 4px;
            height: 40px;
            font-size: 14px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
          } 
          .sided-title{
            font-weight: 700;
            text-transform: uppercase;
          }              
          .table-bordered{
            border-right: 1px solid #e5e9ed;
          } 
          .table {
            thead {                
              th {
                padding: 14px 4px;
              }
            }
            tbody {                
              td {
                padding: 4px 4px;
                height: 49px;
              }
            }
          } 
          .price-th{
            min-width: 120px;
          }
        }
      }      

      // .react-bootstrap-table {
      //   // overflow-x: auto;
      // }
      // .dope-input {
      //   // max-width: 80px;
      // }

      .table {
        thead {
          th{
            white-space: nowrap;
          }
          th:first-child {
            padding: 14px 0;
          }
          th:not(:first-child) {
            padding: 4px;
          }
        }
        tbody {
          td:first-child {
            padding: 4px 0;
          }
          td:not(:first-child) {
            padding: 4px;
          }
        }
      }
    }
  }

  .postcard-pricing-btn {
    display: flex;
    justify-content: flex-end;
  }

  
  .page-title-container{
    margin-bottom: 24px;
    .page-title{
      color: $dark-grey-blue;
      font-family: IBMPlexSans, sans-serif;
      font-size: 28px;
      font-weight: 700;      
      line-height: 1.14;
    }
  }
  .dcard{
    border-radius: 4px;
    background-color: #FFFFFF;   
    &:not(:last-child){
      margin-bottom: 32px;
    } 
  }
  .dcard-header{
    padding: 24px;
    box-shadow: inset 0 -1px 0 0 $dope-grey-20;
    display: flex;
    align-items: center;
    .dcard-header-icon{
      height: 24px;
      width: 24px;
      margin-right: 16px;      
      path{
        fill: $primary-default;
      }
    }
    .header-text{
      color: $dark-grey-blue;
      font-family: IBMPlexSans, sans-serif;
      font-size: 18px;
      font-weight: 700;      
      line-height: 1.33;
      margin: 0;
    }    
  }
  .dcard-body{
    padding: 24px;
    .body-title{
      color: $dark-grey-blue;
      font-family: IBMPlexSans, sans-serif;
      font-size: 18px;
      font-weight: 700;      
      line-height: 1.33;
      margin-bottom: 24px;
    }
    .check-container{
      margin-bottom: 32px;
    }
  }

  .loader-container{
    min-height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
}
.ps__thumb-x {
  background-color: $primary-disabled;  
}

.ps__thumb-y {
  background-color: $primary-disabled;  
}

.ps__rail-x:hover > .ps__thumb-x,
.ps__rail-x:focus > .ps__thumb-x,
.ps__rail-x.ps--clicking .ps__thumb-x {
  background-color: $primary-default;
}

.ps__rail-y:hover > .ps__thumb-y,
.ps__rail-y:focus > .ps__thumb-y,
.ps__rail-y.ps--clicking .ps__thumb-y {
  background-color: $primary-default;
}
