@import "../../../assets/variables";

.dope-input-group {  
  .input-group-append {
    padding: 0 1px 0 1px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid $athens-gray-approx; 
    border-left: none;
    border-radius: 0 4px 4px 0;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
    .input-icon {
      width: 18px;
      height: 18px;
      margin: 0 12px;
    }
  }
  .dope-input.form-control:focus ~ .input-group-append{
    padding: 0 0 0 1px;
    border-width: 2px;
    border-color: #bfc7d1;    
  }
  .dope-input.form-control:hover ~ .input-group-append{    
    border-color: #bfc7d1;    
  }

  .dope-input.form-control.error ~ .input-group-append {
    border-color: $primary-default;
  }
  
  .dope-select{
    flex: 1 1 auto;
    // min-width: 0;
    width: 1%;
    .dope__control {
      border-right: none;
    }
  }
  // .basic-multi-select {
  //   flex: 1 1 auto;    
  //   width: 1%;
  //   .select__control {
  //     border-right: none;
  //   }
  // }
  .dope-datepicker {
    border-right: none;
  }  
}