@import "src/assets/variables";

.PopularOrders{
  .dash-card-body{
    min-height: 208px;
    .perf-item{
      box-shadow: inset 0 -1px 0 0 #E5E9ED;      
      padding: 4px 24px;
      min-height: 52px; 
      display: flex;  
      align-items: center;
      justify-content: flex-start;
      flex-wrap: wrap; 
      @include breakpoint(xl){
        flex-wrap: nowrap; 
        justify-content: space-between;
      }  
    }
    .p-item-info{
      flex: 1 1 auto;      
      padding-right: 16px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      @include breakpoint(lg-down){
        width: 40%;
        flex: 0 1 auto; 
      }
      .p-item-name{
        color: $dark-grey-blue;
        font-size: 14px;
        line-height: 1.43;
      }
      .p-item-type{
        color: $dope-grey-100;        
        font-size: 10px;
        line-height: 1.6;
      }
    }    
    .chart-container{
      padding: 12px 80px 12px 16px;
      flex: 1 1 auto;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint(xl){
        width: 50%;
        flex: 0 1 auto;
      }
    }
    .perf-chart{
      display: block;
      position: relative;
      height: 8px;      
      border-radius: 4px;
      background-color: $primary-default;  
      transition: width 1s ease-in-out;    
      .perf-chart-value{
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(16px,-50%);
        color: $dark-grey-blue;        
        font-size: 14px;        
        line-height: 1.43;
      }
    }  
  }
}