@import "../../assets/variables";

.account-container {
  .card .card-body {
    padding: 20px 24px;
  }

  .account-input-container {
    position: relative;
  }

  .account-header {
    display: flex;
    justify-content: space-between;

    h5 {
      font-size: 16px;
      color: $regent-gray;
    }

    p {
      font-size: 20px;
      color: $primary-default;
      font-weight: 600;
      margin: 0;
    }
  }

  .profile-title {
    font-size: 18px;
    font-weight: 600;
    color: $dark-grey-blue;
    margin-bottom: 20px;
  }

  .account-form-container {
    .account-form {
      display: flex;
      flex-direction: column;      
      max-width: 500px;
    }

    .account-button-container {
      display: flex;
      justify-content: flex-end;
      margin: 24px 0 auto 0;
    }
  }

  .alert {
    &.alert-warning {
      background-color: rgba($cherokee, 0.24);
      color: $brown;
      font-weight: 500;

      svg {
        margin-right: 20px;

        path {
          fill: $tangerine;
        }
      }

      .close {
        color: $tangerine;
      }
    }

    &.alert-success {
      background-color: rgba($celadon, 0.24);
      color: $everglade;
      font-weight: 500;

      svg {
        margin-right: 20px;

        path {
          fill: $stromboli;
        }
      }

      .close {
        color: $stromboli;
      }
    }

    &.alert-danger {
      background-color: rgba($geraldine, 0.24);
      color: $primary-default;
      font-weight: 500;

      svg {
        margin-right: 20px;

        path {
          fill: $primary-default;
        }
      }

      .close {
        color: $primary-default;
      }
    }
  }
}
