@import "../../../../assets/variables";

.auth-container {
  .forgot-password-form-email {
    max-width: 432px;
    padding: 24px;
    background-color: white;
    box-shadow: $form-shadow;    

    .forgot-password-text {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 24px;
      color: $dark-grey-blue;
    }
    .dope-form-group.email-group{
      margin-bottom: 56px;
    }

    .forgot-password-form-bottom {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;

      a {
        color: $primary-default;
        text-decoration: underline;
      } 
    }

    .input-title {
      font-size: 14px;
    }
  }
}
