@import "../../../assets/variables";

.company-logo-container {
  max-width: 250px;

  h4, span {
    text-align: center;
  }

  .company-name {
    font-weight: 500;
  }

  .created-label {
    display: block;
    font-size: 13px;
    color: $regent-gray;
  }

  .default-logo-container, 
  .logo-container {
    height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;

    img {
      max-height: 100%;
      max-width: 100%;
    }
  }

  .default-logo-container {
    background-color: $athens-gray;
    
    svg {
      height: 150px;
      width: 150px;
    }
  }

  .upload-logo {
    display: flex;
    justify-content: center;
    color: $primary-default;
    font-weight: bold;
    padding: 20px 0;

    &:hover {
      color: $primary-hover;
      cursor: pointer;
    }
  }

  .upload-logo.editable {
    visibility: hidden;
  }
}