@import "src/assets/variables";
.AutomationPreview{
  .back-btn-container{
    margin-bottom: 24px;
    .back-btn{
      height: 24px;
      padding: 4px 8px;
      border: none;
      @include outline(0);
      border-radius: 4px;
      background-color: $dope-grey-10;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: $primary-default;
      font-family: IBMPlexSans, sans-serif;
      font-size: 10px;
      font-weight: 700;      
      line-height: 1.6;
      text-transform: uppercase;
    }
    .back-icon{
      height: 16px;
      width: 16px;
      margin-right: 4px;
    }
  }
  .sub-title{
    font-family: IBMPlexSans, sans-serif; 
    font-size: 16px;
    @include breakpoint(xl){
      font-size: 22px;
    }
    line-height: 1.27;   
    font-weight: 700;
    color: $dark-grey-blue; 
    margin-bottom: 16px;    
    .att-icon{
      position: relative;      
      transform: translate(50%,-50%);
      width: 16px;
      height: 16px;
      &.error{
        path{
          fill: $primary-default;
        }
      }
      &.success{
        path{
          fill: #39B54A;
        }        
      }
    }
  } 
  .preview-header{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;    
    .sub-title,.title{
      margin-bottom: 0;
    }
    margin-bottom: 8px;
  }
  .approval-grid{
    @include breakpoint(xl){
      display: flex;
    }
    &:not(:last-child){
      margin-bottom: 8px;
    }
  }
  .approval-grid-item{
    &:not(:last-child){
      margin: 0 0 8px 0;
    }
    @include breakpoint(xl){
      flex: 0 1 auto;
      max-width: 320px;
      width: 50%;
      &:not(:last-child){
        margin: 0 15px 0 0;
      }
    }
    .item-title{
      font-family: IBMPlexSans, sans-serif;
      color: #7F8FA4;
      font-size: 14px;
      font-weight: 700;
      line-height: 1.43;
    }
    .img-prev-container{
      overflow: hidden;
    }
  }
  .list-info{
    @include breakpoint(xl){
      display: flex;
    }
    .dope-form-group{
      &:first-child{         
        @include breakpoint(xl){
          margin-right: 16px;
        }
      }
      &:last-child{
        flex: 1 1 auto;
      }
    }
  }
  .urlInput-container{
    display: inline-flex;
    position: relative;
    margin-bottom: 24px;
    max-width: 100%;
    .CopyUrlIcon{
      position: absolute;
      // left: 8px;
      right: 12px;
      top: 50%;
      transform: translate(0,-50%);
      cursor: pointer;
      path{
        transition: fill 0.8s ease-in-out;        
      }
    }
    &:hover{
      .CopyUrlIcon{
        path{
          fill: #1D73FF;
        }
      }
    }
    .urlInput{
      cursor: pointer;
      @include outline(0);
      // padding: 10px 16px 10px 48px;
      padding: 10px 48px 10px 16px;
      border-radius: 4px;
      background-color: #F8F9FA;
      border: none;
      height: 40px;
      width: 600px;
      max-width: 100%;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 400;      
      line-height: 1.42;
      color: #1D73FF;      
    }
    .urlcopy-text{
      font-size: 12px;
      position: absolute;
      // left: 0;
      right: 0;
      bottom: 0;
      transform: translate(0,50%);
      color: $primary-default;
    }
  }
  .bnts-container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 32px;
    .dope-button{
      &:not(:last-child){
        margin-right: 16px;
      }
    }
  }
  .automation-recharge-card {
    .card-body {
      padding: 16px 24px;
    }
    .automation-recharge-block {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @include breakpoint(xs-only){
        flex-wrap: wrap;
        .dope-button{
          margin-left: auto;
        }
      }      
      .i-t-box{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include breakpoint(xs-only){
          flex: 1 1 100%;
          margin-bottom: 8px;
          flex-direction: column;
        }
      }
      .text{
        margin: 0 12px 0 0;
      }
      .icon{
        flex: 0 0 auto;
        margin-right: 16px;
        @include breakpoint(xs-only){
          width: 40px;
          height: 40px;
        }
        path {
          fill: $alert-default;
        }
      }
    }
  }
  .table-wrapper{
    max-width: 100%;
    overflow-x: auto;
    th{
      white-space: nowrap;
      &:nth-child(1),&:nth-child(2){
        min-width: 140px;
      }
      &:nth-child(3){
        min-width: 190px;
      }
    }
  }
  .aut-nbh-table-wrapper{
    .expand-cell-header{
      width: 66px;
    }    
    .dope-table-container .react-bootstrap-table .table tbody .reset-expansion-style {
      padding: 0;
    }
    .dope-table-container .react-bootstrap-table .table thead th.expand-cell-header{
      pointer-events: none;
      color: transparent;
      // opacity: 0;
      // visibility: hidden;      
    }
    th{
      white-space: nowrap;
      &:nth-child(2){
        width: 220px;
      }
      &:nth-child(3){
        width: 250px;
      }
    }
  }
  .nbh-table-wrapper{ 
    height: 350px;
    overflow-y: auto;
    position: relative;    
    .loader-nbh {
      position: absolute;
      top: calc(50% - 50px);
      left: calc(50% - 25px);
      z-index: 10;
    }
    th{
      white-space: nowrap;      
    }
  }  
}