@import "src/assets/variables";
.startloader{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #fff;
  .loading-gif{
    max-width: 300px;
  }
}
.isMobileModal{
  .icon-container{
    display: flex;
    justify-content: center;
    margin-bottom: 8px;
    .icon{
      width: 48px;
      height: 48px;
      path{
        fill: var(--color-primary, #d52330);
      }            
    }
  }
  .modal-title{
    font-size: 28px;
    font-weight: 700;
    text-align: center;
    color: $dark-grey-blue;
    margin-bottom: 12px;
  }
  .modal-text{
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    color: $dark-grey-blue;
    margin-bottom: 28px;
  }
  .dope-modal-footer{
    justify-content: center;
    .dope-button{
      min-width: 100px;
    }
  }  
}