@import "../../../assets/variables";

.verification-form {
  .circle-container {
    display: flex;
    justify-content: center;
    margin-bottom: 24px;
    .circle {
      height: 48px;
      width: 48px;
      background-color: $primary-default;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      svg{
        width: 24px;
        height: 24px;
      }
    }
  }
  .auth-form-title {
    text-align: center;
  }  
}
