@import "src/assets/variables";

.modal-dialog.add-contact {
  max-width: 536px;
  border-radius: 4px;

  // .modal-content {
  //   background-color: white;
  // }
}

.add-contact {
  .modal-content {
    padding: 16px 16px 16px 24px;
  }

  .create-new-list div[class*=menu] > div:first-child > div:first-child  {
    font-weight: bold;
  }

  .modal-body,
  .modal-header,
  .modal-footer {
    padding: 0;
    border: none;
    background-color: white;
  }

  .dope-modal-header {
    border-radius: 4px 4px 0 0;
    margin-bottom: 18px;

    .modal-title {
      font-size: 22px;
      line-height: 28px;
      color: $dark-grey-blue;
      font-weight: bold;
    }
  }

  .dope-form-group {
    margin-bottom: 24px;    

    .input-title {
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $regent-gray;
      margin-bottom: 0;
    }

    input {
      border: 1px solid $athens-gray-approx;
      border-radius: 4px;
    }

    .dope-input.form-control.error ~ .input-group-append {
      border-color: $primary-default;
    }
  }

  .input-block {
    .dope-select {
      [class*="-control"] {
        border-color: $athens-gray-approx;
        box-shadow: none;
      }
    }

    &.select-error {
      div[class*=control] {
        border-color: $primary-default;
      }
    }
  }

  .one-line {
    display: flex;

    .dope-form-group {
      width: 50%;
      max-width: 50%;

      &:first-child {
        margin-right: 16px;
      }
    }
  }

  sup {
    color: $primary-default;
  }

  .modal-footer {
    .btn:not(:last-child){
      margin-right: 16px;
    }
    .dope-button {
      border-radius: 4px;
      font-size: 14px;
      line-height: 20px;
      font-weight: bold;

      &.btn-secondary {
        background-color: #f8f9fa;
        color: $primary-default;        

        &:hover,
        &:focus {
          background-color: #f2f3f5 !important;
          color: $primary-default !important;
        }

        &:active {
          background-color: #e5e9ed !important;
          color: $primary-default !important;
        }

        &:disabled {
          background-color: #f8f9fa;
          color: #bfc7d1;
        }
      }
    }
  }
}
