@import "src/assets/variables";

.YardSignBuilder {    
  position: relative;
  @media screen and (min-width: 568px){
    padding: 20px;
  }
  @include breakpoint(xl){
    display: flex;
    align-items: flex-start;
  }
  
  .spects-container{
    display: none;    
  }
  .Canvas-container{     
    // box-shadow: 0 6px 10px -4px rgba(0, 0, 0, 0.15);
    background-color: #FFFFFF;         
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;
    padding: 10px;

    position: absolute;
    z-index: 10;
    top: 0%;
    left: 0%;    
    .react-pdf__Page.dope-canvas-hided{
      display: none;
    }
    .pdf-action-container{
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .pdf-num-text{
      color: $dark-grey-blue;
      font-size: 12px;
      line-height: 14px;
      margin-right: 8px;
    }
    .btn-container{
      display: flex;
      flex: 1 1 auto;
      justify-content: flex-end;
      &:not(:last-child){
        margin-right: 8px;
      }
      .dope-button:not(:last-child){
        margin-right: 8px;
      }
    }
    .btn-icon{
      width: 20px;
      height: 20px;
    }
    .loader-main {
      position: absolute;
      top: calc(50% - 50px);
      left: calc(50% - 25px);
      z-index: 10;
    }
  }
  .csi-editor-container{   
    position: relative; 
    z-index: 5;        
    
    display: flex;
    flex-direction: column;
    .allResize-container{
      margin-bottom: 18px;
      @include breakpoint(sm){
        display: flex;         
      }     
    }
    @include breakpoint(xl){
      margin-left: 15px;
      // min-height: 450px;
      align-self: stretch;// don't toch
      // align-items: stretch;  
      flex: 1 1 auto;
      min-width: 0;
    }
    .reset-btn {
      margin: auto auto 0 0;
      button {
        display: flex;
        align-items: center;
        color: $primary-default;
        border: none;
        outline: none;
        background-color: #fff;
        cursor: pointer;
        svg {
          height: 19px;
          width: auto;
          margin-right: 5px;

          path {
            fill: $primary-default;
          }
        }
        &:disabled{
          color: $primary-disabled;
          svg { 
            path {
              fill: $primary-disabled;
            }
          }
        }
      }
    }
  }  
  .color-picker-block{
    background-color: rgb(255, 255, 255);
    border: 0px solid rgba(0, 0, 0, 0.25);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;     
    padding: 10px;
    
    // display: flex;
    // flex-wrap: wrap; 
    display: grid;
    justify-content: flex-start;
    grid-template-columns: repeat(auto-fit, minmax(210px, auto) );
    grid-gap: 15px 15px;
    &:not(:last-child){
      margin-bottom: 15px;
    }   
    // .color-picker-item{ 
    //   margin-bottom: 5px;     
    //   &:not(:last-child){
    //     margin-right: 15px;
    //   }
    // }
    .sketch-picker{
      padding: 5px 5px 0px!important;
      .flexbox-fix{        
        input{
          font-size: 10px!important;
        }
        // input+span{
        //   // font-size: 8px!important;
        // }
        &:nth-child(3){          
          >div:not(:first-child){
            padding-left: 2px!important;
          }
        }
        &:nth-child(4){  
          margin: 0px -5px!important;
          padding: 5px 0px 0px 5px!important;       
          >div{
            margin: 0px 6px 6px 0px!important;
          }
        }
      }
    }
  }  

  .upload-img-container {
    // width: 276px;
    background-color: rgb(255, 255, 255);
    border: 0px solid rgba(0, 0, 0, 0.25);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;    
    @include breakpoint(xl){
      display: flex;
      // flex-direction: column;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
    }    
    padding: 10px;  
    margin-bottom: 15px;  
    // .ReactCrop{width: 500px;} 
  }

  .image-action-container{
    &:not(:last-child){
      margin: 0 0 15px 0;
      @include breakpoint(xl){
        margin: 0 15px 0 0;
      }   
    }     
  }

  .image-action-box{
    display: flex;
    flex-direction: column;
    &:not(:last-child){
      margin-bottom: 8px;
    }
    .btn{      
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }    
  } 
  .image-CD-box{
    .image-CD-title{
      font-size: 16px;
      margin: 5px;
    }
    // .image-CD-editor{}
    .btn{      
      &:not(:last-child){
        margin-right: 8px;
      }
    } 
  }

  // editable/draggable
  .builder-template-focused-item{
    &[contenteditable="true"]{
      cursor: text!important;
    }
    .contenteditable-marker{
      pointer-events: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &:before{
        content: '';
        pointer-events: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border: 20px dashed #f00;
      } 
    }
  }  
  [draggable="true"]{
    // cursor: move; /* fallback if grab cursor is unsupported */
    // cursor: grab;
    // cursor: url(https://dope360-public.s3.amazonaws.com/static/cursors/openhand.cur), default; 
    *{
      cursor: move!important; /* fallback if grab cursor is unsupported */
      cursor: grab!important;   
      cursor: url(https://dope360-public.s3.amazonaws.com/static/cursors/openhand.cur), default !important;      
    }    
  }
  .draggable-cursor {      
    //  cursor: grabbing; 
    //  cursor: url(https://dope360-public.s3.amazonaws.com/static/cursors/closedhand.cur), default;
     *{
      cursor: grabbing!important;   
      cursor: url(https://dope360-public.s3.amazonaws.com/static/cursors/closedhand.cur), default !important;      
    } 
  }
}

@media screen and (max-width: 1600px) {
  .YardSignBuilder {
    .color-picker-container {
      flex-direction: row;
    }
  }
}

.YardSignBuilder{
  .template-container {
    position: relative;
    .Morpheus-card-container{
      pointer-events: auto!important;
      .morpheus-text-token{
        visibility: hidden;
      }
    }               
  } 
}

.YardSignBuilder {  
  .template-container {    
    flex: 0 1 auto;     
    transform-origin: left top;    
  }  
  &.size-ys1{
    .template-container{
      height: 225px;
      width: 320px;
      transform: scale(0.04);
    }
  }
  &.size-ys2{
    .template-container{
      height: 185px;
      width: 300px;
      transform: scale(0.05); 
    }
  }  
}
@media screen and (min-width: 568px){
