@import "src/assets/variables";
.EddmEditor{
  .morpheus-templates-container {  
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .nav-link {
      padding: 8px 16px;
      &.active{
        pointer-events: none;
      }
    }

    .step-container {
      margin-bottom: 20px;
      padding: 20px 0 0 20px;
    }

    .dope-nav.nav {
      padding-left: 0;
    }

    .dope-tab-content {
      width: 100%;
      padding-bottom: 0;    
    }

    .dope-pagination{
      margin-bottom: 0;
    }

    
    .templates-preview-container {
      display: flex;
      flex-wrap: wrap;
      min-height: 390px; 
      position: relative; 
      &.oneRow{
        min-height: 195px;
      }   

      &.disabled {
        pointer-events: none;
      }
      .loader-main{
        position: absolute;  
        z-index: 10;    
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }

      .no-templates-container {
        display: flex;
        justify-content: center;      
        font-size: 24px;
        color: $dope-grey-50;
        flex: 1 1 auto;
        align-items: center;
      }    

      .template-preview-item {      
          flex: 0 1 50%;
        @include breakpoint(xl){
          flex: 0 1 20%;
        }      
        padding: 15px;
        min-width: 0;
        margin-bottom: 12px;
      }

      .template-preview-content {
        border: 1px solid #b7b6b6;
        padding: 10px;
        position: relative;
        &.checked{
          &:before {
            content: "";
            background-color: rgba(0, 0, 0, 0.5);
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;          
          }
          &:after {
            content: "";
            display: block;
            // TODO change to react svg object
            background: url("../../../../../assets/img/icons/select.svg") no-repeat center;
            height: 62px;
            width: 62px;
            background-size: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
          }
        }
      }

      .template-preview-img{
        width: 100%;
        height: auto;
      }      
    }    
  }
  .template-item{
    position: relative;
    cursor: pointer;
    .template-preview-content{
      position: relative;    
      z-index: 1;
      background-color: #fff;  
    }
    .postcards-preview-name-block {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-top: 5px;
      .name-text{        
        padding-left: 2px;
        font-size: 14px;
        font-weight: 500;
        color: #515557;
        min-width: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        // width: calc(100% - 32px);
      } 
    }
    .postcards-action-block {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      padding: 2px 5px;
      svg path {
        fill: $primary-default;
        cursor: pointer;
      }
      transition: 1s transform ease-in-out 0.5s;
      transform: translate(0,0%);
    }
    &:hover,&:focus{
      .postcards-action-block{
        transform: translate(0,100%);
        transition: 1s transform ease-in-out;
      }
    } 
  }
}
