.colorPicker {
  position: relative;
  transition: opacity 0.2s ease-in-out;
  &:hover {
    .colorPicker__close {
      opacity: 1;
      transition: opacity 0.1s ease-in-out;
    }
  }
  &__swatch {
    padding: 1px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 0 1px rgba(0, 0, 0, .1);
    display: inline-block;
    cursor: pointer;
    margin: 5px;
    transition: all 0.2s ease-in-out;

    &:hover {
      box-shadow: 0 1px 11px 0 rgba(0, 0, 0, 0.4);
      transition: all 0.2s ease-in-out;
    }
  }

  &__popover {
    position: absolute;
    z-index: 2;
  }

  &__cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  &__close {
    color: black;
    position: absolute;
    top: 0;
    right: 0;
    background: #fff;
    border-radius: 50%;
    height: 14px;
    width: 14px;
    cursor: pointer;
    opacity: 0;
    &:hover {
      opacity: 1;
    }
    i {
      overflow: hidden;
    }
  }
}