@import "../../assets/variables";

.phones-page {
  .phones-page-title {
    display: flex;
    justify-content: space-between;

    h2 {
      font-size: 28px;
      line-height: 37px;
      font-weight: bold;
      color: $dark-grey-blue;
      margin-bottom: 42px;
    }
  }

  .phones-table {
    .btn-secondary.disabled {
      background-color: transparent;
      border-color: transparent;
    }
  }

  .react-bootstrap-table {
    thead {
      th:nth-child(1) {
        width: 20%;
      }

      th:nth-child(3) {
        width: 10%;
      }

      th:nth-child(2),
      th:nth-child(4) {
        width: 15%;
      }

      th:nth-child(5) {
        width: 15%;
      }

      th:nth-child(6) {
        width: 15%;
      }

      th:nth-child(1) {
        min-width: 150px;
      }

      th:nth-child(3) {
        min-width: 150px;
      }

      th:nth-child(2),
      th:nth-child(4) {
        min-width: 180px;
      }

      th:nth-child(5) {
        min-width: 150px;
      }

      th:nth-child(6) {
        min-width: 150px;
      }
    }

    .status-container {
      position: relative;
      max-width: 88px;
      display: flex;
      justify-content: center;

      .status-text {
        font-size: 10px;
        line-height: 16px;
        color: white;
        font-weight: bold;
        text-transform: uppercase;
        border-radius: 4px;
        width: 52px;
        text-align: center;
        z-index: 2;
      }

      .status-line {
        height: 2px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
      }

      .arrow-right {
        width: 9px;
        height: 9px;
        border-top: 2px solid $primary-default;
        border-left: 2px solid $primary-default;
        transform: rotate(135deg);
        position: absolute;
        z-index: 4;

        &:first-child {
          left: 0;
          top: 25%;
        }

        & {
          right: 6px;
          top: 25%;
        }
      }

      &.active {
        .status-text {
          background-color: $primary-default;
        }

        .status-line {
          background-color: $primary-default;
          width: 85%;
        }

        .arrow-right {
          border-top: 2px solid $primary-default;
          border-left: 2px solid $primary-default;
          transform: rotate(135deg);

          &:first-child {
            left: 0;
            top: 25%;
          }

          & {
            right: 6px;
            top: 25%;
          }
        }
      }

      &.inactive {
        .status-text {
          background-color: $dope-grey-50;
        }

        .status-line {
          background-color: $dope-grey-50;
          width: 100%;
        }

        .arrow-right {
          border-left: 3px solid white;
          border-top: none;
          transform: rotate(45deg);

          &:first-child {
            left: 6px;
            top: 35%;
          }

          & {
            right: 0;
            top: 35%;
          }
        }
      }
    }
  }
  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
