@import "../../../../../assets/variables";
.listMapContainer{
  .leaflet-container {
    z-index: 1;
    flex: 1 1 auto;
    width: 100%;
    .leaflet-interactive:not(.custom-icon){
      stroke-width: 2px;
      stroke: $primary-default;
      fill: $primary-default;
      &:hover{
        fill-opacity: 0.6;
      }
    }
    .custom-icon{
      svg{
        width: 50px;
        height: 50px;
      }
      &.custom-marker{
        path{
          fill: $primary-default;
        }
      }
    }
    .loading-indicator{
      display: none;
      background: $white;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16);
      border-radius: 4px;
      padding: 15px;
      font-family: IBMPlexSans, sans-serif;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      color: $positive-default;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 500;
    }
    &.loading{
      position: relative;
      &:after{      
        opacity: 0.1;
      }
      .loading-indicator{
        display: block;
      }
    }
  }
  .marker-cluster-small,
  .marker-cluster-medium,
  .marker-cluster-large{
    background-color: rgba(0, 0, 0, 0.6);
    div {
      background-color: rgba(0, 0, 0, 0.6);
      color: #fff;
    }
  }
  .marker-cluster {
    background-clip: padding-box;
    border-radius: 20px;
    div {
      width: 30px;
      height: 30px;
      margin-left: 5px;
      margin-top: 5px;
      text-align: center;
      border-radius: 15px;
      font: 12px "Helvetica Neue", Arial, Helvetica, sans-serif;
    }
    span {
      line-height: 30px;
    }
  }
}
