.account-container {
  .dope-nav {
    

    .dope-nav-item {
      flex-grow: 1;
      max-width: 210px;
      .nav-item-container{
        position: relative;        
        .attention-icon{
          position: absolute;
          top: 0;
          right: 0;
          transform: translate(60%, -60%);
          width: 22px;
          height: 22px;
          path{
            fill: #D52330;
          }
        }        
      }
      .nav-link {
        display: flex;
        justify-content: center;
      }
    }
  }
}