@import "src/assets/variables";
.ListDetail{
  .main-title{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.28;
    color: $dark-grey-blue;
    margin-bottom: 16px;
  }
  .listMap{
    display: flex;
    margin-bottom: 40px;
    .listMapSummary{
      flex: 0 1 auto;
      width: 280px;
      background: #FFFFFF;
      box-shadow: inset 1px 0 0 #E5E9ED;
      padding: 0 24px;
      display: flex;
      flex-direction: column;
      .listMapSummaryTitle{
        min-height: 70px;
        border-bottom: 1px solid #F2F3F5;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-family: IBMPlexSans, sans-serif;
        font-weight: 700;
        font-size: 18px;
        line-height: 1.33;
        text-align: center;
        color: $dark-grey-blue;
        @include breakpoint(xl){
          width: 100%;
        }
      }
      .listMapSummaryItem{
        border-bottom: 1px solid #F2F3F5;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-family: IBMPlexSans, sans-serif;
        text-align: center;
        color: $dark-grey-blue;
        .itemTitle{
          font-size: 14px;
          line-height: 1.43;
          margin-bottom: 8px;
        }
        .itemValue{
          flex: 0 0 auto;
          font-weight: 700;
          font-size: 22px;
          line-height: 1.27;
          &.empty{
            color: $dope-grey-50;
          }
          &.sm{
            font-weight: 400;
            font-size: 16px;
          }
          .size-title{
            font-size: 0.7em;
          }
        }
        .table tr{
          cursor: initial;
          display: table;
          width: 100%;
          table-layout: fixed;
        }
      }
      .dope-table-container{
        width: 100%;
        th{
          padding: 14px 5px;
          span.order {
            position: absolute;
          }
        }
        td{
          border: none;
          padding: 5px;
        }
        .react-bs-table-no-data{
          display: none;
        }
        tbody{
          display: block;
          max-height: 325px;
          overflow-y: auto;
        }
      }
    }
    .listMapContainer {
      flex: 1 1 auto;
      display: flex;
      position: relative;
      height: 462px;
      &.eventNone{
        pointer-events: none;
        .leaflet-control {
          pointer-events: none;
        }
      }
    }
  }
  .dope-nav.nav,
  .tab-content{
    padding: 0;
    margin: 0;
  }
}