@import "../../../../assets/variables";

.auth-container {
  .new-password-form {
    .dope-input-group {
      margin-bottom: 0;
    }

    #password + .validation-message {
      display: none;
    }

    .input-block:first-child {
      margin-bottom: 10px;
    }

    .input-group-append {
      cursor: pointer;

      .active {
        -webkit-filter: brightness(0);
        filter: brightness(0);
      }
    }

    .forgot-password-header {
      font-size: 24px;
      font-weight: 600;
      color: $dark-grey-blue;
      margin-bottom: 30px;
    }

    .new-password-form-bottom {
      display: flex;      
      justify-content: space-between;
      align-items: center;

      a {
        color: $primary-default;
        text-decoration: underline;
      }

      .link-block {
        a:first-child {
          margin-right: 40px;
        }
      }
      
      // button {
      //   border-radius: 4px;
      // }
    }

    .input-title {
      font-size: 14px;
    }
  }
}
