@import "../../assets/variables";

.contacts-page {
  .card .contacts-body {
    padding: 0;
    .general-tab.active{
    // highlite empty td city/state/zip5
      .table-row{
        td{        
          &:nth-child(2){
            padding: 0;
            .verified-cell{
              padding: 15px 24px 15px 14px;
              &.small-cell {
                padding: 8px 24px 2px 14px;
              }
              &.isBlank{              
                background-color: #ffe2e4;
              }
            }          
          } 
          &:nth-child(4){
            &:empty{
              background-color: #ffe2e4;
            }
          }        
          &:nth-child(6),&:nth-child(7),&:nth-child(8){
            &:empty{
              background-color: #ffe2e4;
            } 
          }               
        }
      }
    }
  }  
  .dope-nav{
    padding-left: 16px;
    padding-top: 8px;
  }

  .tab-content{
    padding: 0;
  }
  .card-title {
    text-transform: none;
    font-size: 24px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1;
    letter-spacing: normal;
    color: #000000;
  }

  .delete-button {
    display: flex;
    border-radius: 3px;
    width: 38px;
    height: 38px;
    align-items: center;
    justify-content: center;
    margin-left: 12px;

    &.disabled {
      opacity: 0.5;
      pointer-events: none;
    }
    &:not(.disabled):hover {
      cursor: pointer;
      path {
        fill: $primary-default;
      }
    }

    .delete-icon {
      display: block;
      width: 28px;
      height: 28px;
    }
  }

  .contacts-controls {
    display: flex;
    margin-bottom: 24px;

    .button-group {
      width: 100%;
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      padding: 0;

      .dope-button {
        margin-right: 12px;

        &:last-child {
          margin: 0;
        }
      }
    }
  }

  .list-cell{
    .list-text{
      display: block;
      white-space: nowrap;
      max-width: 200px;
      overflow: hidden;      
      text-overflow: ellipsis;
    }      
  }

  .react-bootstrap-table {
    .general-tab {
      .react-bootstrap-table {
        thead {
          th:nth-child(2) {
            width: 300px;
            min-width: 300px;
          }
    
          th:nth-child(3),
          th:nth-child(4),
          th:nth-child(5),
          th:nth-child(6) {
            width: 200px;
            min-width: 200px;
          }
    
          th:nth-child(7),
          th:nth-child(8),
          th:nth-child(9),
          th:nth-child(10),
          th:nth-child(11),
          th:nth-child(12),
          th:nth-child(13) {
            width: 140px;
            min-width: 140px;
          }
        }
      }
    }

    .icon-button {
      width: 24px;
      & > i,
      img {
        width: 24px;
        height: 24px;
        font-size: 16px;
        color: black;
      }
    }

    .contact-cell {
      .invalidated-contact {
        font-size: 10px;
        line-height: 20px;
        // color: $primary-default;
        color: #d52330;
      }
    }
    .list-cell{
      .list-text{
        display: block;
        white-space: nowrap;
        max-width: 200px;
        overflow: hidden;      
        text-overflow: ellipsis;
      }  
      .contact-list-tooltip{        
        max-width: 600px;               
        // background-color: $primary-default;
        border-radius: 4px;
        // &.place-top:after{          
        //   border-top-color: $primary-default;
        // }
        // &.place-bottom:after{          
        //   border-bottom-color: $primary-default;
        // }
        // &.place-left:after{          
        //   border-left-color: $primary-default;
        // }
        // &.place-right:after{          
        //   border-right-color: $primary-default;
        // }
        span{
          display: block;
          font-size: 14px;
          font-weight: 700;          
          line-height: 1.43;          
          color: #ffffff;
        }
      }    
    }
  }
  
  .react-bootstrap-table {
    thead {
      th:not(:first-of-type) {
        width: 20%;
        min-width: 200px;
      }

      th:nth-child(2) {
        width: 300px;
        min-width: 300px;
        padding-left: 0;
      }

      th{
        position: relative;
        span.order{
          position: absolute;
        }
      }
    }

    tbody td:nth-child(2) {
      padding-left: 0;
    }
  }
}

.contacts-page {

  .contact-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;    
  }

  .title {
    font-size: 28px;
    font-weight: 700;
    margin: 0;
    line-height: normal;
    color: #354052;
  } 

  .contact-header {
    box-shadow: inset 0 -1px 0 0 #E5E9ED;
    @include breakpoint(xl){
      display: flex;  
      align-items: center;    
    }    
    padding: 8px 24px;
  }  
  .select-All-container{
    margin-right: 24px;
    .dope-button{
      font-size: 10px;
      line-height: 16px;
      padding: 4px 8px;
    }      
  }
  .contacts-selected {
    margin: 0 0 8px 0;
    @include breakpoint(xl){
      margin: 0 16px 0 0;
    }     
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    // flex-wrap: wrap;

    .dope-button.btn-primary, 
    .dope-button.btn-white {
      font-size: 10px;
      line-height: 16px;
      padding: 4px 8px;
    }

    // .dope-button.btn-primary {
    //   margin-right: 8px;
    // }

    .contacts-selected-count {      
      margin-right: 16px;
      span {
        color: $primary-default;
      }      
    }
    .btn-section{
      display: flex;
      align-items: center;      
      margin-right: 16px;
      .dope-button:not(:last-child){
        margin-right: 8px;
      }
    }
  }

  .contact-header-right {    
    display: flex;
    margin: 0 0 8px 0;
    @include breakpoint(xl){
      margin: 0 16px 0 0;
    } 
    &.contact-header-right-disabled {
      pointer-events: none;
    }
    .checkbox-container {
      margin-right: 12px;
    }
    Label {
      display: flex;
      align-items: center;
      cursor: pointer;
      font-size: 14px;
      color: $dark-grey-blue;
    }
    .DopeCheckbox{
      margin: 0;
    }
  }

  .contacts-count {
    // height: 24px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    margin: 0 0 8px 0;
    @include breakpoint(xl){
      margin: 0 16px 0 0;
    }  
    span {
      color: $primary-default;
    }     
  }
  .contact-filters{
    @include breakpoint(xl){
      display: flex;
      align-items: center;
    }
  }
  .filter-container{
    .dope-button.filtered{
      position: relative;
      &:after{
        content: '';
        position: absolute;
        top: 5px;
        right: 5px;
        background-color: #f00;
        border-radius: 50%;
        width: 8px;
        height: 8px;
      }
    }
  }
  .search-container{
    margin: 0 0 8px 0;
    @include breakpoint(xl){
      margin: 0 16px 0 0;
    }  
    .dope-search-container{
      margin: 0;
    }
  }
  .contacts-matched-container{
    color: #354052;    
    font-size: 14px;    
    line-height: 1.43;        
  } 
  
  .dope-tab-content{
    .noContacts{
      min-height: 92px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $dope-grey-50;
      font-family: IBMPlexSans, sans-serif;
      font-size: 22px;
      font-weight: 700;      
      line-height: 1.27;
      text-align: center;
      border-top: 1px solid #dee2e6;
      box-shadow: inset 0 -1px 0 0 #E5E9ED;
      margin-bottom: 8px;
      position: relative;
      .loader-container{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }  
}