
@import "../../../assets/variables";

.dope-dropdown {
  button.dots-btn {
    border-radius: 4px;
    border: none;
    padding: 0;
    transition: none;

    &:focus,
    &:hover {
      background: transparent !important;
      color: $dope-grey-100 !important;
    }
  }
}

.tracking-numbers-dots-menu.dropdown-menu  {
  border-radius: 4px;
    box-shadow: none;
    border: 1px solid $dope-grey-20;

    &::before,
    &::after {
      display: none;
    }

    .dropdown-item {
      color: $dark-grey-blue;

      &:not(:first-child){
      border-top: 1px solid $dope-grey-20;
      }

      &:last-child {
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
      }

      &:first-child {
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
      }

      &:hover{      
        background-color: $primary-hover; //#C42533;
        // color: #ffffff;
      }
      &:focus,&:active {
        background-color: $primary-pressed;//#b52937
        // color: #ffffff;
      }      
    }
}

.phones-table {
  tbody tr:hover .dots-btn{
    background-color: $dope-grey-5 !important;
  }
}
