@import "../../assets/variables";

.auth-container {
  display: flex;
  padding: 88px 0 24px 0;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  // overflow: hidden;
  min-height: 100vh;
  background-color: $mystic;
  position: relative;

  .logo-container {
    position: absolute;
    top: 24px;
    height: 40px;
    img{
      width: auto;
      height: 100%;
      max-height: 100%;
      max-width: 100%;
    }
  }

  .dope-input-group{
    margin-bottom: 0;
  }

  .input-group-append {
    cursor: pointer;

    .active path {
      fill: $primary-default;
    }
  }

  .auth-link {
    font-size: 14px;
    line-height: 24px;
    color: $primary-default;
    cursor: pointer;
    text-decoration: underline;
  }

  .login-form,
  .forgot-password-form,
  .new-password-form,
  .sign-up-form,
  .verify-email,
  .verify-email-message,
  .account-details-form,
  .verification-form {
    width: 432px;
    max-width: 100%;
    padding: 24px;
    background-color: white;
    box-shadow: $form-shadow;    
  }

  .auth-form-title {
    font-size: 26px;
    line-height: 32px;
    color: $dark-grey-blue;
    font-weight: 600;
    margin-bottom: 24px;
  }

  // .dope-button {
  //   font-size: 14px;
  //   line-height: 24px;
  //   padding: 8px 16px;
  // }

  small {
    color: $regent-gray;
    font-size: 10px;

    &.validation-message {
      position: static;
      color: #d52330;
    }
  }

  .dope-form-group {
    margin-bottom: 24px;    

    .input-title {
      font-size: 14px;
      line-height: 24px;
      // font-weight: 600;
      // color: $regent-gray;
      margin-bottom: 0;
    }

    // .form-control {
    //   border: 1px solid $athens-gray-approx;      
    // }

    .dope-input-group .input-group-append {
    //   border: 1px solid $athens-gray-approx;
    //   border-left: none;
    //   border-radius: 0 4px 4px 0;

      .input-icon {
        width: 24px;
        height: 24px;
      }
    }

    // .dope-input.form-control.error ~ .input-group-append {
    //   border-color: $primary-default;
    // }
  }

  .link-block {
    a {
      color: $primary-default;
      text-decoration: underline;
    }
  }

  sup {
    color: $primary-default;
  }
}
