@import "src/assets/variables";
.neighborhoodsFilter.modal-dialog{
  max-width: 275px;
  .filter-section{
    margin-bottom: 24px;
    h4{
      font-family: IBMPlexSans, sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      color: $dope-grey-100;
      margin-bottom: 10px;
    }
    .filter-keeper{
      display: flex;
      label{
        width: 50%;
        position: relative;
        height: 36px;
        margin: 0;
        &:first-of-type{
          span{
            border-radius: 4px 0 0 4px;
          }
        }
        &:last-of-type{
          span{
            border-radius: 0 4px 4px 0;
          }
        }
      }
      span{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border: 1px solid $dope-grey-20;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: .3s;
        &:hover{
          background-color: #FBEAEB;
          color: $primary-default;
          border-color: $primary-default;
        }
      }
      input{
        position: absolute;
        left: -9999px;
        width: 0;
        height: 0;
        visibility: hidden;
        &:checked + span{
          background-color: #FBEAEB;
          color: $primary-default;
          border-color: $primary-default;
        }
      }
    }
  }
  .input-range{
    margin-bottom: 30px;
    margin-top: 30px;
    .input-range__track{
      background-color: $dope-grey-10;
      .input-range__track--active{
        background-color: $primary-default;
      }
      .input-range__slider{
        background-color: #fff;
        width: 16px;
        height: 16px;
        border: 4px solid $primary-default;
        margin-top: -0.85rem;
        margin-left: -0.7rem;
        box-sizing: content-box;
        transform: scale(1);
      }
      .input-range__label-container{
        color: #fff;
        background-color: $dark-grey-blue;
        top: -10px;
        padding: 4px 8px;
        border-radius: 4px;
        &::after{
          content: "";
          width: 0;
          height: 0;
          position: absolute;
          bottom: -4px;
          left: 50%;
          margin-left: -6px;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-top-color: $dark-grey-blue;
          border-top-style: solid;
          border-top-width: 4px;
        }
      }
    }
    .input-range__label:not(.input-range__label--value){
      display: none;
    }
  }
}
