@import "src/assets/variables";

.Analytics{
  display: grid;
  grid-gap: 16px 16px;
  grid-template-columns: 1fr;
  @include breakpoint(sm){
    grid-template-columns: repeat(2, 1fr);
  } 
  @include breakpoint(xl){
    grid-template-columns: repeat(4, 1fr);
  } 
  .analitic-card{
    min-height: 84px;    
    border-radius: 4px;
    background-color: #FFFFFF;
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  } 
  .an-value{
    color: #354052;    
    font-size: 26px;    
    line-height: 1.23;
    text-align: center;
    margin-bottom: 4px;    
  }
  .an-link{
    cursor: pointer;
    .an-value{
      color: $primary-default;      
      text-decoration: underline;
    }
  }
  .an-text{
    color: #7F8FA4;    
    font-size: 10px;    
    line-height: 1.6;
    text-align: center;
  }
}