@import "src/assets/variables";
// .AdminAccountsPage{}
.PostcardPreferences,.AccountGeneral{
	.page-title-block{
		min-height: 50px;
	}
	.fictitious-tabs{
		padding: 0;
		margin: 0;
		display: flex;
		list-style: none;
	}
	.fictitious-tabs-item{
		margin-bottom: -4px;
	}
	.fictitious-tab{
		padding: .5rem 1rem;
		font-size: 16px;
		font-weight: 500;
		border: none;
		text-align: center;
		text-decoration: none;
		transition: all .3s ease 0s;
		border-top-left-radius: .25rem;
		border-top-right-radius: .25rem;
		cursor: pointer;		
		background-color: transparent;
		border-bottom: 4px solid transparent;
		color: $abbey;
		&.active{
			background-color: #ffffff;
			border-bottom: 4px solid var(--color-primary);
			color: var(--color-primary);
		}
	}
	.back-btn-container{
		margin-bottom: 24px;
		.back-btn{
		  height: 24px;
		  padding: 4px 8px;
		  border: none;
		  @include outline(0);
		  border-radius: 4px;
		  background-color: #FFFFFF;
		  display: inline-flex;
		  justify-content: center;
		  align-items: center;
		  color: $primary-default;
		  font-family: IBMPlexSans, sans-serif;
		  font-size: 10px;
		  font-weight: 700;      
		  line-height: 1.7;
		  text-transform: uppercase;
		}
		.back-icon{
		  height: 16px;
		  width: 16px;
		  margin-right: 4px;
		}
	}
}