@import "src/assets/variables";

.ListsFilter {  
  @include breakpoint(md){ 
    display: flex;
  }
  padding: 0 0 16px;
  .filter-col{    
    @include breakpoint(sm-down){
      &:not(:last-child){
        margin-bottom: 16px;
      }
    }
    @include breakpoint(md){
      display: flex;
      flex-direction: column;
      flex: 0 1 auto;
      width: 50%;
      &:not(:last-child){
        margin-right: 16px;
      }
      height: 281px;
    }    
  }
  .filter-col-header{
    margin-bottom: 8px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    @include breakpoint(md){
      flex: 0 0 auto;
    }
    .col-header-title{
      color: $dope-grey-100;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 700;      
      line-height: 1;
    }
    .dope-button{
      padding: 4px 8px; 
      font-size: 10px;      
      line-height: 1.6;     
    }
  }
  .search-container{
    @include breakpoint(md){
      flex: 0 0 auto;
    }
    .dope-search-container{
      margin: 0;
      .dope-input{
        width: 100%;
      }
    }
    margin-bottom: 8px;
  }
  .fs-leftcol-list{
    flex: 1 1 auto;
    overflow-y: auto;
    margin: 0;
    padding: 0;
    list-style: none;
    border: 1px solid #E5E9ED;
    border-radius: 4px;
    position: relative;
    .fs-leftcol-item{
      height: 40px;      
      box-shadow: inset 0 -1px 0 0 #E5E9ED;
      padding: 8px 16px;
      display: flex;
      align-items: center;
      .DopeCheckbox{
        max-width: 100%;
      }
      .custom-checkbox{
        flex: 0 0 auto;
      }
      .checkbox-label{
        white-space: nowrap;
        text-overflow: ellipsis;        
        overflow: hidden;
      }      
    }
    .no-fs-leftcol-item{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: $dope-grey-100;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;      
      line-height: 1.43;
      text-align: center;      
      font-weight: 400;
    }
  }
  .fs-rightcol-list{
    @include breakpoint(md){
      flex: 1 1 auto;      
      overflow-y: auto;
      align-content: flex-start;
    }
    margin: 0;
    padding: 8px;
    list-style: none;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: wrap;
    border: 1px solid #E5E9ED;
    border-radius: 4px;
    &.empty{
      justify-content: center;
      align-items: center;
      align-content: center;
    }
    .fs-rightcol-item{
      margin: 0 8px 8px 0;
      padding: 5px 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 32px;      
      border-radius: 20px;
      background-color: $primary-default; 
      color: #FFFFFF;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 700;      
      line-height: 1.43;
    }
    .close-icon{
      width: 16px;
      height: 16px;
      margin-left: 4px;
      flex: 0 0 auto;
      cursor: pointer;
    }
  }
  .no-fs-leftcol-item{    
    color: $dope-grey-100;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;      
    line-height: 1.43;
    text-align: center;
    font-weight: 400;
  }
}
