@import "../../assets/variables";

.campaigns-page {
  color: $dark-grey-blue;

  .dope-page-title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 42px;

    h2 {
      font-size: 28px;
      line-height: 37px;
      font-weight: bold;
      color: $dark-grey-blue;
    }
  }

  .dope-card.card {
    border-radius: 4px;

    .dope-card-header {
      padding: 16px 0 18px 24px;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
      text-transform: capitalize;
      color: $dark-grey-blue;
    }

    .card-body {
      padding: 0 24px 24px;
    }
  }

  .draft-block {
    background-color: $dope-grey-100;
  }

  .confirmed-block {
    background-color: #2292d6;
  }

  .sent-block {
    background-color: $positive-default;
  }

  .unsent-block {
    background-color: $primary-default;
  }

  .payment-failed-block {
    background-color: $alert-default;
  }

  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
