@import "../../../assets/variables";

.auth-container {
  .one-line {
    display: flex;
    justify-content: space-between;

    .dope-form-group {
      width: 50%;

      &:first-child {
        margin-right: 16px;
      }
    }
  }

  .account-details-form {
    .auth-link-container {
      margin-bottom: 24px;
    }

    .dope-form-group {
      .dope-select {
        [class*="-control"] {
          border-color: $athens-gray-approx;
          box-shadow: none;
        }
      }

      &.select-error {
        div[class*=control] {
          border-color: $primary-default;
        }
      }
    }

    .company-information {
      margin-bottom: 40px;
      position: relative;
    }

    .credit-card-container {
      box-shadow: none;
      border: none;

      .card-header {
        height: 0;
        padding: 0;
      }
    }

    .charged-info{
      font-size: 14px;
      line-height: 18px;
      color: $dope-grey-100;
    }

    .image-icon {
      width: 80px;
      height: 80px;
      display: flex;
      align-self: center;
    }

    .logo-upload-container {
      display: flex;

      .logo-img {
        background-color: $athens-gray;
        height: 80px;
        width: 80px;
        display: flex;
        align-items: center;
        justify-content: center;

        .svg-container {
          svg {
            height: 48px;
            width: 48px;
          }
        }
      }
    }

    .logo-upload {
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-bottom: 24px;

      .validation-message {
        width: 100%;
        text-align: center;
      }

      img {
        max-width: 80px;
        max-height: 80px;
        width: auto;
        height: auto;
        display: flex;
        align-self: center;
      }

      .input-title {
        display: flex;
        align-self: center;
      }
    }

    .logo-input {
      height: 0.1px;
      opacity: 0;
      overflow: hidden;
      position: absolute;
      z-index: -1;
      display: block;
      // width: 100%;
    }

    .logo-link-block {
      display: flex;
      justify-content: center;
      flex-direction: column;

      label,
      span {
        display: block;
        padding-left: 16px;
        font-size: 14px;
        line-height: 24px;
        color: $primary-default;
        text-decoration: underline;
        cursor: pointer;
      }
    }

    .form-header {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-bottom: 20px;
      font-size: 16px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $dark-grey-blue;
    }

    .back-button {
      margin-top: 20px;
      display: flex;
      align-items: center;
      .button-text {
        margin-left: 12px;
        font-size: 16px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: $primary-default;
      }
    }

    .group-title {
      font-size: 20px;
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      color: $dark-grey-blue;
      margin-bottom: 20px;
      margin-top: 20px;
    }

    .group-subtitle {
      font-size: 14px;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: 1.5;
      letter-spacing: normal;
      color: $dark-grey-blue;
    }

    // .input-block {
    //   position: relative;
    //   margin-bottom: 24px;
    // }

    .credit-card-container {
      .card-body {
        padding: 0;
      }

      tbody tr:last-child {
        padding: 0;
        margin-top: 20px;
      }

      button {
        // background-color: $primary-default;
        // font-size: 14px;
        // margin-top: 20px;
      }
    }

    .form-footer {
      display: flex;
      justify-content: flex-end;
    }
  }

  a {
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: $primary-default;

    &:hover,
    &:active,
    &:focus {
      color: $primary-default;
    }
  }
}
