@import "../../../assets/variables";

.report-campaign-container {  
  position: relative;
  .loader-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }
  .report-title{
    color: $dark-grey-blue;    
    font-size: 28px;
    font-weight: 700;
    line-height: 1.14;
    margin-bottom: 32px;
  }
  .stat-container{
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    background-color: #fff;
    border-radius: 5px;
    margin-bottom: 16px;
    .stat-th{
      flex: 1 1 100%;
      @include breakpoint(xl){
        flex: 1 1 auto;
      }      
    }
    .stat-td{
      flex: 0 1 100%;
      @include breakpoint(sm){
        flex: 0 1 50%;        
      }
      @include breakpoint(xl){
        flex: 0 1 auto;
        width: 172px;
      }
    }
    .stat-th, .stat-td{
      box-shadow: inset -1px -1px 0 0 #E5E9ED;
      @include breakpoint(xl){
        box-shadow: inset -1px 0 0 0 #E5E9ED;
      }
      min-height: 76px;
      padding: 16px 24px;      
    }
    .stat-title{
      color: $dope-grey-100;      
      font-size: 14px;
      line-height: 1.43;
      margin: 0;
    }
    .stat-info{
      color: $dark-grey-blue;      
      font-size: 18px;
      font-weight: 700;
      line-height: 1.33;
    }
  }
  .performance-container{
    position: relative;
    background-color: #fff;
    border-radius: 5px;
    .perf-th{
      display: flex;
      flex-wrap: wrap;      
      padding: 16px 24px 0;      
      box-shadow: inset 0 -1px 0 0 #E5E9ED;
      @include breakpoint(xl){
        flex-wrap: nowrap;
        min-height: 60px;
        padding: 0 24px; 
      }
    }    
    .perf-title{
      align-self: center;
      flex: 1 1 100%;
      color: $dark-grey-blue;      
      font-size: 22px;
      font-weight: 700;
      line-height: 1.27;
      margin: 0 0 12px 0;
      @include breakpoint(xl){
        flex: 1 1 auto;
        margin: 0;
      }
    }
    .perf-nav-tabs{
      border: none;
      align-self: flex-end; 
      margin: 0;   
      padding: 0;  
    }
    .perf-nav-item{
      margin: 0;    
      &:not(:last-child){
        margin-right: 12px;
      } 
      @include outline(0);      
      .nav-link{
        color: $dark-grey-blue!important;        
        font-size: 14px;
        line-height: 1.43;
        border-style: solid;
        border-width: 0 0 2px 0;
        border-color: transparent; 
        transition: border-color 300ms ease 0s; 
        min-width: 120px;
        height: 44px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
        &.active{
          font-weight: 700;          
          border-width: 0 0 2px 0;
          border-color: $primary-default;
        }
        @include outline(0);
        &:hover,&:active,&:focus{
          border-width: 0 0 2px 0;
          border-color: $primary-default;          
        }
      }
    }
    .perf-tab-content{
      padding: 0 0 16px 0;
      border: none;
    }
    .perf-item{
      box-shadow: inset 0 -1px 0 0 #E5E9ED;      
      padding: 8px 24px;
      min-height: 80px; 
      @include breakpoint(sm){
        display: flex;  
        flex-wrap: wrap; 
      }
      @include breakpoint(xl){
        flex-wrap: nowrap; 
      }  
    }
    .p-item-info{
      flex: 0 1 auto;
      width: 136px;
      display: flex;           
      flex-direction: column;
      justify-content: center;
      align-items: center;      
      @include breakpoint(sm){
        padding-left: 24px; 
        align-items: flex-start;
      }
      @include breakpoint(xs-only){
        margin: 0 auto;
      }
      .p-item-date{
        color: $dark-grey-blue;
        font-size: 14px;
        line-height: 1.43;
      }
      .p-item-phone{
        color: $dope-grey-100;        
        font-size: 10px;
        line-height: 1.6;
      }
    }
    .p-item-preview{      
      flex: 0 1 auto;
      display: flex; 
      justify-content: center;     
      .preview-img-box{  
        align-self: center;      
        width: 97px;
        border: 1px solid #E5E9ED;
        border-radius: 3px;
        overflow: hidden;
        &:not(:last-child){
          margin-right: 16px;
        }
      }
      .preview-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: 50% 50%;
      }
    }
    .chart-container{
      padding: 12px 80px 12px 0;
      flex: 1 1 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint(sm){
        padding: 12px 80px 12px 32px;
      }
      @include breakpoint(xl){
        flex: 1 1 auto;
      }
      .dope-attention{
        .icon{
          margin-right: 8px;
          path{
            fill: #F5A623;
          }          
        }
        display: flex;        
        align-items: center;
        padding: 8px 16px 8px 8px;
        margin: 0;        
        min-height: 40px;
        border-radius: 4px;
        background-color: rgba(#F5A623,0.1);
        color: #F5A623;        
        font-size: 14px;
        line-height: 1.43;
      }
    }
    .perf-chart{
      display: block;
      position: relative;
      height: 24px;      
      border-radius: 4px;
      background-color: $primary-default;
      width: 0;
      transition: width ease-in-out 1s;
      .perf-chart-value{
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(16px,-50%);
        color: $dark-grey-blue;        
        font-size: 14px;
        font-weight: 700;
        line-height: 1.43;
      }
    }
  }
}
