@import "../../../../assets/variables";
.matching-contact {
  h2 {
    margin-bottom: 32px;
    font-size: 28px;
    line-height: 37px;
    font-weight: bold;
    text-transform: capitalize;
  }

  sup {
    color: $primary-default;
  }
  
  .matching-contact-container {
    background-color: white;
    border-radius: 4px;

    .error-text {
      color: $primary-default;
    }

    .csv-row {
      display: flex;

      &:not(:first-child) {
        .csv-cell {
          padding: 14px 24px;
          border-bottom: 1px solid $dope-grey-20;
        }

      }

      &:first-child  .csv-cell {
        background-color: $dope-grey-10;
      }

      .csv-cell {
        flex-grow: 1;
        flex-basis: 0;
        min-width: 250px;
        padding: 8px 24px;
        &:not(.csv-cell-skip) .matchingContacts__clear-indicator:hover{
          path{
            fill: $primary-default;
          }
        }

        &.csv-cell-skip {
          .dope__single-value ,
          .matchingContacts__single-value {
            color: $primary-default;
          }
          .matchingContacts__clear-indicator{
            display: none;
          }
        }
        &.ignored{
          position: relative;
          color: #aaa;
          &:after{
            content: 'IGNORED';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            color: $primary-default;
            font-size: 32px;
            opacity: 0.2;
            display: flex;
            // justify-content: center;
            align-items: center;
          }
          // background-color: #f00;
        }
      }

      &.csv-row-header {
        color:$dope-grey-100;
        font-weight: bold;
      }
    }

    .one-line {
      display: flex;
      justify-content: flex-start;
      margin-bottom: 16px;

      .dope-form-group {
        flex: 1 1 auto;
        max-width: 432px;       

        &:first-child {
          margin-right: 48px;
        }
        
        .input-title {
          margin-bottom: 0;
          font-size: 14px;
          line-height: 20px;
        }

        &.select-error {
          div[class*=control] {
            border-color: $primary-default;
          }
        }
      }
    }

    .matching-contact-header {
      padding: 18px 24px 20px;

      p {
        margin-bottom: 8px;
      }

      .header-label {
        display: inline-flex;
        align-items: center;
        margin: 0;
        cursor: pointer;
        user-select: none;
        .styled-checkbox {
          margin-right: 12px;
        }
      }
    }

    .matching-contact-body {
      width: 100%;
      // overflow-x: scroll;
      // overflow-y: visible;
      margin-bottom: 24px;
      // padding-bottom: 24px;
    }

    .matching-contacts-footer {
      display: flex;
      justify-content: space-between;
      width: 100%;
      // padding: 0 24px 16px;
      padding: 5px 24px 16px;
    }
  }
  .ps__thumb-x {
    background-color: $primary-disabled;  
  }
  
  .ps__thumb-y {
    background-color: $primary-disabled;  
  }
  
  .ps__rail-x:hover > .ps__thumb-x,
  .ps__rail-x:focus > .ps__thumb-x,
  .ps__rail-x.ps--clicking .ps__thumb-x {
    background-color: $primary-default;
  }
  
  .ps__rail-y:hover > .ps__thumb-y,
  .ps__rail-y:focus > .ps__thumb-y,
  .ps__rail-y.ps--clicking .ps__thumb-y {
    background-color: $primary-default;
  }
  .dope-select{
    .matchingContacts__control {
      &.matchingContacts__control--is-focused{
        .matchingContacts__indicator{
          padding-left: 1px;
        }
      }
      .matchingContacts__indicator{
        padding-left: 0;
      }
    }
  }
}

.matchingContacts__menu{
  .matchingContacts__option:first-of-type{
    font-weight: bold;
    color: $primary-default;
  }
}


  .list-id-select__menu > div:first-child> div:first-child  {
    font-weight: bold;
  }

  .csv-column-select__menu > div:first-child> div:first-child {
    color: $primary-default;
    font-weight: normal;
  }

  .csv-cell.csv-cell-skip .csv-column-select__single-value {
    color: #d52330;
  }
