@import "../../../assets/variables";
.postcard-wizard {
  .order-screenshot-block {
    background-color: #edf0f5;
    padding-top: 15px;
  }
  
  .contacts-table {
    position: relative;

    .loader-container {
      position: absolute;
      top: calc(50% - 50px);
      left: calc(50% - 50px);
    }
  }

  .react-bootstrap-table {
    thead {
      th:nth-child(1),
      th:nth-child(2),
      th:nth-child(3),
      th:nth-child(4),
      th:nth-child(5) {
        width: 14%;
        min-width: 200px;
      }

      th:nth-child(6),
      th:nth-child(7),
      th:nth-child(8)
       {
        width: 10%;
        min-width: 150px;
      }
    }
  }

  .template-preview {
    img {
      width: 70%;
    }

    .front-postcard {
      display: flex;
      justify-content: flex-end;
      align-items: flex-start;
      padding-right: 25px;
    }

    .back-postcard {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      padding-left: 25px;
    }
  }

  .pricing-card{
    padding: 0 15px;    
  }
  .view-pricing-link{
    color: $primary-default;
    font-size: 16px;
    line-height: 24px;
    text-decoration: underline;
    cursor: pointer;
    &:hover {
      color: $primary-hover;
    }
  }
  .card .DopeRadio{
    margin-bottom: 0;
  }
  .card .DopeCheckbox{
    margin-bottom: 0;
  }
  .pricing-radio-container{
    margin-bottom: 16px;
  }
  .pricing-radio-row{
    display: flex;
    flex-wrap: wrap;
    &:not(:last-child){
      margin-bottom: 16px;
    }
    .DopeRadio{
      margin-right: 16px;
      min-width: 200px;
    }    
    .price-per-text{
      margin-right: 16px;
      min-width: 150px;
      color: #000000;
      font-size: 16px;
      font-weight: 500;
      line-height: 23px; 
    }
    .delivery-days{
      margin-right: 16px;
      min-width: 300px;
      color: #354052;
      font-size: 14px;
      line-height: 20px;
    }
    .tooltips-text{
      color: #979797;
      font-size: 14px;
      font-weight: 600;
      line-height: 18px;
      text-decoration: underline;
      cursor: pointer;
    }
    .pricing-tooltip{
      width: 360px;
      max-width: 100%;
      border-radius: 4px;
      span{
        display: block;
        font-size: 12px;
        font-weight: 400;          
        line-height: 18px;          
        color: #ffffff;
      }
    }
  }  
  .pricing-link-container{
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 1600px) {
  .postcard-wizard {
    .template-preview {
      img {
        width: 80%;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .postcard-wizard {
    .template-preview {
      img {
        width: 100%;
      }
    }
  }
}
