@import "../../../assets/variables";

.template-container {
  .template-preview-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    .nav-link {
      padding: 8px;
    }

    .step-container {
      margin-bottom: 20px;
      padding: 20px 0 0 20px;
    }

    .dope-tab-content {
      width: 100%;
      padding-bottom: 0;

      .no-templates-container {
        display: flex;
        justify-content: center;
        flex-grow: 1;
        font-size: 24px;
        color: $dope-grey-50;
        margin-top: 20px;
      }
    }

    .tabs-container {
      span {
        min-width: 120px;
        display: inline-block;
        padding-bottom: 8px;
        color: $abbey;
        text-align: center;
        cursor: pointer;
      }

      .active {
        color: $primary-default;
        border-bottom: 2px solid $primary-default;
      }
    }
  }
  .card-body {
    display: flex;
    flex-direction: column;
    margin-top: 20px;

    &.disabled,
    .page-item.active {
      pointer-events: none;
    }

    .row > div {
      padding: 15px;
      display: flex;
    }

    .template-item {
      border: 1px solid #b7b6b6;
      padding: 10px;
      flex-grow: 1;
      position: relative;

      img {
        width: 100%;
        height: auto;
      }
    }
  }

  .template-checked {
    background-color: rgba(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
      content: "";
      display: block;
      background: url("../../../assets/img/icons/select.svg") no-repeat center;
      height: 62px;
      width: 62px;
      background-size: 100%;
    }
  }

  .postcards-preview-container {
    display: flex;
    flex-wrap: wrap;
    min-height: 300px;

    &.disabled {
      pointer-events: none;
    }

    .postcards-preview-item {
      flex-basis: 25%;
      padding: 15px;
      min-width: 0;
    }

    .postcards-preview-content {
      border: 1px solid #b7b6b6;
      padding: 10px;
      position: relative;
    }

    .postcards-preview-name-block {
      display: flex;
      justify-content: space-between;
      margin-top: 5px;
      .name-text{        
        padding-left: 2px;
        font-size: 16px;
        font-weight: 500;
        color: #515557;
        min-width: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }      
      .del-icon{
        width: 24px;
        flex: 0 0 auto;
        cursor: pointer;
        path {
          fill: $primary-default;
        }
      }
    }
  }

  .active.nav-link {
    pointer-events: none;
  }


  .pagination {
    justify-content: center;
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: 1rem;
  
  
    img.prev-icon {
      transform: scaleX(-1);
      filter: FlipH;
    }
  
    .page-item {
      margin-right: 16px;
      .page-link {
        border: none;
        text-align: center;
        padding: 0;
        width: 24px;
        height: 24px;
        background-color: transparent;
        font-size: 17px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #3d3d3d;
        border-radius: 0;
      }
      
      &.active {
        .page-link {
          background-color: #e5e9ed;
          color: #3d3d3d;
          border-radius: 0;
        }
      }
  
      &.disabled {
        opacity: 0.3;
      }
    }
  }
}
