@import "../../../assets/variables";

.campaigns-page .sending-page-container {
  .disabled-dope-card {
    pointer-events: none;
  }

  .dope-page-title {
    flex-direction: column;
    margin-bottom: 32px;

    p {
      margin: 0;
    }

    .status-block {
      .status-text {
        color: $dope-grey-100;
        font-weight: bold;
        margin-right: 8px;
      }
    }
  }

  .sending-page-content {
    .sending-recharge-card {
      .card-body {
        padding: 16px 24px;
      }

      .sending-recharge-block {
        display: flex;
        align-items: center;

        p {
          margin: 0 76px 0 0;
        }

        svg {
          margin-right: 16px;

          path {
            fill: $alert-default;
          }
        }
      }
    }

    .sending-details-card {
      .card-body {
        display: flex;

        .sending-details-left {
          max-width: 464px;
          width: 100%;
          margin-right: 72px;

          .disabled-block {
            pointer-events: none;
          }

          .sending-details-radio-btn {
            display: flex;
            margin-bottom: 10px;

            label {
              font-size: 14px;
              line-height: 20px;
              color: $dark-grey-blue;
              margin-bottom: 0;

              &:first-child {
                margin-right: 24px;
              }
            }

            .checkmark {
              margin-right: 12px;
            }
          }

          .sending-tracking-number-container {
            .option-container {
              display: flex;
              justify-content: space-between;
              width: 100%;
            }

            .dope__single-value {
              width: calc(100% - 20px);

              .option-container {
                display: flex;
                justify-content: space-between;
                width: 100%;
              }
            }           

          }
          

          .sending-details-add-tracking-number {
            margin-bottom: 16px;

            .add-tracking-number-text {
              color: $dope-grey-100;
              margin-right: 4px;
            }

            .add-tracking-number-link {
              color: $primary-default;
              text-decoration: underline;
              cursor: pointer;
            }
          }
        }

        .sending-details-right {
          .sending-template-preview-header {
            font-weight: bold;
            color: $dope-grey-100;
            font-size: 14px;
            line-height: 20px;
            margin-bottom: 8px;
          }

          .sending-details-content {
            display: flex;

            .sending-template-preview-img {
              margin-right: 16px;
              width: 180px;
            }
          }
        }
      }
      .postcard-other-number-container{
        .help-own-number-icon{
          position: absolute;
          right: 16px;
          top: 50%;
          transform: translate(0,-50%);
        }
        .help-own-number-tooltip{
          background-color: #000;
          padding: 8px 8px;
          // max-width: 720px;
          &.place-right:after{          
            border-right-color: transparent;
          }
          border-radius: 4px;		
          span{
            display: block;
            font-size: 12px;
            font-weight: 500;          
            line-height: 1.33;          
            color: #ffffff;
          }
        }
      }
    }

    .template-container {
      flex: 0 1 auto;
      height: 400px;
      width: 520px;
      transform: scale(.19);
      transform-origin: left top;

      // //slim red line around postcard(:hover) for pictures with dataattribute data-hover-highlighted 
      // scale(.19);  0.19*6=1.14px  (!!!0.xxx px rounding in different browsers)
      // [data-hover-highlighted]{ 
      //   position: relative;
      //   &:before{
      //     transition: border-color 300ms cubic-bezier(0.34, 2, 0.6, 1);  
      //     content: '';
      //     pointer-events: none;
      //     position: absolute;
      //     top: 50%;
      //     left: 50%; 
      //     transform: translate(-50%,-50%);               
      //     height: 100%;      
      //     width: 100%;
      //     border: solid 6px transparent;            
      //     z-index: 100;          
      //   }       
      //   &:hover{
      //     position: relative;
      //     &:before{        
      //       border: solid 6px #D52330;
      //     }    
      //   }  
      // }
    } 
  }

  .sending-page-btn {
    display: flex;
    justify-content: flex-end;

    button {
      margin-left: 8px;
    }
  }

  .sendings-status {
    border-radius: 4px;
    font-size: 10px;
    line-height: 16px;
    color: white;
    font-weight: bold;
    text-transform: uppercase;
    padding: 0 4px;
  }
}
