@import "../../../assets/variables";

.work-result {
    display: flex;
    margin-bottom: 30px;
    color: white;

    &__item {
        display: flex;
        flex-direction: column;
        flex-grow: 1;

        .work-result-img {
            margin-bottom: 10px;

            svg {
                path {
                    fill: white;
                }
            }
        }

        &:not(:last-child) {
            margin-right: 20px;
        }

        &--top {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 20px;
            padding: 42px 5px;

            .work-result-count {
                font-size: 24px;
                line-height: 28px;
                margin-bottom: 10px;
                text-align: center;
            }

            .work-result-title {
                font-size: 18px;
                line-height: 20px;
            }
            &.theme-bg{
                background-color: $primary-default;
            }
        }

        &--bottom {
            background-color: $primary-default;
            margin: 0 50px;
            padding: 10px 0;
            font-size: 18px;
            line-height: 20px;
            text-transform: uppercase;
            text-align: center;

            & p:nth-child(1) {
               margin-bottom: 10px;
               font-size: 18px;
               line-height: 14px;
            }

            & p:nth-child(2) {
                font-size: 12px;
                line-height: 14px;
            }
        }
    }
}
