      stroke-width: 8px;
      &:hover{
        //fill-opacity: 0.6;
        stroke-opacity: 0.6;
      }
      // &.active{
      //   //fill-opacity: 0.8;
      //   stroke-opacity: 0.8;
      // }
    }
    // SAFARI issue with owerlap test
    // &:after{
    //   transition: opacity ease-in-out 0.2s;
    //   content: '';
    //   position: absolute;
