    //   width: 100%;
    //   height: 100%;
    //   top: 0;
    //   left: 0;
    //   background-color: #000;
    //   pointer-events: none;
    //   opacity: 0; 
    // }
    .loading-indicator{
      display: none;
      background: $white;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.16);
      border-radius: 4px;
      padding: 15px;
      font-family: IBMPlexSans, sans-serif;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      color: $positive-default;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 500;
    }
    &.loading{
      position: relative;
      &:after{      
        opacity: 0.1;
      }
      .loading-indicator{
        display: block;
      }
    }
  }
}
