@import "src/assets/variables";
// @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@800&display=swap');
// @import url('https://fonts.googleapis.com/css2?family=Oswald&display=swap');

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@800&family=Oswald:wght@400;700&display=swap');

.LandingPage {
  min-height: 100vh;  
  padding-top: 80px;
  // background-color: #f00;
  .landing-header{
    padding: 10px 15px;
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;    
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    background-color: #fff;
  }
  .to-app-btn{
    @include outline(0);
    cursor: pointer;
    min-width: 160px;
    min-height: 60px;
    border: 2px solid #000000;
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.5;
    text-align: center;
    color: #000000;
    background-color: #fff;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.6s ease-in-out;
    &:hover{
      background-color: $primary-default;
    }
  }
  .title{
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    font-size: 56px;
    line-height: 1.21;
    text-align: center;
    color: #FFFFFF;
    margin-bottom: 20px;
    word-break: break-word;
    .accent{
      text-decoration: underline;
    }
  }
  .sub-title{
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    font-size: 24px;
    line-height: 1.5;
    text-align: center;
    color: #FFFFFF;    
    max-width: 1000px; 
    margin: 0 auto 60px;   
  }
  .hero-section{
    min-height: 100vh;
    @include breakpoint(xl){
      min-height: 600px;
    }
    padding: 50px 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: url('https://dope360-public.s3.amazonaws.com/landing/land-1.jpg') no-repeat center/cover #000000;
  }
  .landing-sign-up{
    display: flex; 
    width: 100%;
    justify-content: center;
    .sign-up-input{
      @include outline(0);
      padding: 5px 20px;
      height: 60px;
      border: none;
      background: #FFF;
      font-family: 'Oswald', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.5;
      color: #000000;
      flex: 1 1 50%;
      @include breakpoint(md){
        max-width: 431px;
      }
    }
    .sign-up-btn{
      @include outline(0);
      border: none;
      background: #E6000F;
      padding: 5px 20px;
      font-family: 'Oswald', sans-serif;
      font-weight: 400;      
      line-height: 1.5;
      color: #ffffff;
      font-size: 16px;
      @include breakpoint(sm){
        font-size: 24px;
      }
      @include breakpoint(xl){
        width: 267px;
      }
    }    
  }
  .sqr-items{
    margin: -50px auto 0;
    max-width: 1920px;
    padding: 0 15px;
    display: grid;    
    justify-content: center;
    justify-items: center;
    grid-gap: 15px;
    grid-template-columns: repeat(auto-fit, 140px);
    @include breakpoint(sm){
      grid-template-columns: repeat(auto-fit, 200px);
    } 
    @include breakpoint(xl){
      grid-template-columns: repeat(auto-fit, minmax(220px,1fr)); 
    }      
  }  
  .sqr-item{
    background-color: #000;
    max-width: 270px;
    width: 100%;    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: 140px;
    padding: 15px;
    @include breakpoint(sm){
      min-height: 200px;
    } 
    @include breakpoint(xl){
      padding: 20px;
      min-height: 270px; 
    }  
    &.accent{
      padding: 20px;
      @include breakpoint(xl){
        padding: 30px;
      }
      background-color: #E6000F;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .icon{
      margin-bottom: 12px;
      @include breakpoint(xl){
        margin-bottom: 24px;
      }
      // @include breakpoint(xl){}
    }
  }
  .sqr-text{
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    font-size: 14px;
    @include breakpoint(sm){
      font-size: 18px;
    }
    @include breakpoint(xl){
      font-size: 24px;
    }
    line-height: 1.5;
    text-align: center;
    color: #FFFFFF;
  }
  .sqr-accent-text{
    font-family: 'Oswald', sans-serif;
    font-weight: 700;
    font-size: 16px;
    @include breakpoint(sm){
      font-size: 24px;
    }
    @include breakpoint(xl){
      font-size: 36px;
    }
    line-height: 1.33;    
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    .accent{
      color: #000000;
    }
  }
  .services-section{
    padding: 50px 15px;
    background-color: #fff;
    .services-container{
      background-color: #000;
      padding: 50px 15px;
      width: 95%;
      margin: 0 auto;
      @include breakpoint(xl){
        padding: 100px 60px;
        width: 85%;
        max-width: 1440px;
      }      
    }
    .title{
      font-family: 'Oswald', sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 1.5;
      text-align: center;
      text-transform: uppercase;
      color: #FFFFFF;
      margin-bottom: 50px;
    }
    .services-list{
      padding: 0;
      margin: 0;
      list-style: none;
    }
    .services-item{
      display: flex; 
      position: relative;     
      &:not(:last-child){
        margin-bottom: 80px;
      }
      &:nth-child(1){   
        padding-top: 50px; 
        @include breakpoint(xl){
          padding-top: 100px; 
        }
        .image-box{
          top: 0;
          left: 0;
          &:before{            
            top: 0;
            left: 0;            
            width: 18%;
            height: 100%;
          }
        }    
        .text-box{
          width: 80%;
          margin-left: auto;
        }
      }
      &:nth-child(2){   
        padding-top: 60px;
        @include breakpoint(xl){
          padding-top: 147px; 
        } 
        .image-box{
          top: 0;
          left: 7%;
          &:before{            
            top: 0;
            left: 0;            
            width: 100%;
            height: 21%;
          }
        }     
        .text-box{
          width: 70%;
          margin-left: auto;
        }
      }
      &:nth-child(3){   
        padding-top: 40px; 
        @include breakpoint(xl){
          padding-top: 30px; 
        } 
        .image-box{
          top: 0;
          right: 0;
          &:before{            
            bottom: 0;
            right: 0;            
            width: 35%;
            height: 48%;
          }
        }      
        .text-box{
          width: 86%;
        }
      }
    }
    .image-box{
      position: absolute;
      padding: 20px;            
      width: 250px;
      height: 182px;      
      @include breakpoint(xl){
        padding: 30px;
        width: 400px;
        height: 290px;
      }
      &:before{
        content:'';
        position: absolute;        
        background-color: #E6000F;
        width: 18%;
        height: 100%;
      }
    }
    .internall-image-box{
      overflow: hidden;
      .img{
        position: relative;
        // z-index: 1;        
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: 50% 50%;        
      }
    }
    .text-box{
      position: relative;
    }
    .item-title{
      font-family: 'Oswald', sans-serif;
      font-weight: bold;
      font-size: 32px;
      @include breakpoint(xl){
        font-size: 64px;
      }
      line-height: 1.48;
      text-transform: uppercase;
      color: #FFFFFF;
      margin-bottom: 20px;
      word-break: break-word;
    }
    .item-text{
      font-family: 'Oswald', sans-serif;
      font-weight: 400;
      font-size: 24px;
      @include breakpoint(xl){
        font-size: 36px;
      }
      line-height: 1.47;
      color: #FFFFFF; 
      word-break: break-word;     
      &:last-child{
        margin: 0;
      }
    }
  }
  .toggler-section{
    padding: 50px 15px;  
    margin: 0 auto;
    max-width: 1440px;  
  }
  .video-section{
    padding: 50px 15px 100px;
    margin: 0 auto;
    max-width: 1920px;
    .title{
      font-family: 'Oswald', sans-serif;
      font-weight: 400;
      font-size: 24px;
      line-height: 1.5;
      text-align: center;
      text-transform: uppercase;
      color: #000000;
      margin-bottom: 60px;
    } 
    .video-box{
      width: 90%;
      margin: 0 auto;      
      @include breakpoint(xl){
        width: 60%;
        max-width: 1920px;
      }
    }   
  }
  .hubspot-form-section{
    background: url('https://dope360-public.s3.amazonaws.com/landing/connect-bg.jpg') no-repeat center/cover #000000;
    .form-section-container{
      padding: 100px 15px;
      margin: 0 auto;
      max-width: 1440px;
    }
    .form-ifr-box{
      width: 100%;
      max-width: 519px;
    }
    .form-title{
      font-family: 'Oswald', sans-serif;
      font-weight: 700;
      font-size: 48px;
      line-height: 1.48;
      text-transform: uppercase;
      color: #FFFFFF;
      text-align: left;
      margin-bottom: 40px;
    }
    .hs-form-iframe{
      max-width: 100%;
    }
  }
  .footer{
    min-height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 10px 15px;
    background-color: #000;    
    .f-item{      
      padding: 2px 6px;
      font-family: 'Oswald', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 1.5;
      color: #FFFFFF;
      position: relative;      
      &.hr{
        cursor: pointer;
        text-decoration: none;
        transition: background-color 0.6s ease-in-out;
        &:hover{
          color: #E6000F;
        }
      }
      &:not(:last-child){
        &:after{
          content: '';
          position: absolute;
          right: 0;
          top: 50%;
          transform: translate(0,-50%);
          width: 1px;
          height: 20px;
          background-color: #fff;
        }
      }
      &.copy{      
        @include breakpoint(lg-down){
          flex: 1 1 100%;
          text-align: center;
          &:after{
            content: none;
          }
        }        
      }
    }    
  }
}
