.map-info-container {
    display: flex;
    flex-direction: row;
}

.map-container {
    width: 70%;
    margin: 0;
    margin-right: 20px;
    margin-bottom: 20px;
    text-align: left;
}

.info-container {
    width: 30%;
    text-align: center;
    .img {
        width: 80%;
        margin: 15px;
    }
}

.map-button-group {
    margin-right: 20px;
}

@media (max-width: 700px) {
    .map-info-container {
        display: flex;
        flex-direction: column;
    }

    .map-container {
        width: 100%;
        margin: 0;
        display: flex;
        justify-content: center;
        text-align: center;
    }

    .info-container {
        width: 100%;
        margin: 0;
        margin-top: 20px;
        margin-bottom: 20px;
        padding: 10px;
    }
}