@import "../../../assets/variables";

.order-block-container {
  margin-bottom: 20px;

  svg {
    height: 60px;
    width: 60px;

    path {
      fill: white;
    }
  }

  .order-block-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    padding: 20px;
    height: 150px;
    color: white;
    font-size: 18px;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 10px;
    }
  }

  .postcard-block {
    background-color: $primary-default;
  }

  .yard-sign-block {
    background-color: $abbey;
  }

  .print-block,
  .voice-block {
    background-color: $abbey;
    opacity: .5;
  }
}
