@import "src/assets/variables";

.ConversionRecent{
  .dash-card-body{
    min-height: 208px;
    .perf-item{
      box-shadow: inset 0 -1px 0 0 #E5E9ED;      
      padding: 4px 24px;
      min-height: 52px; 
      display: flex;  
      align-items: center;
      flex-wrap: wrap; 
      @include breakpoint(xl){
        flex-wrap: nowrap; 
      }  
    }
    .p-item-info{
      flex: 0 1 auto;
      width: 116px;
      padding-right: 16px;
      display: flex;      
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      .p-item-date{
        color: $dark-grey-blue;
        font-size: 14px;
        line-height: 1.43;
      }
      .p-item-name{
        color: $dope-grey-100;        
        font-size: 10px;
        line-height: 1.6;
      }
    }
    .p-item-preview{      
      flex: 0 1 auto;
      display: flex;      
      .preview-img-box{  
        align-self: center;      
        width: 56px;
        border: 1px solid #E5E9ED;
        border-radius: 3px;
        overflow: hidden;
        &:not(:last-child){
          margin-right: 8px;
        }
      }
      .preview-img{
        width: 100%;
        height: 100%;
        object-fit: contain;
        object-position: 50% 50%;
      }
    }
    .chart-container{
      padding: 12px 80px 12px 16px;
      flex: 1 1 100%;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      @include breakpoint(xl){
        flex: 1 1 auto;
      }
    }
    .perf-chart{
      display: block;
      position: relative;
      height: 8px;      
      border-radius: 4px;
      background-color: $primary-default;      
      .perf-chart-value{
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(16px,-50%);
        color: $dark-grey-blue;        
        font-size: 14px;        
        line-height: 1.43;
      }
    }
  }
}