@import "../../../assets/variables.scss";

.dope-modal.postcard-template-modal {
  max-width: 816px;

  .modal-content {
    padding: 16px;
  }

  .dope-modal-footer,
  .dope-modal-header {
    padding: 0 8px;
  }

  .dope-modal-body {
    & > p {
      padding: 0 8px;
      margin-bottom: 10px;
    }
  }

  .postcard-template-content {
    min-height: 370px;
    display: flex;
    flex-direction: column;

    .loader-main {
      position: absolute;
      top: calc(50% - 50px);
      left: calc(50% - 25px);
      z-index: 10;
    }

    .dope-nav.nav {
      // justify-content: space-between;
      padding: 0 8px;

      .nav-link {
        border-bottom: 2px solid transparent;
        font-size: 14px;
        padding: 8px;

        &.active {
          border-color: $primary-default;
        }
      }
    }

    .dope-tab-content {
      padding: 8px 0 16px;
      display: flex;
      flex-grow: 1;

      & > .active {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-grow: 1;
      }
    }
    .postcards-preview-container {
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;

      .no-templates-container {
        flex-grow: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-grow: 1;
        font-size: 24px;
        color: #bfc7d1;
        margin-top: 20px;
      }

      .postcards-preview-item {
        flex-basis: 25%;
        padding: 8px;
        min-width: 0;

        .postcards-preview-content {
          position: relative;

          .template-checked {
            background-color: rgba(213, 35, 48, 0.5);
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            align-items: center;
            justify-content: center;

            &::before {
              content: "";
              display: block;
              background: url("../../../assets/img/icons/select.svg") no-repeat
                center;
              height: 62px;
              width: 62px;
              background-size: 100%;
            }
          }
        }
        .postcards-preview-name-block{
          margin-top: 5px;
          display: flex;
          justify-content: space-between;
          .name-text{        
            padding-left: 2px;
            font-size: 16px;
            font-weight: 500;
            color: #515557;
            min-width: 0;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }          
          .del-icon{
            width: 24px;
            flex: 0 0 auto;
            cursor: pointer;
            path {
              fill: $primary-default;
            }
          }
        }
      }
    }
  }

  .dope-pagination {
    padding: 10px 0 0;
    margin: 0;
  }
}
