@import "../../assets/variables";

.add-tracking-number {
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	margin-bottom: 10px;
	.help-own-number-icon{
		position: absolute;
		right: 16px;
		top: 50%;
		transform: translate(0,-50%);
	}
	.help-own-number-tooltip{
		background-color: #000;
		padding: 8px 8px;
		// max-width: 720px;
		&.place-right:after{          
			border-right-color: transparent;
		}
		border-radius: 4px;		
		span{
			display: block;
			font-size: 12px;
			font-weight: 500;          
			line-height: 1.33;          
			color: #ffffff;
		}
	}
	& > span {
		margin: 0 20px;
		font-weight: 700;
	}
  

	.postcard-other-number-container,.yard-other-number-container {
		margin-bottom: 0;
		position: relative;		
	}
	.postcard-tracking-number-container{		
		.dope__value-container.dope__value-container--has-value{
			.dope__single-value{
				width: calc(100% - 20px);				
				.option-container {
					display: flex;
					justify-content: space-between;
					width: 100%;
				}	
			}
		}
		.dope__control--is-focused{
			.dope__value-container.dope__value-container--has-value{
				.dope__single-value{
					width: calc(100% - 19px);									
				}
			}
		}
		.dope__option{			
			div{
				display: flex;
				justify-content: space-between;
				width: 100%;	
				div:first-child{
					flex: 1 1 auto;					
				}
				div:last-child{									
					white-space: nowrap;							
					flex: 0 1 0%;							
				}
			}		
		}
	}
	.yard-tracking-number-container {
		.dope__value-container.dope__value-container--has-value{
			.dope__single-value{
				width: calc(100% - 20px);	
				.option-container {
					display: flex;
					justify-content: space-between;
					width: 100%;
				}	
			}
		}
		.dope__control--is-focused{
			.dope__value-container.dope__value-container--has-value{
				.dope__single-value{
					width: calc(100% - 19px);									
				}
			}
		}
		.dope__option{			
			div{
				display: flex;
				justify-content: space-between;
				width: 100%;	
				div:first-child{
					flex: 1 1 auto;					
				}
				div:last-child{									
					white-space: nowrap;							
					flex: 0 1 0%;							
				}
			}		
		}
	}
	.number-container {
		min-width: 300px;
		.option-container {
			display: flex;
			justify-content: space-between;
			width: 100%;
		}
	}
	.dope-select{		
		.dope__control{
			// height: 62px;			
			// border: 1px solid #BFBFBF;
			// border-radius: 9px;
			min-width: 250px;
			// font-size: 17px;
			// .dope__indicator-separator{
			// 	display: none;
			// }
			// .dope__value-container{
			// 	padding: 2px 24px;
			// }
		}
	}
	.yard-tracking-number-container {
		flex: 1 1 auto;
		max-width: 428px;
		margin-right: 23px;
	}	
	.postcard-tracking-number-container{
		flex: 1 1 auto;
		max-width: 428px;
		margin-right: 23px;
	}
	.add-btn-container{
		display: flex;
		flex-direction: column;
		align-items: flex-start;		
		font-size: 17px;			
	}
	.add-number-btn{
		color: $primary-default;
		text-decoration: underline;
		cursor: pointer;
	}
	.yard-other-number-container{
		// flex: 1 1 auto;
		flex: 0 1 auto;
		margin-right: 23px;
		// max-width: 428px;
		.form-control{
			// height: 62px;
			width: 100%;
			// border: 1px solid #BFBFBF;
			// border-radius: 4px;
			// font-size: 17px;
			// padding: 2px 24px 2px 24px !important;
		}
	}
	.postcard-other-number-container{
		// flex: 1 1 auto;
		flex: 0 1 auto;
		margin-right: 23px;
		// max-width: 428px;
		.form-control{
			// height: 62px;
			width: 100%;
			// border: 1px solid #BFBFBF;
			// border-radius: 4px;
			// font-size: 17px;
			// padding: 2px 24px 2px 24px !important;
		}
	}
	.alert.dope-attention{
		display: flex;
    	align-items: center;
		margin: 0;
		// height: 63px;
		height: 38px;				
		// border-radius: 4px;
		background-color: rgba(#FBA633, 0.1);

		font-size: 12px;

		color: #F5A623;		
		// font-size: 16px;
		// line-height: 24px;
		.icon{	
			margin-right: 18px;
			path{
				fill: #FBA633;
			}					
		}
	}
}
.card .add-tracking-number.card-body {
  padding-top: 0;
}

.wizard-label {
	padding: 0 15px 15px;
}

.add-number-modal.modal-dialog {
	position: relative;

	.add-number-modal-loader {
		position: absolute;
		top: calc(50% - 50px);
		left: calc(50% - 25px);
		z-index: 10;
	}

	.search-input {
		position: relative;

		.phone-numbers-container {
			position: absolute;
			left: 0;
			max-height: 200px;
			overflow-y: scroll;
			z-index: 10;
			width: 100%;
			background-color: white;
			box-shadow: $shadow-main;

			& > div {
				padding: 8px 20px;
				font-size: 14px;


				&:hover {
					background-color: rgba( $primary-default, .03 );
				}
			}
		}
	}	
}