.inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile + label {
    font-size: 12px;
    font-weight: 600;
    color: white;
    background-color: #66625c;
    display: inline-block;
    cursor: pointer;
    padding: 11px 22px;
    border-radius: 3px;
    text-align: center;
    // text-transform: uppercase;
}

.inputfile:focus + label,
.inputfile + label:hover {
    background-color: #54504b;
}

html body {
    font-family: 'IBMPlexSans', sans-serif;    
