.primary {
    background: #ea0029;
}

.secondary {
    background: #898989;
}

.loader {
    display: flex;
    justify-content: center;
}

.rbc-time-view {
    position: relative;
}

.rbc-time-header {
    height: 100%;
}

.rbc-time-header-content {
    position: relative;
}

.rbc-allday-cell {
    height: calc(100% - 20px) !important;
}

.rbc-row-bg {
    height: 100% !important;
}

.rbc-row-content {
    height: 100% !important;
    overflow-y: auto;
}

