@import "../../../assets/variables";

.verify-email-message {
  .circle-container {
    display: flex;
    justify-content: center;
    margin-bottom: 24px;

    .circle {
      height: 48px;
      width: 48px;
      background-color: $primary-default;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }

  .auth-form-title {
    text-align: center;
  }

  .verify-email-text {
    margin-bottom: 24px;
    font-size: 14px;
    line-height: 24px;
    text-align: center;
  }
  .verify-email-bottom-text,
  .verify-email-text {
    color: $dark-grey-blue;
  }

  .verify-email-bottom {
    display: flex;

    .link-block{
      margin-left: 5px;
    }
  }
  .link-item {      
    color: $primary-default;
    text-decoration: underline;
    padding: 0;
    border: 0;
    background-color: transparent;
    margin: 0;
    cursor: pointer;
    &:hover {
      color: $primary-hover;
    }
    &:disabled{
      cursor: default;
      color: #666;
    }
  }
  .phone-verify-container{
    margin-bottom: 24px;
    text-align: center;
    .dope-button{
      font-size: 13px;
    }
    .phone-number{
      color: #aaa;
      font-size: 11px;
      line-height: 1.3;
    } 
    .error-message{
      display: block;
      color: $primary-default;;
      font-size: 12px;
    }  
  }
  .check-code-form{
    opacity: 0;
    transition: opacity 0.6s ease-in;
    pointer-events: none;
    &.active{
      opacity: 1;
      pointer-events: auto;
    }
    margin-bottom: 24px;
    .dope-input-group{
      justify-content: center;
    }
    .dope-input{
      text-align: center;
      background-color: #eee;
      font-weight: 700;
      padding: 1px 8px; 
      &:focus{
        padding: 1px 7px 1px 7px;
      }     
      font-size: 13px;
      flex: 0 0 100px;
      @include breakpoint(sm){
        font-size: 18px;
        flex: 0 0 200px;
        letter-spacing: 10px;
      }
      &.form-control:focus ~ .input-group-append {
        padding: 0 9px 0 10px;
      }
    }
    .check-text{
      text-align: center;
      padding: 0 4px;
      font-weight: 700;      
      color: #fff;
      font-size: 10px;
    }
    .input-group-append{
      padding: 0 10px 0 10px;
      height: 40px;
      display: flex;      
      justify-content: center;      
      align-items: center;
      border: 1px solid #e5e9ed;
      border-left: none;
      border-radius: 0 4px 4px 0;     
      background-color: $primary-default;
      transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out, background-color 0.3s ease-in-out;
    } 
    .input-group .validation-message{
      left: 50%;
      transform: translate(-50%,0);
    }
  }
}
