@import '../../../../../assets/variables.scss';

.sending-cell {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: 0;

  .sendings-preview-block {
    display: flex;
    justify-content: center;
    min-height: 0;
    z-index: 2;
  }

  .sendings-preview-item {
    position: relative;
    flex-grow: 1;
    flex-basis: 0;
    min-height: 0;

    &:first-child {
      margin-right: 4px;

      img {
        margin: 0 0 0 auto;
      }
    }

    img {
      display: block;
      max-height: 100%;
    }
  }

  .status-sending-block {
    position: absolute;
    bottom: 4px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    z-index: 2;
    white-space: nowrap;

    .sendings-status {
      border-radius: 4px;
      font-size: 10px;
      line-height: 16px;
      color: white;
      font-weight: bold;
      text-transform: uppercase;
      padding: 0 4px;

      &.no-sending-status {
        background-color: $primary-default;
      }
    }
  }
}
