@import "src/assets/variables";
.neighborhoodBlitzMap{
  .main-title{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.28;
    color: $dark-grey-blue;
    margin-bottom: 16px;
  }
  .info-text{
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;
    line-height: 1.43;
    color: $dark-grey-blue;
    margin-bottom: 32px;
  }
  .mapCard{
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
    margin-bottom: 84px;
  }
  .topMapCard{
    position: relative;
    z-index: 1;//error under map
    padding: 15px 24px;
    display: flex;
    align-items: center;
    background: #FFFFFF;
    box-shadow: inset 0px -1px 0px #E5E9ED;    
  }
  .searchAddress-info{
    @include breakpoint(sm-down){
      display: none;
    }
    flex: 1 1 auto;
    font-family: IBMPlexSans, sans-serif;
    font-size: 12px;
    line-height: 1.1;
    font-weight: 700;
    color: $dark-grey-blue;
    margin-left: 12px;
    text-align: right;
  }
  .cityResult{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.33;
    color: $dark-grey-blue;
    margin-right: 16px;
  }
  .DopeRadioGroup{
    margin-right: 18px;
  }
  .neighborhoodBlitz-wrap{
    display: flex;
    align-items: center;    
    .header-label{
      margin-bottom: 0;
      margin-right: 8px;
    }
    .form-control{
      padding: 5px;
      width: 80px;
      &:focus{
        border-width: 1px;
      }
    }
  }
  .togglemapCardViewBox{
    margin-left: auto;
    align-self: stretch;
    height: 40px;
    background: $dope-grey-10;
    border-radius: 4px;
    padding: 2px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .togglemapCardViewBtn{
      @include outline(0);
      border: none;
      padding: 2px 4px;
      border-radius: 4px;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      line-height: 1.43;
      text-align: center;
      color: $dope-grey-100;
      min-width: 104px; //because bold/normal
      min-height: 36px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: transparent;
      &:not(:last-child){
        margin-right: 2px;
      }
      &.active{
        font-weight: 700;
        background: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
        color: $dark-grey-blue;
      }
    }
  }
  .neighborhoodBlitz-container{
    @include breakpoint(xl){
      display: flex;
    }
  }
  .neighborhoodBlitzSummary{
    flex: 0 1 auto;
    width: 40%;
    background: #FFFFFF;
    box-shadow: inset 1px 0px 0px #E5E9ED;
    padding: 0 24px;
    display: flex;
    flex-direction: column;
    @include breakpoint(lg-down){
      width: 100%;
      display: flex;
      padding: 24px 12px;
    }
    .neighborhoodBlitzSummaryTitle{
      min-height: 70px;
      border-bottom: 1px solid #F2F3F5;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      font-family: IBMPlexSans, sans-serif;
      font-weight: 700;
      font-size: 18px;
      line-height: 1.33;
      text-align: center;
      color: $dark-grey-blue;
      @include breakpoint(xl){
        width: 100%;
      }
    }
    .neighborhoodBlitzSummaryItem{
      //min-height: 350px;
      //max-height: 350px;
      overflow-y: auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      font-family: IBMPlexSans, sans-serif;
      text-align: center;
      color: $dark-grey-blue;
      .dope-pagination{
        margin-bottom: 0;
      }
      .itemTitle{
        font-size: 14px;
        line-height: 1.43;
        margin-bottom: 8px;
      }
      .itemValue{
        font-weight: bold;
        font-size: 22px;
        line-height: 1.27;
        &.empty{
          color: $dope-grey-50;
        }
      }
      .itemTitleTooltips{
        margin-left: 2px;
        svg{
          path{
            fill: #354052;
          }
        }
      }
      .dope-table-container{
        width: 100%;
        th{
          padding: 14px 5px;
        }
        td{
          border: none;
          padding: 5px;
        }
        .react-bs-table-no-data{
          display: none;
        }
      }
    }
    .btnContainer{
      min-height: 88px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: auto;
    }
  }
  .neighborhoodBlitzMapContainer {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    position: relative;
    // height: 468px; //temporary
    @include breakpoint(lg-down){
      height: 468px;
    }
    .neighborhoodBlitz-amount{
      padding: 8px 24px;
      background-color: #fff;
      .d-flex{
        flex-wrap: wrap;
      }
      .title{
        margin-bottom: 6px;
        align-items: center;
        justify-content: space-between;
        display: flex;
      }
      .DopeRadioGroup{
        // flex-wrap: nowrap;
        .radio:not(:last-child){
          margin-right: 20px;
        }
      }
      svg{
        cursor: pointer;
        path{
          transition: .3s;
        }
        &:hover:not(.disabled),
        &.active{
          path{
            fill: $primary-default
          }
        }
        &.disabled{
          cursor: not-allowed;
        }
      }
    }
  }
  .neighborhoodBlitzStat{
    flex: 0 0 auto;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    &.large{
      grid-template-columns: repeat(5,1fr);
    }
  }
  .leafletMapContainer{
    flex: 1 1 auto;
  }
  .neighborhoodBlitzStatItem{
    flex: 1 1 auto;
    background: #FFFFFF;
    box-shadow: inset 0px -1px 0px #E5E9ED, inset -1px 0px 0px #E5E9ED;
    padding: 16px 8px;
    .statItemTitle{
      font-family: IBMPlexSans, sans-serif;
      font-weight: 700;
      font-size: 10px;
      line-height: 1.6;
      text-transform: uppercase;
      color: $dark-grey-blue;
    }
    .statItemVal{
      display: block;
      font-family: IBMPlexSans, sans-serif;
      font-weight: 700;
      font-size: 14px;
      line-height: 1.43;
      color: $dark-grey-blue;
      &.empty{
        text-align: center;
        color: $dope-grey-50;
      }
    }
  }
  .adv-stat-container{
    margin: 0;
    padding: 0;
    list-style-position: inside;
    display: flex;
    flex-wrap: wrap;
    font-size: 8px;
    .adv-stat-item{
      margin-right: 8px;

    }
    .title{
      color: $primary-default;
      font-weight: 700;
    }
    .info{
      color: $primary-default;
    }
  }

  .Test-container{
    margin-bottom: 12px;
  }

  .selectedResult{
    // display: flex;
    // flex-wrap: wrap;
    margin-bottom: 12px;
  }
  .selectedLatLng-container{
    margin-bottom: 12px;
    .sc-data{
      display: flex;
    }
    .sl-location{
      border: 1px solid $primary-default;
      padding: 2px 8px;
      width: 200px;
      height: 30px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 12px;
      }
    }
  }
  .selectedRoutes-container{
    .sl-routes{
      border: 1px solid $primary-default;
      padding: 2px 8px;
      // width: 200px;
      height: 30px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
    }
  }
}
.zipByAddress{
  // margin-right: 24px;
  display: flex;
  form{
    margin-right: 5px;
    .input-container{
      margin-bottom: 0;
      position: static;
    }
  }
  .dope-form-group{
    margin-bottom: 0;
  }  
  .searchZip{
    .input-container{
      @include breakpoint(sm){
        width: 350px;
      }
    }
    svg{
      position: absolute;
      top: 50%;
      left: 16px;
      transform: translateY(-50%);
    }
    .dope-input{
      padding-left: 40px;
      &:focus{
        padding-left: 39px;
      }
    }
  }
}