// Modals

.modal-header,
.modal-footer {
  background: $gray-100;
}

.modal-footer {
  @include border-bottom-radius($border-radius);  
}

.modal-content {
  border-radius: 4px;
  box-shadow: 0 4px 12px 6px rgba(0, 0, 0, 0.04);
  background-color: #ffffff;
}
.modal{
  .modal-content{
    border: none;
  }
  .modal-footer{
    .btn{
      margin: 0;
      &:not(:last-child){
        margin-right: 16px;
      }
    }
  }
}