@import "../../../../assets/variables.scss";
.Calendar {
  display: block;
  position: relative;
  width: 100%;
  .calendar-shadow{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: #ffffff;
    opacity: 0.8;
  }

  .row {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
  }

  .row-middle {
    align-items: center;
  }

  .col-start {
    justify-content: flex-start;
    text-align: left;
  }

  .col-center {
    justify-content: center;
    text-align: center;
  }

  .col-end {
    justify-content: flex-end;
    text-align: right;
  }

  .loader-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }

  &.calendar-disabled {
    pointer-events: none;
  }

  .body .row:last-child {
    border-bottom: none;
  }

  .body .cell:last-child {
    border-right: none;
  }

  .body .row {
    border-bottom: 1px solid $dope-grey-20;
  }

  .body .col {
    flex-grow: 0;
    flex-basis: calc(100%/7);
    width: calc(100%/7);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .row {
    margin: 0;
  }

  .body-disabled {
    pointer-events: none;
  }

  .body .disabled {
    color: $dope-grey-50;
  }

  .body .cell .bg {
    font-weight: 700;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;

    &.draft {
      background-color: #fcf4f4;
    }
  }

  .calendar-header {
    text-transform: capitalize;
    display: flex;
    justify-content: space-between;
    padding: 16px 0;
    z-index: 15;
    position: relative;

    .calendar-header-nav {
      display: flex;
      align-items: center;
    }

    .calendar-header-month {
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      min-width: 160px;
      text-align: center;
    }

    .calendar-header-nav-btn {
      padding: 8px;
    }

    div.calendar-header-title.dope-card-header {
      padding: 0;
    }
  }

  .days {
    text-transform: capitalize;
    font-weight: bold;
    color: $dope-grey-100;
    background-color: $dope-grey-10;
    font-size: 14px;
    line-height: 20px;
    border: 1px solid $dope-grey-20;
    border-bottom: 0;

    & > div {
      padding: 8px;

      &:not(:last-child) {
        border-right: 1px solid $dope-grey-20;
      }
    }
  }

  .body {
    border: 1px solid $dope-grey-20;

    .col {
      flex-grow: 1;
      flex-basis: 0;
      max-width: 100%;
    }

    .cell {
      position: relative;
      height: 86px;
      border-right: 1px solid $dope-grey-20;
      overflow: hidden;
      cursor: pointer;
      min-height: 0;
      padding-bottom: 12px;

      &:hover,
      &.today:hover {
        .bg {
          display: block;
        }
      }

      &.disabled:hover {
        .bg {
          display: none;

          &.sending-event {
            display: block;
          }
        }
      }

      .number {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        padding: 8px 0 4px;
        z-index: 3;
      }

      &.today .number {
        color: $primary-default;
        font-weight: bold;
      }

      .bg-status {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        opacity: .1;
      }
    }

    .bg {
      display: none;
      z-index: 5;

      .sending-block {
        opacity: .5;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
      }

      .sending-btn {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;

        button {
          padding: 8px;
          margin-right: 8px;

          svg path {
            fill: $primary-default;
          }

          &:last-of-type {
            margin-right: 0;
          }
        }
      }

      .no-sending-block + .sending-btn button {
        margin-right: 0;
      }

      .no-sending-block {
        background-color: $primary-default;
      }
    }
  }
}
