@import "../../../assets/variables";
$md-checkbox-bg-color:$dope-grey-20;
$label-color: $dark-grey-blue;
$md-checkbox-size: 24px;
$md-checkbox-int-size: 16px;

// .card .DopeCheckbox{
//     margin-bottom: 0;
// }

.DopeCheckbox {
  display: inline-flex;
  align-items: center;
  margin: 0;  
  vertical-align: middle;
  cursor: pointer; 
  input[type="checkbox"] {
    display: none;
    &:checked + .custom-checkbox:before {
      background-color: $primary-default;   
    }
    &:checked + .custom-checkbox{
      svg{
        opacity: 1;
      }
    }
    &:checked:disabled + .custom-checkbox:before {
      background-color: $primary-disabled;  
    }
    &:disabled + .custom-checkbox:before {
        background-color: $dope-grey-10;        
    }    
  }
  
  .custom-checkbox {
    display: inline-block; 
    position: relative;   
    height:$md-checkbox-size;
    width:$md-checkbox-size;   
    position: relative;    
    margin-bottom: 0;
    cursor: pointer;
    vertical-align: bottom;
    &:not(:last-child){
      margin-right: 8px;
    }
    &:before{
        position: absolute;                 
        content: '';  
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        width: $md-checkbox-int-size;
        height: $md-checkbox-int-size;
        border-radius: 4px;
        background-color: $md-checkbox-bg-color;
        transition: all .3s ease;            
    }  
    svg {
      width: $md-checkbox-int-size;
      height: $md-checkbox-int-size;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      fill: none;
      stroke: white;
      stroke-width: 3px;
      opacity: 0; 
      transition: all .3s ease;     
    }          
  }
  .checkbox-label{
      color: $label-color;
      // font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      line-height: 20px;
      user-select: none;
  }     
}