@import "src/assets/variables";

.QuarterlyPaymentModal { 
  @media (min-width: 576px){
    &.modal-dialog {
      max-width: 536px;        
    }
  } 
  .modal-content {    
    padding: 16px 24px;
  }
  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }
  .dope-modal-body{
    margin-bottom: 16px;
  }
  .dope-modal-header {
    background-color: white;
    margin-bottom: 18px;
    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }
  .modal-footer {
    border: 0;
    background-color: white;
  }
}
