@import "src/assets/variables";
.AutomationCreate{
  .back-btn-container{
    margin-bottom: 24px;
    .back-btn{
      height: 24px;
      padding: 4px 8px;
      border: none;
      @include outline(0);
      border-radius: 4px;
      background-color: $dope-grey-10;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: $primary-default;
      font-family: IBMPlexSans, sans-serif;
      font-size: 10px;
      font-weight: 700;      
      line-height: 1.6;
      text-transform: uppercase;
    }
    .back-icon{
      height: 16px;
      width: 16px;
      margin-right: 4px;
    }
  }
  .status-title{
    font-family: IBMPlexSans, sans-serif;
    font-size: 28px;
    font-weight: 700;      
    line-height: 1.14;
    margin-bottom: 32px;   
  }
  .title {
    font-family: IBMPlexSans, sans-serif; 
    font-size: 16px;
    margin-bottom: 8px;
    @include breakpoint(xl){
      font-size: 22px;
      margin-bottom: 16px;
    }
    line-height: 1.27;   
    font-weight: 700;
    color: $dark-grey-blue;
    .badge-step{
      height: 18px;
      min-width: 50px;
      font-size: 12px;
      line-height: 1.5; 
      color: #fff;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      border-radius: 4px;
      background-color: $primary-default;
      margin-right: 16px;
      @include breakpoint(xl){
        height: 32px;
        font-size: 16px;
        min-width: 72px;
      }
    }      
  }
  .sub-title{
    font-family: IBMPlexSans, sans-serif; 
    font-size: 16px;
    @include breakpoint(xl){
      font-size: 22px;
    }
    line-height: 1.27;   
    font-weight: 700;
    color: $dark-grey-blue; 
    margin-bottom: 16px;    
    .att-icon{
      position: relative;      
      transform: translate(50%,-50%);
      width: 16px;
      height: 16px;
      &.error{
        path{
          fill: $primary-default;
        }
      }
      &.success{
        path{
          fill: #39B54A;
        }        
      }
    }
  } 
  .automationEditorCard-v1{
    padding: 18px 24px;
    background: #FFFFFF;
    border-radius: 4px;    
    &:not(:last-child){
      margin-bottom: 24px;
    } 
  }  
  .automationEditorCard-v2{  
    padding: 8px 0;  
    background: #FFFFFF;
    border-radius: 4px;
    &:not(:last-child){
      margin-bottom: 24px;
    } 
    .section{
      padding: 16px;
      &:not(:last-child){
        box-shadow: inset 0 -1px 0 0 #E5E9ED;
      }      
    } 
    .section-v2{
      padding: 16px;
    }   
  }
  .bnts-container{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 32px;
    .dope-button{
      &:not(:last-child){
        margin-right: 16px;
      }
    }
  }
  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }  
  .preview-header{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;    
    .sub-title,.title{
      margin-bottom: 0;
    }
    margin-bottom: 16px;
  }
  .morpheus-templates-container {  
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .nav-link {
      padding: 8px 16px;
      &.active{
        pointer-events: none;
      }
    }
    .step-container {
      margin-bottom: 20px;
      padding: 20px 0 0 20px;
    }
    .dope-nav.nav {
      width: 100%;
      box-shadow: inset 0 -1px 0 0 #E5E9ED;
      padding: 0 16px;
    }
    .dope-tab-content {
      width: 100%;
      padding: 20px 8px 0;    
    }
    .dope-pagination{
      margin-bottom: 0;
    }
    .templates-preview-container {
      display: flex;
      flex-wrap: wrap;
      min-height: 390px; 
      position: relative; 
      &.oneRow{
        min-height: 195px;
      }
      &.disabled {
        pointer-events: none;
      }
      .loader-main{
        position: absolute;  
        z-index: 10;    
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      .no-templates-container {
        display: flex;
        justify-content: center;      
        font-size: 24px;
        color: $dope-grey-50;
        flex: 1 1 auto;
        align-items: center;
      }
      .template-preview-item {      
          flex: 0 1 50%;
        @include breakpoint(xl){
          flex: 0 1 25%;
        }      
        padding: 8px;
        margin-bottom: 20px;
        min-width: 0;
      }
      .template-preview-content {
        border: 1px solid #b7b6b6;
        padding: 10px;
        position: relative;
        &.checked{
          &:before {
            content: "";
            background-color: rgba(0, 0, 0, 0.5);
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;          
          }
          &:after {
            content: "";
            display: block;
            // TODO change to react svg object
            background: url("../../../assets/img/icons/select.svg") no-repeat center;
            height: 62px;
            width: 62px;
            background-size: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
          }
        }
      }
      .template-preview-img{
        width: 100%;
        height: auto;
      }      
    }    
  }
  .template-item{
    position: relative;
    cursor: pointer;
    .template-preview-content{
      position: relative;    
      z-index: 1;
      background-color: #fff;  
    }
    .postcards-preview-name-block {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: space-between;
      margin-top: 5px;
      .name-text{        
        padding-left: 2px;
        font-size: 14px;
        font-weight: 500;
        color: #515557;
        min-width: 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        // width: calc(100% - 32px);
      } 
    }
    .postcards-action-block {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: flex-end;
      padding: 2px 5px;
      svg path {
        fill: $primary-default;
        cursor: pointer;
      }
      transition: 1s transform ease-in-out 0.5s;
      transform: translate(0,0%);
    }
    &:hover,&:focus{
      .postcards-action-block{
        transform: translate(0,100%);
        transition: 1s transform ease-in-out;
      }
    } 
  }  
  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;    
    .AutomationSummary{
      flex: 1 1 auto;
      display: flex;
      justify-content: center;
      align-items: center;    
    }
  }
}