@import "src/assets/variables";
.AutomationEditor{
	.automationEditorCard{
		padding: 0;
		background: #FFFFFF;
		border-radius: 4px;
		// overflow: hidden;        
		&:not(:last-child){
			margin-bottom: 16px;
		}
		.title-container{
			padding: 8px 24px;
			display: flex;
			justify-content: space-between;
			align-items: center;
			min-height: 60px;
			&.t-shadow{
				box-shadow: inset 0px -1px 0px #E5E9ED;
			}
		}  
		.title {
			font-family: IBMPlexSans, sans-serif; 
			font-size: 18px;
			line-height: 1.33;   
			font-weight: 700;
			color: $dark-grey-blue;            
		}       
		.cardBody{
			padding: 24px 24px;
		}        
	}
	.automationEditorCard-v1{
		padding: 18px 24px;
		background: #FFFFFF;
		border-radius: 4px;
		// overflow: hidden;        
		&:not(:last-child){
			margin-bottom: 16px;
		}
		.title {
			font-family: IBMPlexSans, sans-serif; 
			font-size: 18px;
			line-height: 1.33;   
			font-weight: 700;
			color: $dark-grey-blue; 
			margin-bottom: 18px;           
		}  
	}    
	.contactInfo-grid{
		display: grid;
		grid-template-columns: minmax(auto,240px);
		grid-gap: 16px 0;
		@include breakpoint(sm){
			grid-template-columns: repeat(2,minmax(auto,240px));
			grid-gap: 16px 24px;
		} 
		.two-column{
			grid-column: 1/3;
		}           
	}    
	.contactInfo{
		.dope-textarea.form-control{
			min-height: unset;            
		}
		.dope-form-group{
			margin-bottom: 16px;
		}
	}    
	.btnContainer{
		padding: 6px 0 0;
		margin-bottom: 24px;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		.dope-button{
			&:not(:last-child){
				margin-right: 24px;
			}
		}
	}
	.htmltoimg-test-container{
		display: grid;
		grid-template-columns: repeat(4,1fr);
		grid-gap: 15px 15px;
		.img-container{            
			img{
				width: 100%;
				height: auto;
			}
		}
	}    
}
