.palette {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 20px 0;
  &__label {
      line-height: 2;
  }
  &__body {
    display: flex;
    flex-wrap: wrap;
    &__add {
      background: #fff;
      border-radius: 5px;
      box-shadow: 0 0 0 1px rgba(0, 0, 0, .1);
      cursor: pointer;
      margin: 5px;
      height: 38px;
      width: 38px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 2px;

    }
  }
}