@import "../../../assets/variables";

.campaigns-list-container {
  .campaigns-list-header {
    background-color: white;
    border-radius: 4px 4px 0 0;
    padding: 18px 24px;
  }

  .react-bootstrap-table .campaigns-list-table tbody tr {
    cursor: default;
  }

  .campaigns-list-table {
    .table-icon-btn {
      width: 30px;
      padding: 0;
      background-color: transparent;

      svg path {
        fill: $dope-grey-100;
      }

      &:hover,
      &:focus,
      &:active,
      &:focus:active {
        background-color: transparent !important;
      }

      &:hover {
        path {
          fill: $primary-default;
        }
        &:active {
          path {
            fill: $primary-pressed;
          }
        }
      }
    }

    tbody {
      td:nth-child(1),
      td:nth-child(2) {
        width: 30%;
      }
      td:nth-child(3),
      td:nth-child(4),
      td:nth-child(5) {
        width: 15%;
      }
      td:nth-child(6),
      td:nth-child(7) {
        width: 48px;
        padding: 14px 12px;
      }
      td:nth-child(1),
      td:nth-child(2) {
        min-width: 250px;
      }
      td:nth-child(3),
      td:nth-child(4) {
        min-width: 140px;
      }

      td:nth-child(5) {
        min-width: 165px;
      }
    }
  }
}
