@import "../../../assets/variables";

.dope-button {
  width: fit-content !important;

  &.btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 10px 16px;
    width: unset;
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
    font-weight: bold;
    white-space: nowrap;
    border: none;
  }

  &.btn-primary {    
    background-color: $primary-default;
    color: #ffffff;

    &:hover,
    &:focus {
      background-color: $primary-hover !important;
    }
    &:hover:active{
      background-color: $primary-hover !important;
    }

    &:active{
      background-color: $primary-pressed !important;
    }
    &:focus:active {
      background-color: $primary-pressed !important;
    }
    

    &:disabled {
      background-color: $primary-disabled;
    }
    .btn-icon{      
      path{
        fill: #ffffff;
      }
    }
  }

  &.btn-white {
    background-color: $dope-grey-10;  
    color: $primary-default;    

    &:hover,
    &:focus {
      background-color:  $dope-grey-10 !important;
      color: $primary-default !important;
    }
    &:hover:active{
      background-color:  $dope-grey-20!important;
      color: $primary-default !important;
    }
    
    &:active {
      background-color: $dope-grey-50!important;
      color: $primary-default !important;
    }
    &:focus:active{
      background-color:  $dope-grey-50!important;
      color: $primary-default !important;
    }

    &:disabled {
      background-color: $dope-grey-10;
      color: $dope-grey-50;
    }
    .btn-icon{      
      path{
        fill: $primary-default;
      }
    }
  }
  // span{
  //   pointer-events: none;
  // }
  .btn-icon{
    pointer-events: none;
    &:not(:first-child){
      margin-left: 8px;
    }    
  }
}