@import "src/assets/variables";

.OnePictureBuilder {    
  position: relative;
  @media screen and (min-width: 568px){
    padding: 20px;
  }
  @include breakpoint(xl){
    display: flex;
    align-items: flex-start;
  }

  .Canvas-container{     
    // box-shadow: 0 6px 10px -4px rgba(0, 0, 0, 0.15);
    background-color: #FFFFFF;         
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;
    padding: 10px;

    position: absolute;
    z-index: 10;
    top: 0%;
    left: 0%;    
    .react-pdf__Page.dope-canvas-hided{
      display: none;
    }
    .pdf-action-container{
      padding: 5px 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    .pdf-num-text{
      color: $dark-grey-blue;
      font-size: 12px;
      line-height: 14px;
      margin-right: 8px;
    }
    .btn-container{
      display: flex;
      flex: 1 1 auto;
      justify-content: flex-end;
      &:not(:last-child){
        margin-right: 8px;
      }
      .dope-button:not(:last-child){
        margin-right: 8px;
      }
    }
    .btn-icon{
      width: 20px;
      height: 20px;
    }
    .loader-main {
      position: absolute;
      top: calc(50% - 50px);
      left: calc(50% - 25px);
      z-index: 10;
    }
  }
  .csi-editor-container{   
    position: relative; 
    z-index: 5;        
    
    display: flex;
    flex-direction: column;
    .allResize-container{
      margin-bottom: 18px;
      @include breakpoint(sm){
        display: flex;         
      }     
    }
    @include breakpoint(xl){
      margin-left: 15px;
      // min-height: 450px;
      align-self: stretch;// don't toch
      // align-items: stretch;  
      flex: 1 1 auto;
      min-width: 0;
    }
    .reset-btn {
      margin: auto auto 0 0;
      button {
        display: flex;
        align-items: center;
        color: $primary-default;
        border: none;
        outline: none;
        background-color: #fff;
        cursor: pointer;
        svg {
          height: 19px;
          width: auto;
          margin-right: 5px;

          path {
            fill: $primary-default;
          }
        }
        &:disabled{
          color: $primary-disabled;
          svg { 
            path {
              fill: $primary-disabled;
            }
          }
        }
      }
    }
  }  
  .color-picker-block{
    background-color: rgb(255, 255, 255);
    border: 0px solid rgba(0, 0, 0, 0.25);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;     
    padding: 10px;
    
    // display: flex;
    // flex-wrap: wrap; 
    display: grid;
    justify-content: flex-start;
    grid-template-columns: repeat(auto-fit, minmax(210px, auto) );
    grid-gap: 15px 15px;
    &:not(:last-child){
      margin-bottom: 15px;
    }   
    // .color-picker-item{ 
    //   margin-bottom: 5px;     
    //   &:not(:last-child){
    //     margin-right: 15px;
    //   }
    // }
    .sketch-picker{
      padding: 5px 5px 0px!important;
      .flexbox-fix{        
        input{
          font-size: 10px!important;
        }
        // input+span{
        //   // font-size: 8px!important;
        // }
        &:nth-child(3){          
          >div:not(:first-child){
            padding-left: 2px!important;
          }
        }
        &:nth-child(4){  
          margin: 0px -5px!important;
          padding: 5px 0px 0px 5px!important;       
          >div{
            margin: 0px 6px 6px 0px!important;
          }
        }
      }
    }
  }  

  .upload-img-container {
    // width: 276px;
    background-color: rgb(255, 255, 255);
    border: 0px solid rgba(0, 0, 0, 0.25);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;    
    @include breakpoint(xl){
      display: flex;
      // flex-direction: column;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
    }    
    padding: 10px;  
    margin-bottom: 15px;  
    // .ReactCrop{width: 500px;} 
  }

  .image-action-container{
    &:not(:last-child){
      margin: 0 0 15px 0;
      @include breakpoint(xl){
        margin: 0 15px 0 0;
      }   
    }     
  }

  .image-action-box{
    display: flex;
    flex-direction: column;
    .btn{      
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }
  } 
  
}

@media screen and (max-width: 1600px) {
  .OnePictureBuilder {
    .color-picker-container {
      flex-direction: row;
    }
  }
}

.OnePictureBuilder{
  .template-container {
    position: relative;
    .Morpheus-card-container{
      pointer-events: auto!important;
      .morpheus-text-token{
        visibility: hidden;
      }
    }          
    &:before{
      transition: border-color 300ms ease-in-out; 
      content: '';
      pointer-events: none;
      position: absolute;
      top: 0px;
      left: 0px; 
      border: solid 37px transparent;              
      z-index: 100;
      opacity: 0.95;
      // height: 1950px;      
      // width: 2775px;          
    }
    &:hover{
      &:before{                  
        border: solid 37px #FCD3D5;        
      }      
    }                  
  }    
  .spects-container{
    margin-bottom: 15px;
    @include breakpoint(xl){
      margin-bottom: 0;
      position: absolute;      
      left: 25px;
    } 
    .spects-text{
      color: $dope-grey-100;        
      font-size: 14px;        
      line-height: 1.43;
    }
    .spects-linktext{
      margin-left: 8px;
      color: $primary-default;
      text-decoration: underline;
      cursor: pointer;
    }      
  } 
  &.size-xs{
    .template-container:before{
      height: 1875px;
      width: 2775px;
    }
  }
  &.size-sm{
    .template-container:before{
      height: 2025px;
      width: 2775px;
    }
  }
  &.size-md{
    .template-container:before{
      height: 2025px;      
      width: 3375px;
    }
  }  
  &.size-lg{
    .template-container:before{
      height: 2625px;      
      width: 3375px;
    }
  }    
  @media screen and (min-width: 568px){
    &.size-sm{
      .template-container:before{
        height: 2025px;      
        width: 2775px;
      }
    } 
    &.size-md{
      .template-container:before{
        height: 2025px;      
        width: 3375px;
      }
    }  
    &.size-lg{
      .template-container:before{
        height: 2625px;      
        width: 3375px;
      }
    }   
  }  
  @media screen and (min-width: 1200px){
    &.size-xs{
      .spects-container{
        top: 308px;
      }
    }
    &.size-sm{
      .spects-container{
        top: 330px;
      }
    }
    &.size-md{
      .spects-container{
        top: 280px;
      }
    }
    &.size-lg{
      .spects-container{
        top: 356px;
      }
    }
  }
  @media screen and (min-width: 1350px){
    &.size-xs{
      .spects-container{
        top: 375px;
      }
    }
    &.size-sm{
      .spects-container{
        top: 390px;
      }
    }
    &.size-md{
      .spects-container{
        top: 330px;
      }
    }
    &.size-lg{
      .spects-container{
        top: 420px;
      }
    }
  }
  @media screen and (min-width: 1600px){
    &.size-xs{
      .spects-container{
        top: 395px;
      }
    }
    &.size-sm{
      .spects-container{
        top: 415px;
      }
    }
    &.size-md{
      .spects-container{
        top: 355px;
      }
    }
    &.size-lg{
      .spects-container{
        top: 450px;
      }
    }
  }
}

