@import "src/assets/variables";

.delete-yardsigntemplate-modal {
  top: 50% !important;
  transform: translateY(-50%) !important;  
  &.modal-dialog {
    max-width: 400px;
  }
  .modal-content {
    background-color: white;
    padding: 16px 24px;
  }
  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }
  .dope-modal-header {
    background-color: white;
    margin-bottom: 18px;
    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }
  .dope-modal-body {
    margin-bottom: 24px;
  }
  .modal-footer {
    border: 0;
    background-color: white;
  }
}
