  }
  .spects-container{
    margin-bottom: 15px;
    @include breakpoint(xl){
      margin-bottom: 0;
      position: absolute;
      // bottom: 20px;
      left: 25px;
    } 
    .spects-text{
      color: $dope-grey-100;        
      font-size: 14px;        
      line-height: 1.43;
    }
    .spects-linktext{
      margin-left: 8px;
      color: $primary-default;
      text-decoration: underline;
      cursor: pointer;
    }      
  }
}
.postcard-wizard{
  .spects-container{
    @media screen and (min-width: 1200px){
      top: 308px;
    }
    @media screen and (min-width: 1350px){
      top: 375px;
    }
    @media screen and (min-width: 1600px){
      top: 395px;
    }
  }
}
.sending-page-content{
  .spects-container{
    @include breakpoint(xl){      
      bottom: 20px;
    }
  }
}

.sending-page-content{
  .disabled-dope-card{
    .spects-container{
      display: none;    
    }
  }
}