@import "src/assets/variables";
.CreditCardsAfter{
  flex: 1 1 auto;
  max-width: 525px;
  padding: 24px;
  background: #FFFFFF;
  border-radius: 4px;
  .credit-cards-title{
    font-family: IBMPlexSans, sans-serif;   
    font-weight: 700;
    font-size: 18px;
    line-height: 1.33;
    color: $dark-grey-blue;
    margin-bottom: 16px;
  }
  .credit-cards-text{
    font-family: IBMPlexSans, sans-serif;    
    font-weight: 400;
    font-size: 14px;
    line-height: 1.43;    
    color: $dark-grey-blue;
    margin-bottom: 18px;
  }
  
  .credit-card-after-container{  
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 16px;
    .CardElement-CN{
      flex: 1 1 auto;
      margin-right: 16px;
    }
    .CardElement-ED{
      flex: 0 0 auto;
      min-width: 86px;
      margin-right: 16px;
    }
    .CardElement-CVC{
      flex: 0 0 auto;
      min-width: 70px;
    }
    .CardElTitle{
      color: #7F8FA4;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      font-weight: 600;    
      line-height: 1.7;
      .star{
        color: #C7001C;
      }
    }  
  
    .border-filed{
      height: 40px;  
      padding: 8px 14px;  
      // border: 1px solid #B9B9B9;
      border: 1px solid #e5e9ed;    
      border-radius: 4px;
      display: flex;
      justify-content: flex-start;
      align-items: center;    
    }
    .StripeElement{
      flex: 1 0 auto;
      // width: 100%;
      // height: 40px;    
      // border: 1px solid #B9B9B9;
      // border-radius: 4px;
    }
    &.twoRows{
      flex-wrap: wrap;
      .CardElement-CN{
        margin-right: 16px;
        flex: 1 1 auto;
        width: 100%;
        max-width: 195px;
      }
  
      // centering cards inputs
      @media screen and (min-width: 1200px) and (max-width: 1720px){
        display: flex;
        flex-direction: column;
        .CardElement-CN{       
          margin-right: 0;
        }
      } 
      @media screen and (max-width: 576px){
        display: flex;
        flex-direction: column;
        .CardElement-CN{       
          margin-right: 0;
        }
      } 
    }
  }
  .credit-card-actions{
    display: flex;
    justify-content: space-between;
    .btn-icon{
      margin-right: 12px;
    }
  }
}