@import "../../assets/variables";

.credit-card-container {
    border-radius: 0;

    .master-card-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/master-card-logo.png') no-repeat center;
        background-size: 80%;
        height: 37px;
        width: 71px;

    }

    .visa-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/visa-logo.png') no-repeat center;
        background-size: 80%;
        height: 37px;
        width: 71px;

    }

    .american-express-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/express.jpg') no-repeat center;
        background-size: 80%;
        height: 37px;
        width: 71px;
    }

    .discover-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/discover.png') no-repeat center;
        background-size: 100%;
        height: 37px;
        width: 71px;
    }

    .diners-club-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/diners-club.png') no-repeat center;
        background-size: 80%;
        height: 38px;
        width: 71px;
    }

    .jcb-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/jcb.png') no-repeat center;
        background-size: 85%;
        height: 38px;
        width: 71px;
    }

    .unionpay-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/unionpay.png') no-repeat center;
        background-size: 85%;
        height: 38px;
        width: 71px;
    }

    .unknown-ico::before {
        content: "";
        display: block;
        background: url('../../assets/img/credit-cards/unknown.jpeg') no-repeat center;
        background-size: 85%;
        height: 38px;
        width: 71px;
    }
    

    

    
    
    table {
        width: 100%;
    }

    td,
    tr {
        display: flex;
        align-items: center;
    }

    tr {
        // padding: 0 20px;
        // justify-content: space-between;
        // position: relative;

        &:not(:last-child):hover,
        &.selected-card {
            background-color: $athens-gray;
            cursor: pointer;
        }

        &:last-child { 
            padding-top: 10px;           
            display: block;
            
            td:first-child {
                display: block;
            }
        }

        // &.selected-card::before {
        //     content:"";
