        //     background: url('../../assets/img/icons/select-grey.svg') no-repeat center;
        //     height: 24px;
        //     width: 24px;
        //     background-size: 100%;
        //     position: absolute;
        //     top: calc( 50% - 12px);
        //     left: 0;
        // }
    }

    .validation-message {
        position: relative;
    }

    h6 {
        margin: 0 10px 0 0;
    }

    // .card-header > .wizard-error {
    //     font-family: IBMPlexSans, sans-serif;
    // }
}

.order-step3 {
    .wizard-block-header-container {
        display: flex;

        .wizard-block-header {
            margin-right: 10px;
            font-size: 24px;
            line-height: 28px;
            margin-bottom: 10px;
        }

        .wizard-error {
            display: flex;
            align-items: flex-end;
            margin-bottom: 10px;
        }
    }
}