@import "../../assets/variables";

.dope-table-container {
  position: relative;

  .loader-container {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }

  .react-bootstrap-table {
    background-color: white;
    // WTF ??? WHY???
    // overflow-x: scroll; 
    overflow-x: auto;   

    .table {
      margin-bottom: 0;
      border-left: 0;
      border-right: 0;

      thead th {
        color: $dope-grey-100;
      }

      tbody tr {
        color: $dark-grey-blue;
        cursor: pointer;

        &:hover {
          background-color: $dope-grey-5;
        }
      }

      thead,
      tbody {
        th,
        td {
          padding: 14px 24px;
          line-height: 20px;
          text-align: left;
          outline: none;
          border-left: 0;
          border-right: 0;
        }
      }

      tbody td.icon-cell {
        padding: 0 12px;

        svg path {
          fill: $dope-grey-100;

          &:hover {
            fill: $dark-grey-blue;
          }
        }

        .disabled-ico {
          pointer-events: none;
      
          svg path {
            fill: $dope-grey-20;
          }   
        }
      }

      .react-bs-table-no-data {
        text-align: center;
        color: $dope-grey-100;
        padding: 22px;

      }
    }

    .dropup .caret {
      content: "";
      border-top: 0;
      border-bottom: 4px dashed;
    }

    .dropup,
    .dropdown {
      color: $primary-disabled;
    }
  }

  .table-row {
    cursor: pointer;
    &.active{
      background-color: $dope-grey-10;
    }
  }
  .table-row:hover {
    background: #dee2e6;
  }

  .verified-cell {   

    .invalidated-contact {
      font-size: 10px;
      line-height: 20px;
      // color: $primary-default;
      color: #d52330;
    }
  }
}

.dope-pagination  {
  display: flex;
  list-style: none;
  justify-content: center;
  padding: 18px 0;
  background-color: white;

  .rc-pagination-item, 
  .rc-pagination-jump-prev,
  .rc-pagination-jump-next {
    @include outline(0);        
    flex-basis: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    // color: $primary-default;
    color: $dope-grey-50;    
    line-height: 20px;
    font-weight: bold;    
    cursor: pointer;
    margin-right: 8px;    
    &:hover{
      color: $primary-default;
    }
  }

  .rc-pagination-item-active {    
    color: $primary-default;
    // color: $dope-grey-50;
    cursor: initial;
  }

  .rc-pagination-disabled {
    svg path {
      fill: $dope-grey-50;
      cursor: initial;
    }
  }

  li.rc-pagination-prev {
    @include outline(0);
    margin-right: 16px;
    cursor: pointer;
    svg{
      path{
        fill: $primary-default;
      }
    }
  }

  li.rc-pagination-next {
    @include outline(0);
    margin-left: 8px;
    cursor: pointer;
    svg{
      path{
        fill: $primary-default;
      }
    }
  }
}

@include breakpoint(notebook){
  .dope-table-container .react-bootstrap-table .table {
    thead th, 
    tbody td {
      padding: 14px;
    }

    .icon-button:not(:last-of-type) {
      margin-right: 10px;
    }
  }
}