@import "src/assets/variables";

.ListData {  
  .title{
    font-family: IBMPlexSans, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.29;
    color: $dark-grey-blue;
    margin-bottom: 30px;
  }
  .listDataCard{
    padding: 0;
    background: #FFFFFF;
    border-radius: 4px;
    // overflow: hidden;        
    &:not(:last-child){
        margin-bottom: 16px;
    }
    .cardTitleContainer{
        padding: 8px 24px;
        display: flex;        
        align-items: center;
        min-height: 60px;
        &.t-shadow{
            box-shadow: inset 0px -1px 0px #E5E9ED;
        }
    }  
    .cardTitle {
        font-family: IBMPlexSans, sans-serif; 
        font-size: 18px;
        line-height: 1.33;   
        font-weight: 700;
        color: $dark-grey-blue;            
    }       
    .cardBody{
        padding: 24px 24px;
    }  
         
  }
  .listDataRequestCard{
    .dope-form-group{
      margin-bottom: 15px;
    } 
    .textarea-title{
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $dope-grey-100;
      margin-bottom: 0;
    }
    .checkboxes-title{
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: #7f8fa4;
      margin-bottom: 0;
      sup{
        color: var(--color-primary, #d52330);
      }
    }
    .checkboxes-group{
      @include outline(0);
      display: flex;
      flex-wrap: wrap;
      padding: 8px 12px 0;
      border: 1px solid #e5e9ed;      
      box-shadow: 0px 0px 2px #c5cbd2;
      border-radius: 4px;
      background-color: #fff;
      > label{
        &:not(:last-child){
          margin-right: 16px;
        }
        font-size: 14px;
        line-height: 24px;
        font-weight: 600;
        color: #7f8fa4;
      }      
    }
  }
  .list-grid{    
    @include breakpoint(md){
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 0 24px;
    }
    @include breakpoint(xl){
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .list-50-grid{
    @include breakpoint(sm){
      display: grid;
      grid-template-columns: auto 1fr;
      grid-gap: 0 24px;
    }
  }  
}
.list-data-final-modal{    
  .modal-img-container {
    display: flex;
    justify-content: center;
    margin: 50px 100px;
  }

  p {
    font-size: 20px;
    text-align: center;
  }

  .modal-footer {
    display: flex;
    justify-content: center;
    border-top: none;
    background-color: white;
    padding-bottom: 24px;

    .dope-button {
      padding: 10px 50px;
    }
  }
}