@import "src/assets/variables";
.NeighborhoodBlitzSummary{
  .deleted{
    position: relative;
    &:after{
      content: 'deleted';
      position: absolute;
      width: 100%;
      height: 100%;
      opacity: 0.4;
      color: $primary-default;
      top: 0;
      left: 0;
      font-size: 5vw;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &.f10{
      &:after{
        font-size: 10vw;
      }
    }
    &.f19{
      &:after{
        font-size: 19vw;
      }
    }
  }
  .main-title{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 22px;
    line-height: 1.27;
    color: $dark-grey-blue;
    margin-bottom: 16px;
  }
  .EddmSummaryCard{
    padding: 0;
    background: #FFFFFF;
    border-radius: 4px;
    overflow: hidden;
    &:not(:last-child){
      margin-bottom: 16px;
    }
    .react-bootstrap-table{
      thead th{
        width: auto;
        min-width: auto;
      }
      tbody{
        user-select: none;
      }
    }
    .dope-pagination{
      margin-bottom: 0;
    }
  }
  .title-container{
    padding: 8px 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 60px;
    &.t-shadow{
      box-shadow: inset 0px -1px 0px #E5E9ED;
    }
  }
  .title {
    font-family: IBMPlexSans, sans-serif;
    font-size: 18px;
    line-height: 1.33;
    font-weight: 700;
    color: $dark-grey-blue;
  }
  .cardBody{
    padding: 0 24px 8px;
    &.scroll304{
      max-height: 304px;
      overflow: auto;
    }
  }
  .summaryGrid{
    margin-bottom: 16px;
    @include breakpoint(xl){
      display: grid;
      grid-template-columns: repeat(2,1fr);
      align-items: start;
      grid-gap: 0 16px;
    }
    .summaryGridCard{
      padding: 0;
      background: #FFFFFF;
      border-radius: 4px;
      @include breakpoint(lg-down){
        &:not(:last-child){
          margin-bottom: 16px;
        }
      }
    }
    .cardBodySection{
      box-shadow: inset 0px -1px 0px $dope-grey-20;
    }
    .sectionOrderItem{
      min-height: 48px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 4px 24px;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      line-height: 1.43;
      color: $dark-grey-blue;
    }
    .item-text{
      font-weight: 400;
    }
    .item-value{
      font-weight: 700;
    }
    .totalSection{
      min-height: 64px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 4px 24px;
      font-family: IBMPlexSans, sans-serif;
      font-weight: 700;
    }
    .item-total-text{
      font-size: 18px;
      line-height: 1.33;
    }
    .item-total-value{
      font-size: 28px;
      line-height: 1.27;
    }
    .delivGridCard{
      padding: 0;
      background: #FFFFFF;
      border-radius: 4px;
      .title{
        margin-bottom: 15px;
      }
      &.coldirection100{
        display: flex;
        flex-direction: column;
      }
      .title-container{
        &.noShrink{
          flex: 0 0 auto;
        }
        .itemTitleTooltips{
          margin-left: 8px;
          path{
            fill: $dark-grey-blue;
          }
        }
      }
      .delivGridCardBody{
        &.fullServ{
          flex: 1 1 auto;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center;
        }
      }
      .fullInfo{
        font-family: IBMPlexSans, sans-serif;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.43;
        text-align: center;
        text-transform: uppercase;
        color: $dark-grey-blue;
        margin-bottom: 8px;
      }
      .fullInfo2{
        font-family: IBMPlexSans, sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.43;
        text-align: center;
        color: $dope-grey-100;
        margin-bottom: 0;
      }
      .deliveryInfo-grid{
        box-shadow: inset 0px -1px 0px #E5E9ED;
        padding: 24px 24px 8px;
        label{
          font-size: 0.8571em;
          margin-bottom: 5px;
          color: #9A9A9A;
        }
        .account-input-container{
          margin-bottom: 15px;
        }
      }
      .shipping-container{
        padding: 24px;
        .sub-title{
          font-family: IBMPlexSans, sans-serif;
          font-weight: 700;
          font-size: 14px;
          line-height: 1.43;
          color: $dark-grey-blue;
          margin-bottom: 24px;
        }
        .shipping-radio-row{
          display: flex;
          justify-content: space-between;
          align-items: center;
          &:not(:last-child){
            margin-bottom: 14px;
          }
          .shipingService-price{
            font-family: IBMPlexSans, sans-serif;
            font-weight: 700;
            font-size: 14px;
            line-height: 1.43;
            text-align: right;
            color: $dark-grey-blue;
          }
        }
      }
    }
  }

  .summary-radio-row{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    min-height: 48px;
    // &:not(:last-child){
    //   margin-bottom: 16px;
    // }
    .DopeRadio{
      margin-right: 16px;
      min-width: 200px;
    }
    .tooltips-text{
      color: $dope-grey-100;
      font-size: 12px;
      font-weight: 600;
      line-height: 1.33;
      text-decoration: underline;
      cursor: pointer;
    }
    .summary-tooltip{
      width: 360px;
      max-width: 100%;
      border-radius: 4px;
      span{
        display: block;
        font-size: 12px;
        font-weight: 400;
        line-height: 18px;
        color: #ffffff;
      }
    }
  }
  // .prev-container{
  // 	padding: 0 24px 24px;
  // 	display: inline-grid;
  // 	grid-template-columns: minmax(auto,300px);
  // 	grid-gap: 24px 0;
  // 	@include breakpoint(sm){
  // 		grid-template-columns: repeat(2, minmax(auto,300px));
  // 		grid-gap: 0 24px;
  // 	}
  // 	.img-container{
  // 		// max-width: 300px;
  // 	}
  // }
  .btnContainer{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 8px;
    margin-bottom: 24px;
  }
  .EddmSummary-less{
    padding-top: 8px;
    text-align: center;
    h5{
      font-weight: bold;
      font-family: IBMPlexSans, sans-serif;
      font-size: 14px;
      line-height: 20px;
      text-transform: uppercase;
      color: $dark-grey-blue;
      margin-bottom: 0;
      span{
        color: #D52330;
        cursor: pointer;
      }
    }
    p{
      margin-bottom: 8px;
      color: $dope-grey-100;
    }
  }
}