@import "../../../../assets/variables";

.modal-dialog.edit-list {
  max-width: 536px;
  border-radius: 4px;
  // .modal-content {
  //   background-color: white;
  // }
}
.edit-list {
  .modal-content {
    padding: 16px 24px;
  }
  .modal-body,
  .modal-header,
  .modal-footer {
    padding: 0;
    border: none;
    background-color: white;
  }
  .dope-modal-header {
    border-radius: 4px 4px 0 0;
    margin-bottom: 18px;

    .modal-title {
      font-size: 22px;
      line-height: 28px;
      color: $dark-grey-blue;
      font-weight: bold;
    }
  }
  .dope-form-group {
    margin-bottom: 24px;     

    .input-title {
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $regent-gray;
      margin-bottom: 0;
    }
    .textarea-title {
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $regent-gray;
      margin-bottom: 0;
    }
    .dope-textarea{
      min-height: 80px;
    }        
  }
  sup {
    color: $primary-default;
  }  
}
