@import "../../../../assets/variables";

.postcard-order-summary {
    flex-grow: 1;
    margin-bottom: 0;
    padding: 0 15px;

    .card-header {
        margin-bottom: 10px;
        text-transform: none;
        font-weight: 700;
    }

    .order-summary-label {
        font-size: 16px;
        color: black;
    }

    .order-summary-count {
        margin-bottom: 24px;

        & > div {
            max-width: 210px;
        }
    }

    .order-summary-total {
        margin-bottom: 13px;
        font-size: 16px;

        &:last-child {
            margin-bottom: 0;
        } 
    }

    .input-group {
        max-width: 425px;
        margin-bottom: 24px;         
    }
    .form-group{
        .input-group {
            margin-bottom: 8px;
        }
    }
}