@import "src/assets/variables";


.FiltersModal {  
  &.modal-dialog{
    @media screen and (min-width: 576px){
      max-width: 800px;    
    }
  }
  .modal-content {
    // background-color: white;
    padding: 0;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }

  .dope-modal-header {
    padding: 16px 24px;
    background-color: white;
    margin-bottom: 0;
    box-shadow: inset 0 -1px 0 0 #E5E9ED;

    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }

  .dope-modal-body {
    box-shadow: 0 1px 0 0 #E5E9ED;
    // padding: 0 24px 12px;
    padding: 0 24px;
    .filters-list{
      margin: 0;
      padding: 0;
      list-style: none;
    }
    .filters-list-item{
      color: $dark-grey-blue;
      font-family: IBMPlexSans, sans-serif;
      font-size: 18px;
      font-weight: 700;      
      line-height: 1.5;             
      &:not(:last-child){
        box-shadow: inset 0 -1px 0 0 #E5E9ED;
      }          
    }    
  }

  .filter-set{
    padding-bottom: 16px;
  }

  .modal-footer {
    border: 0;
    padding: 16px 24px;
    background-color: white;
    justify-content: space-between;
  }
}
