@import "src/assets/variables";
.MapSetting{
  position: absolute;
  bottom: 10px;
  right: 10px;
  z-index: 2;
  .map-setBtn{
    border: none;
    border-radius: 4px;
    background-color: #fff;
    color: $primary-default;
    &:not(:last-child){
      margin-right: 1px;
    }
    &.active{
      background-color: $primary-default;
      color: #fff;
    }
    &:hover{
      background-color: $primary-hover;
      color: #fff;
    }
  }
}
