@import "src/assets/variables";

.UsingData{
  .page-title {
    font-size: 28px;
    line-height: 32px;
    font-weight: bold;
    margin-bottom: 32px;
    color: $dark-grey-blue;
  }
  .usdata-container {
    background-color: white;
    border-radius: 4px;
  }  
  .dope-tab-content{
    padding: 0 0 40px;
  }  
  .usdata-nav-tabs{
    border: none;
    align-self: flex-end; 
    margin: 0;   
    padding: 8px 24px 0;  
    .usdata-nav-item{
      margin: 0;    
      &:not(:last-child){
        margin-right: 12px;
      } 
      @include outline(0);      
      .nav-link{
        color: $dark-grey-blue!important;        
        font-size: 14px;
        line-height: 1.43;
        border-style: solid;
        border-width: 0 0 2px 0;
        border-color: transparent; 
        transition: border-color 300ms ease 0s; 
        // min-width: 130px;
        height: 40px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
        &.active{
          font-weight: 700;          
          border-width: 0 0 2px 0;
          border-color: $primary-default;
        }
        @include outline(0);
        &:hover,&:active,&:focus{
          border-width: 0 0 2px 0;
          border-color: $primary-default;          
        }
      }           
    } 
    .usdata-nav-item:nth-child(1){      
      .nav-link{
        min-width: 100px;
      }
    }
    .usdata-nav-item:nth-child(2){
      .nav-link{
        min-width: 100px;
      }
    }    
  }
  .usdata-table {
    .react-bootstrap-table {
      thead {        
        th{
          user-select: none;
          white-space: nowrap;
          &.sortable{
            cursor: pointer;
            // .order{
            //   display: none;
            // }
            .react-bootstrap-table-sort-order{
              color: var(--color-primary);
            }
          }          
          &:nth-child(1){
            width: 250px;
            min-width: 250px;            
          }
          &:nth-child(2){
            width: 150px;
            min-width: 150px;
          }
          &:nth-child(3){
            width: 130px;
            min-width: 130px;
          } 
          &:nth-child(4){
            width: 130px;
            min-width: 130px;
          } 
          &:nth-child(5){
            width: 130px;
            min-width: 130px;
          }                    
        }        
      }
      td{
        word-break: break-word;
      }
    }  
  }
  .filters-container{
    background-color: white;
    border-radius: 4px;    
    margin-bottom: 16px;
    padding: 8px 24px; 
    .filters-wrapper{
      padding: 24px 0;      
    }  
    .udbtn-container{
      display: flex;
      .dope-button{
        &:not(:first-child){
          margin-left: 12px;
        }
      }      
      .btn-primary{
        path{
          fill: #ffffff;
        }
      }
    }
    .filter-wrapper{
      margin-bottom: 16px;
      .filter-title{
        font-size: 18px;
        line-height: 1;
        font-weight: bold;
        margin-bottom: 8px;
        color: $dark-grey-blue;
      }
    }
  }
  .date-filters-wrapper{
    @include breakpoint(sm){
      display: flex;
    }
    .date-filter{
      max-width: 200px;
      @include breakpoint(xs-only){
        &:not(:last-child){
          margin-bottom: 4px;
        }
      }
      @include breakpoint(sm){        
        &:not(:last-child){
          margin-right: 12px;
        }
      }      
      label{
        margin-bottom: 4px;
        font-weight: 700;
        cursor: pointer;
      }
      .dope-input{
        cursor: pointer;
      }
    }
  }
  .count-range{
    padding: 0 12px;
  }
  .input-range{
    margin: 32px 0 12px;
    .input-range__track{
      background-color: $dope-grey-10;
      .input-range__track--active{
        background-color: $primary-default;
      }
      .input-range__slider{
        background-color: #fff;
        width: 16px;
        height: 16px;
        border: 4px solid $primary-default;
        margin-top: -0.85rem;
        margin-left: -0.7rem;
        box-sizing: content-box;
        transform: scale(1);
      }
      .input-range__label-container{
        color: #fff;
        background-color: $dark-grey-blue;
        top: -10px;
        padding: 4px 8px;
        border-radius: 4px;
        &::after{
          content: "";
          width: 0;
          height: 0;
          position: absolute;
          bottom: -4px;
          left: 50%;
          margin-left: -6px;
          border-left: 6px solid transparent;
          border-right: 6px solid transparent;
          border-top-color: $dark-grey-blue;
          border-top-style: solid;
          border-top-width: 4px;
        }
      }
    }
    .input-range__label:not(.input-range__label--value){
      display: none;
    }
  }
}