@import "src/assets/variables";

.wizard-main-container {  

  .validation-message {
    color: #d52330;
    font-size: 10px;
    font-weight: normal;
    text-transform: none;
    position: absolute;
  }

  .template-preview {
    margin-bottom: 50px;

    & + div {
      margin-bottom: 50px;
    }
  }

  .card-header {
    color: black;
    font-size: 16px;
    font-weight: normal;
  }

  .order-summary-container,
  .shipping-address-container,
  .credit-card-container,
  .postcard-order-summary,
  .pricing-card{
    .card-header {
      font-weight: 700;
      text-transform: none;
      h6{
        font-weight: 700;        
      }
    }
  }

  .credit-card-container {
    // padding: 0 15px;
    padding: 0;
    border-radius: 0;
  }

  .field {
    margin-bottom: 20px;
    position: relative;
  }

  .dope-input.form-control {
    // border-color: $nobel;
    // color: $mine-shaft;

    &::placeholder {
      color: rgba($mine-shaft, .5);
    }
  }
}
.postcard-wizard{
  .header-title{
    font-size: 26px;
    color: $primary-default;
  }
  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.postcard-wizard .builder-container {  
  .template-container {
    flex: 0 1 auto;
    height: 310px;
    width: 410px;
    transform: scale(0.15);  
    transform-origin: left top;  

