@import "src/assets/variables";
.resize-OPB-container{
  display: flex;
  &:not(:last-child){
    margin: 0 0 8px 0;
    @include breakpoint(sm){
      margin: 0 8px 0 0;         
    }    
  }
  .resize-OPB-title{
    font-size: 16px;
    margin: 5px;
  }
  .resize-OPB-editor{
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 15px;
    background: #ffffff;
    border: 0px solid rgba(0, 0, 0, 0.25);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 1px 4px;
    border-radius: 4px;
    .btn{
      &:not(:last-child){
        margin-right: 8px;
      }
    }
  }
}