@import "src/assets/variables";

.EddmList {
  .title-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
  }
  .title{
    font-family: IBMPlexSans, sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.29;
    color: $dark-grey-blue;
  }
  .eddmListTable{
    border-bottom: 1px solid transparent;
    background: #fff;
    .table-row{
      td{
        &:nth-of-type(2){
          max-width: 300px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          span:not(:last-of-type){
            margin-right: 5px;
            &::after{
              content: ',';
            }
          }
        }
        &:nth-of-type(4){
          font-weight: bold;
          text-transform: capitalize;
        }
      }
      &.sent{
        td:nth-of-type(4){
          color: #39B54A;
        }
      }
      &.in-progress{
        td:nth-of-type(4){
          color: #F5A623;
        }
      }
    }
    th{      
      &:last-of-type{
        width: 48px;
      }
    }
    @include breakpoint(xl){
      // th{
      //   &:last-of-type{
      //     display: none;
      //   }
      // }
      tr{
        transform: scale(1);        
        .icon-cell{
          // position: absolute;//SAFARI DON'T WORK POSITION: RELATIVE FOR ROW
          // top: 0;//SAFARI DON'T WORK POSITION: RELATIVE FOR ROW
          // bottom: -1px; //SAFARI DON'T WORK POSITION: RELATIVE FOR ROW   
          // display: flex;
          // justify-content: center;
          // align-items: center;
          .del-icon{
            opacity: 0;
            transition: .3s;
            pointer-events: none;
          }
        }
        &:hover{
          .icon-cell{            
            .del-icon{
              opacity: 1;
              pointer-events: auto;
            }
          }
        }
      }
    }
  }
  .eddmList-header {    
    display: flex;
    align-items: center;
    justify-content: space-between;    
    padding: 8px 24px;
    min-height: 56px;
  }
  .zipSearch{
    flex: 0 1 auto;
    margin-left: 16px;
    display: flex;
    .dope-form-group{
      margin-bottom: 0;
    }
    .searchZip{
      svg{
        position: absolute;
        top: 50%;
        left: 16px;
        transform: translateY(-50%);
      }
      .dope-input{
        padding-left: 40px;
        &:focus{
          padding-left: 39px;
        }
      }
    }
  }
  .noEddmList{
    min-height: 92px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $dope-grey-50;
    font-family: IBMPlexSans, sans-serif;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.27;
    text-align: center;
    border-top: 1px solid #dee2e6;
    box-shadow: inset 0 -1px 0 0 #E5E9ED;
    margin-bottom: 8px;
    position: relative;
    .loader-container{
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}