@import "src/assets/variables";


.SpectsModal {  
  &.modal-dialog{
    @media screen and (min-width: 576px){
      max-width: 624px;    
    }
  }
  .modal-content {
    // background-color: white;
    padding: 16px 24px;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }

  .dope-modal-header {
    background-color: white;
    margin-bottom: 18px;

    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }

  // .dope-modal-body {}

  .modal-footer {
    border: 0;
    background-color: white;
  }
}
