@import "../../assets/variables";
.yarn-sign-wizard {
  .header-title{
    font-size: 26px;
    color: $primary-default;
  }
  .order-screenshot-block {
    background-color: #edf0f5;
    padding: 15px 0;
  }  
  .pagination {
    justify-content: center;
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: 1rem;
    img.prev-icon {
      transform: scaleX(-1);
      filter: FlipH;
    }  
    .page-item {
      margin-right: 16px;
      .page-link {
        border: none;
        text-align: center;
        padding: 0;
        width: 24px;
        height: 24px;
        background-color: transparent;
        font-size: 17px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #3d3d3d;
        border-radius: 0;
      }      
      &.active {
        .page-link {
          background-color: #e5e9ed;
          color: #3d3d3d;
          border-radius: 0;
        }
      }  
      &.disabled {
        opacity: 0.3;
      }
    }
  }
  .order-summary-size-container{
    padding: 8px 20px;
		margin-bottom: 8px;
    .order-radio-container{
      .radio{
        min-width: 100px;
      }
    }
	}
  .order-summary-label {
		font-size: 20px;
		color: black;
		margin-bottom: 12px;
		font-weight: 500;
	}
  .order-radio-container{
		.radio{
			min-width: 150px;
		}
	}
}
.wizard-main-container {  
  .wizard-section-header.card-header {
    margin-bottom: 24px;
  }
  .radio-container{
    padding: 0 15px;
  }
  .templates {
    min-height: 300px;
    position: relative;
    padding: 15px;
  }
  .wizard-btn-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 48px;
    &.btn-center {
      justify-content: center;
    }
    .btn:not(:last-child) {
      margin-right: 16px;
    }
  }
  .wizard-error {
    font-size: 10px;
    color: #dc3545;
    text-transform: none;
    font-weight: 400;
  }
  .loader-main {
    position: absolute;
    top: calc(50% - 50px);
    left: calc(50% - 25px);
    z-index: 10;
  }
  .wizard-section-header {
    .step {
      text-transform: uppercase;      
      &:nth-child(2){
        margin-left: 12px;           
      }
    }
  }
}
.yard-sign-final-modal {
  .modal-img-container {
    display: flex;
    justify-content: center;
    margin: 50px 10px;
  }  
  p {
    font-size: 20px;
    text-align: center;
  }
  .modal-footer {
    display: flex;
    justify-content: center;
    border-top: none;
    background-color: white;
    padding-bottom: 24px;
    .main-btn {
      padding: 10px 50px;
    }
  }
}
.yarn-sign-wizard .builder-container {  
  .template-container {
    flex: 0 1 auto;
    width: 350px;
    height: 300px;
    transform: scale(0.1);
    transform-origin: left top;    
  }
}
@media screen and (min-width: 1350px) {
  .yarn-sign-wizard .builder-container {
    .template-container {
      flex: 0 1 auto;
      width: 440px;
      height: 390px;
      transform: scale(0.12);      
    }
  }
}
@media screen and (min-width: 1600px) {
  .yarn-sign-wizard .builder-container {
    .template-container {
      flex: 0 1 auto;
      width: 560px; 
      height: 480px;   
      transform: scale(0.15);
    }
  }
} 