@import "../../../assets/variables";

.modal-dialog.edit-contact {
  max-width: 536px;
  border-radius: 4px;

  // .modal-content {
  //   background-color: white;
  // }
}

.edit-contact {
  .modal-content {
    padding: 16px 16px 16px 24px;
  }

  div[class*=menu] > *:first-child> *:first-child  {
    font-weight: bold;
  }

  .modal-body,
  .modal-header,
  .modal-footer {
    padding: 0;
    border: none;
    background-color: white;
  }

  .dope-modal-header {
    border-radius: 4px 4px 0 0;
    margin-bottom: 18px;

    .modal-title {
      font-size: 22px;
      line-height: 28px;
      color: $dark-grey-blue;
      font-weight: bold;
    }
  }

  .dope-form-group {
    margin-bottom: 24px;    

    .input-title {
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $regent-gray;
      margin-bottom: 0;
    }

    // input {
    //   border: 1px solid $athens-gray-approx;
    //   border-radius: 4px;
    // }

    // .dope-input.form-control.error ~ .input-group-append {
    //   border-color: $primary-default;
    // }
  }

  .input-block {
    .dope-select {
      [class*="-control"] {
        border-color: $athens-gray-approx;
        box-shadow: none;
      }
    }

    &.select-error {
      div[class*=control] {
        border-color: $primary-default;
      }
    }
  }

  .one-line {
    display: flex;
    .form-group{
      width: 50%;
      max-width: 50%;
      &:first-child {
        margin-right: 16px;
      }
    }
    // .input-block {
    //   width: 50%;
    //   max-width: 50%;

    //   &:first-child {
    //     margin-right: 16px;
    //   }
    // }
  }

  sup {
    color: $primary-default;
  }
}
