@import "../../../assets/variables";

.lists-page {
  .list-table-title {        
    display: flex;
    justify-content: space-between;
    align-items: center;        
    margin-bottom: 32px;
    .title {
      font-size: 28px;
      font-weight: 700;
      margin: 0;
      line-height: normal;
      color: #354052;
    }
    .button-group {
      display: flex;
      align-items: center;            
      padding: 0;
      .btn{
        &:not(:last-child){
          margin-right: 8px;
        }          
      }
    }
  }
  .verified-cell {
    &.small-cell {
      margin: -8px 0 -14px 0;
    }
  }

  .react-bootstrap-table {
    thead {
      th:nth-child(1) {
        width: 26%;
      }

      th:nth-child(2) {
        width: 10%;
      }

      th:nth-child(3),
      th:nth-child(4),
      th:nth-child(5),
      th:nth-child(6) {
        width: 16%;
      }  

      th:nth-child(7) {
        width: 100px;
      }

      th:nth-child(1) {
        min-width: 180px;
      }

      th:nth-child(2) {
        min-width: 80px;
      }

      th:nth-child(5) {
        min-width: 140px;
      }

      th:nth-child(3),
      th:nth-child(4) {
        min-width: 140px;
      }

      th:nth-child(6) {
        min-width: 100px;
      }
    }
  }
}
