@import '../../variables';

.calendar-container {
    margin-bottom: 48px;
    margin-top: 30px;

    .calendar-header {
        font-size: 20px;
        font-weight: 500;
    }

    .rbc-month-view {
        min-height: 500px;
    }

    .call-label {
        background-color: $red;
    }

    .yard-label {
        background-color: $black;
    }

    .rbc-header {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 50px;
        background-color: $grey;
        color: white;
        font-size: 18px;
        line-height: 22px;
    }

    .rbc-row.rbc-time-header-cell {
        min-height: 50px;
    }

    .rbc-toolbar {
        justify-content: space-between;
        margin-bottom: 32px;

        .calendar-period-btn {
            button {
                background-color: black;
                color: white;
                border: none;
                border-radius: 0;
                font-weight: 700;
                font-size: 17px;
                text-transform: uppercase;
            }

            button:active:hover {
                background-color: $red;
                color: white;
            }

            .rbc-active {
                background-color: $primary-default;
                color: white;
            }
        }

        .rbc-toolbar-label {
            text-transform: uppercase;
            display: inline-block;
            min-width: 170px;
            padding: 0;
        }

        .rbc-toolbar-label-container {
            display: flex;
            align-items: center;
        }

        .arrow-right,
        .arrow-left {
            border: none;

            &::before {
                content: "";
                display: block;
                width: 12px;
                height: 21px;
                border: 0;
            }
        }

        .arrow-right {
            background: url('../assets/img/dashboard/arrow-right.png') center
                no-repeat;
        }

        .arrow-left {
            background: url('../assets/img/dashboard/arrow-left.png') center
                no-repeat;
        }

        .rbc-toolbar-fill-block {
            width: 285px;
        }
    }

    .rbc-month-view,
    .rbc-time-view {
        background-color: white;
    }
}
