@import "../../../assets/variables";

.new-campaign-container {
  .campaign-name-card {
    .campaign-name {
      max-width: 464px;
    }
  }

  .campaign-lists-card {
    .dope-search-container {
      margin-left: 0;

      input {
        width: 100%;
        max-width: 464px;
      }
    }

    .contact-list-text {
      color: $dope-grey-100;
    }

    .react-bootstrap-table {
      padding: 0 -24px;

      thead {
        th:nth-child(1) {
          width: 20px !important;
          padding: 14px 10px;
        }

        th:nth-child(3),
        th:nth-child(4),
        th:nth-child(5) {
          width: 15%;
        }

        th:nth-child(2),
        th:nth-child(6) {
          width: 30%;
        }

        th:nth-child(1) {
          // min-width: 150px;
        }

        th:nth-child(3) {
          min-width: 150px;
        }

        th:nth-child(2),
        th:nth-child(4) {
          min-width: 180px;
        }

        th:nth-child(5) {
          min-width: 150px;
        }

        th:nth-child(4) {
          min-width: 150px;
        }
      }

      tbody td:first-child {
        padding: 14px 0 14px 28px;
      }

      .disabled-row {
        color: $dope-grey-50;
        pointer-events: none;

        .dope-radio-container {
          color: $dope-grey-20;
        }
      }
    }
  }

  .campaign-calendar-card {
  }

  .campaign-pricing-card {
    .postage-type-item {
      margin-bottom: 12px;
      height: 20px;

      &:nth-child(2) {
        margin-bottom: 16px;
      }

      .dope-radio-container, 
      .postcard-price-per-one {
        font-size: 14px;
        line-height: 20px;
        color: $dark-grey-blue;
        margin-bottom: 0;

        .checkmark {
          margin-right: 16px;
        }
      }

      .dope-radio-container {
        min-width: 220px;
      }
    }
    .pricing-link-container{
      margin-bottom: 16px;
    } 
    .view-pricing-link {
      color: $primary-default;
      text-decoration: underline;
      cursor: pointer;
      &:hover {
        color: $primary-hover;
      }
    }
  }

  .postage-total-price {
    .postage-total-label,
    .postage-total-amount {
      font-size: 18px;
      line-height: 24px;
      margin-right: 4px;
    }

    .postage-total-label {
      font-weight: bold;
    }

    .postage-total-per-sending {
      color: $dope-grey-100;
    }
  }

  .new-campaign-btn {
    display: flex;
    justify-content: flex-end;
  }
}

.dope-radio-container {
  display: inline-flex;
  align-items: center;
  position: relative;
  margin-bottom: 0;
  cursor: pointer;
  font-size: 22px;
  user-select: none;

  /* Hide the browser's default checkbox */
  & input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;

    /* Show the checkmark when checked */
    &:checked ~ .checkmark:after {
      display: block;
    }
  }

  /* Create a custom checkbox */
  .checkmark {
    position: relative;
    display: inline-block;
    top: 0;
    left: 0;
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background-color: #eee;

    /* Create the checkmark/indicator (hidden when not checked) */
    &:after {
      content: "";
      position: absolute;
      display: none;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: #fff;
    }
  }

  /* On mouse-over, add a grey background color */
  &:hover input ~ .checkmark {
    background-color: #ccc;
  }
  /* When the checkbox is checked, add a blue background */
  & input:checked ~ .checkmark {
    background-color: $primary-default;
  }
}
