@import "../../../assets/variables";

.dope-input.form-control {
  padding: 10px 16px 10px 16px;  
  font-size: 14px;
  line-height: 18px;
  border-radius: 4px;  
  border-color: $athens-gray-approx;
  color: $dark-grey-blue;
  &:hover{
    border-color: #bfc7d1;
    color: $dark-grey-blue;
  }
  &:focus{
    border-width: 2px;
    padding: 9px 15px 9px 15px;
    // border-color: rgba(213, 35, 48, 0.2);
    border-color: #bfc7d1;
    color: $dark-grey-blue;
  }

  &::placeholder {
    color: $dope-grey-50;
  }

  &:disabled {
    background-color: $dope-grey-5;
    color: $dope-grey-50;
    border: 1px solid $dope-grey-10;
  }
}

.dope-input.form-control.error {
  border-color: $primary-default !important;
}

.dope-input[type=file] {
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  display: block;
  width: 100%;
}

.dope-input[type=file] + label,
.dope-input-file {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin: 0;
  line-height: 1.35em;
  border-radius: 0;
  border: none;
  padding: 8px 16px;
  height: inherit;
  width: 163px;
  background-color: $abbey;
  color: white;
  transition: all 150ms linear;

  &:hover {
    cursor: pointer;
    background-color: darken($abbey,10%)
  }
}

.form-group .dope-input.form-control,
.input-group .dope-input.form-control {
  padding: 10px 16px 10px 16px;
  &:focus{    
    padding: 9px 15px 9px 15px;    
  }  
}

