.form-group,
.input-group {  
  .input-title {
    font-size: 14px;
    line-height: 24px;
    font-weight: 600;
    color: $regent-gray;
    margin-bottom: 0;
  }
  .validation-message{
    position: absolute;
    top: 100%;
    left: 0;
    font-weight: 400;    
    line-height: 1.6;    
    color: #d52330;
    font-size: 10px;    
  }

  sup {
    color: $primary-default;
  } 
}