@import "src/assets/variables";

.Handwritten{
  .main-title{
    font-family: IBMPlexSans, sans-serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 1.28;
    color: $dark-grey-blue;
    margin-bottom: 16px;
    width: 100%;
  }

  .HandwrittenCard{
    padding: 0;
    background: #fff;
    border-radius: 4px;
    &:not(:last-child){
      margin-bottom: 16px;
    }
    .title-container{
      padding: 8px 24px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 60px;
      &.t-shadow{
        box-shadow: inset 0px -1px 0px #E5E9ED;
      }
    }
    .title {
      font-family: IBMPlexSans, sans-serif;
      font-size: 18px;
      line-height: 1.33;
      font-weight: 700;
      color: $dark-grey-blue;
    }
    .cardBody{
      padding: 0 24px 8px;
      &.scroll304{
        max-height: 304px;
        overflow: auto;
      }
      &.cardBodyText{
        padding-bottom: 20px;
        .cardBodyText-indicator{
          position: absolute;
          right: 0;
          font-size: 12px;
          color: $dope-grey-100;
          &.error{
            color: $primary-default;
          }
        }
      }
    }

    .radio-row{
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      min-height: 48px;
      .DopeRadio{
        margin-right: 16px;
        min-width: 200px;
      }
    }
  }


  .form-group{
    & > div:first-child{
      max-width: 300px;
    }
    p{
      margin-top: 20px;
    }
  }

  .HandwrittenBtns{
    display: flex;
    justify-content: flex-end;
  }
}

.HandwrittenModal{
  .dope-modal-body .form-group {
    margin-bottom: 15px;
  }
}