@import "src/assets/variables";
.LandToggler {
  &:not(:last-child){    
    margin-bottom: 20px;
  }
  .toggle-btn{    
    // height: 95px; 
    display: flex;
    align-items: center;  
    cursor: pointer;
    font-family: 'Oswald', sans-serif;
    font-weight: 700;
    font-size: 24px;
    @include breakpoint(sm){
      font-size: 32px;
    }
    @include breakpoint(xl){
      font-size: 64px;
    }
    line-height: 1.49;
    text-transform: uppercase;
    color: #000000;
    transition: color 0.6s cubic-bezier(0.34, 2, 0.6, 1); 
    &.active{
      color: #E6000F;
      .arrow-icon{
        transform: rotate(90deg);
        path{
          fill: #E6000F;
        }
      }
    }
  }
  .arrow-icon{
    margin-right: 24px;
    width: 36px;
    height: 36px; 
    transition: transform 0.6s cubic-bezier(0.34, 2, 0.6, 1);   
    path{
      fill: #000;
      transition: fill 0.6s cubic-bezier(0.34, 2, 0.6, 1);
    }    
    // &.active{
    //   transform: rotate(90deg);
    //   path{
    //     fill: #E6000F;
    //   } 
    // }    
  }
  .collapsed-text{
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    font-size: 18px;
    @include breakpoint(sm){
      font-size: 24px;
    }
    @include breakpoint(xl){
      font-size: 48px;
    }    
    line-height: 1.48;
    color: #E6000F;
  }
}
