@import "src/assets/variables";
.EddmCreate{  
  .back-btn-container{
    margin-bottom: 32px;
    .back-btn{
      height: 24px;
      padding: 4px 8px;
      border: none;
      @include outline(0);
      border-radius: 4px;
      background-color: $dope-grey-10;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: $primary-default;
      font-family: IBMPlexSans, sans-serif;
      font-size: 10px;
      font-weight: 700;      
      line-height: 1.6;
      text-transform: uppercase;
    }
    .back-icon{
      height: 16px;
      width: 16px;
      margin-right: 4px;
    }
  }
  &.addCard{
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}