@import "../../../../assets/variables";

.upload-list-modal {  
  .modal-content {
    // background-color: white;
    padding: 16px 24px;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }

  .dope-modal-header {
    background-color: white;
    margin-bottom: 18px;

    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }

  .dope-modal-body {    
    .dope-form-group {
      margin-bottom: 16px;      

      .input-group-append {
        border: none;
      }

      .dope-input-group{
        margin-bottom: 0;
      }

      .input-title {
        font-size: 14px;
        line-height: 24px;
        font-weight: 600;
        color: $regent-gray;
        margin-bottom: 0;
      }

      input {
        border: 1px solid $athens-gray-approx;
        border-radius: 4px;
        pointer-events: none;
        margin-right: 16px;
      }

      .dope-input + label {
        width: auto;
        border-radius: 4px;
        background-color: $dope-grey-5;
        color: $primary-default;  
      }

      .small {
        font-size: 10px;
        line-height: 16px;
        color: $dope-grey-100;

        &.error-text {
          color: $primary-default;
        }
      }
    }
  }

  .modal-footer {
    border: 0;
    background-color: white;
  }
}
