@import "src/assets/variables";

.save-template-modal {
  top: 50% !important;
  transform: translateY(-50%) !important;

  .modal-content {
    background-color: white;
    padding: 16px 24px;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 0;
  }

  .dope-modal-header {
    background-color: white;
    margin-bottom: 18px;

    h5 {
      color: $dark-grey-blue;
      font-size: 22px;
      line-height: 28px;
      font-weight: bold;
    }
  }

  .dope-modal-body {
    .input-title {
      font-size: 14px;
      line-height: 24px;
      font-weight: 600;
      color: $regent-gray;
      margin-bottom: 0;
    }

    input {
      border: 1px solid $athens-gray-approx;
      border-radius: 4px;
    }

    sup {
      color: $primary-default;
    }

    .small {
      font-size: 10px;
      line-height: 16px;
      color: $dope-grey-100;

      &.error-text {
        color: $primary-default;
      }
    }
  }

  .modal-footer {
    border: 0;
    background-color: white;

    .btn:nth-child(2) {
      margin-right: 0;
    }
  }
}
