@import "../../../assets/variables";

.MoreTab {
  .more-title{
    color: $dark-grey-blue;
    font-family: IBMPlexSans, sans-serif;
    font-size: 22px;
    font-weight: 700;    
    line-height: 1.27;
    margin-bottom: 8px;
  }
  .more-text{
    color: $dope-grey-100;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;    
    line-height: 1.43;
    margin-bottom: 12px;
  }
  .comp-info-container{
    display: flex;
    margin-bottom: 32px;
  }
  .img-container{
    width: 85px;
    height: 85px;
    overflow: hidden;
    margin-right: 16px;
    .comp-logo{
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: 50% 50%;
    }
  }
  .comp-name{
    color: $dark-grey-blue;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;
    font-weight: 700;    
    line-height: 1.43;
    margin-bottom: 8px;
  }
  .comp-phone{
    display: block;
    color: $dark-grey-blue;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;    
    line-height: 1.43;
  }
  .comp-mail{
    display: block;
    color: $dark-grey-blue;
    font-family: IBMPlexSans, sans-serif;
    font-size: 14px;    
    line-height: 1.43;
  }
  .cancelDel-container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .alert.alert-warning{
      padding: 8px;    
      border-radius: 4px;
      background-color: rgba(#F5A623,0.1);
      color: #F5A623;
      font-family: IBMPlexSans, sans-serif;
      font-weight: 400;
      font-size: 14px;    
      line-height: 1.43;
      margin-bottom: 24px;
      .icon{
        margin-right: 8px;
      }
    }
  }  
}

